import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import {
  Row,
  Col,
  Button,
  Table,
  Space,
  Select,
  Tooltip,
  Popconfirm,
} from "antd";
import styled from "styled-components";
import PropTypes from "prop-types";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";

const { Option } = Select;

// Components
import ColumnsModal from "../Modal/ColumnsModal";
import GalleryView from "../GalleryView";
import TableActionsBar from "../TableActionsBar";

export default function DefaultTable({
  dataSource,
  columns,
  onUpdate,
  onDelete,
  onEdit,
  actions,
  listType,
  onReset,
  columnsChange,
  onDeleteMultiple,
  dynamicColumns,
  rowKey,
  rowSelection,
  onRefresh,
  filters,
  ...props
}) {
  const [type, setType] = useState(listType);
  const [t] = useTranslation();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [selectedItems, setSeletedItems] = useState([]);

  const onChangeCheckbox = (val) => {
    const findItemIndex = selectedItems.findIndex((id) => {
      return id === val[rowKey];
    });
    let items = [...selectedItems];
    if (findItemIndex != -1) {
      items = items.filter((id) => id !== val[rowKey]);
    } else {
      items.push(val[rowKey]);
    }
    setSeletedItems(items);
  };

  function deleteItem(data) {
    onDelete(data);
  }

  function getColumns() {
    return [
      ...columns,
      {
        title: t("Actions"),
        key: "action",
        width: "10%",
        render(text, record) {
          return (
            <Space size="middle">
              {onEdit && (
                <Tooltip onClick={() => onEdit(record)} title={t("Update")}>
                  <EditOutlined />
                </Tooltip>
              )}
              {onDelete && (
                <Tooltip title={t("Delete")}>
                  <Popconfirm
                    placement="leftTop"
                    title={t("DeleteConfirmMessage")}
                    onConfirm={() => deleteItem(record)}
                    okText={t("Yes")}
                    cancelText={t("No")}
                  >
                    <DeleteOutlined onClick={() => onUpdate(record)} />
                  </Popconfirm>
                </Tooltip>
              )}
            </Space>
          );
        },
      },
    ];
  }

  const handleExport = () => alert("exported");

  const handleDelete = () => onDeleteMultiple(selectedItems);

  return (
    <>
      <Row gutter={[10, 20]}>
        <Col span={12}>
          <FilterWrapper>
            {dynamicColumns && (
              <Button
                onClick={() => setIsModalVisible(true)}
                style={{ marginRight: 10 }}
              >
                {t("Columns")}
              </Button>
            )}
            {filters}
          </FilterWrapper>
        </Col>

        {filters && (
          <Col span={12}>
            <ActionsWrapper>
              <ActionButton onClick={onRefresh} type="primary">
                {t("Refresh")}
              </ActionButton>
              <ActionButton onClick={onReset} type="primary">
                {t("Reset")}
              </ActionButton>
              {/* <Select
              defaultValue={type}
              style={{ width: 75 }}
              onChange={setType}
            >
              <Option value="list">List</Option>
              <Option value="gallery">Gallery</Option>
            </Select> */}
            </ActionsWrapper>
          </Col>
        )}
      </Row>
      {type == "gallery" ? (
        <GalleryView
          data={dataSource}
          label="name"
          onChangeCheckbox={onChangeCheckbox}
          selectedItems={selectedItems}
        />
      ) : (
        <Table
          {...props}
          rowKey={rowKey}
          rowSelection={
            rowSelection
              ? {
                  type: "checkbox",
                  onSelect: (record) => onChangeCheckbox(record),
                  selectedRowKeys: selectedItems,
                }
              : false
          }
          columns={actions ? getColumns(columns) : columns}
          dataSource={dataSource}
        />
      )}

      <ColumnsModal
        modalColumns={columns}
        onSave={(selectedColumns) => {
          columnsChange(selectedColumns);
          setIsModalVisible(false);
          //console.log("sColumns", selectedColumns);
        }}
        isModalVisible={isModalVisible}
        onCancel={() => setIsModalVisible(false)}
      />
      {selectedItems.length > 0 && (
        <TableActionsBar
          onExport={handleExport}
          onDelete={handleDelete}
          count={selectedItems.length}
        />
      )}
    </>
  );
}

DefaultTable.propTypes = {
  dataSource: PropTypes.any.isRequired,
  columns: PropTypes.any.isRequired,
  rowKey: PropTypes.string,
  onUpdate: PropTypes.func,
  onDelete: PropTypes.func,
  onEdit: PropTypes.func,
  actions: PropTypes.bool,
  listType: PropTypes.string,
  columnsChange: PropTypes.func,
  onReset: PropTypes.func,
  onDeleteMultiple: PropTypes.func,
  dynamicColumns: PropTypes.bool,
  rowSelection: PropTypes.bool,
  filters: PropTypes.ReactNode,
  onRefresh: PropTypes.func,
};

DefaultTable.defaultProps = {
  onUpdate: () => console.log("..update"),
  onReset: () => console.log("..reset"),
  onRefresh: () => console.log("..refresh"),
  actions: false,
  listType: "list",
  dataSource: [],
  dynamicColumns: false,
  rowKey: "Id",
  rowSelection: false,
};

const FilterWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const ActionsWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
`;
const ActionButton = styled(Button)`
  margin-right: 10px;
`;
