import { FETCH_CATEGORY, SET_CATEGORY_LOADING } from "../consts/category";

const initialState = {
  data: {},
  loading: false,
};

export default function alertReducer(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case FETCH_CATEGORY: {
      return {
        ...state,
        data: payload,
      };
    }
    case SET_CATEGORY_LOADING: {
      return {
        ...state,
        loading: payload,
      };
    }
    default:
      return state;
  }
}
