import i18n from "i18next";
import Backend from "i18next-xhr-backend";
import { initReactI18next } from "react-i18next";

import { RESOURCE } from "../consts/api";
import { API_URL } from "../consts";

export const initLocalization = async () => {
  const localization = JSON.parse(localStorage.getItem("persist:root"))
    ?.localization;
  const lang = localization ? JSON.parse(localization).lang : "tr-TR";
  return i18n
    .use(Backend)
    .use(initReactI18next)
    .init({
      backend: {
        header: {
          Accept: "application/json, text/plain, */*",
        },
        loadPath: `${API_URL}${RESOURCE}/{{lng}}`,
      },
      debug: true,
      lng: lang,
      fallbackLng: ["tr-TR", "en-US"],
      whitelist: ["tr-TR", "en-US"],
    });
};
