import React, { useState, useRef, useEffect } from "react";
import styled from "styled-components";
import Layout from "../../components/Layouts/Layout";
import ReactBpmnModeler from "../../components/Bpmn/Modeler";

const defaultDiagram = "/test.bpmn";

function Modeler(props) {
  const [diagramXML, setDiagramXML] = useState(null);
  const dragRef = useRef();

  async function fetchDiagram(url) {
    const res = await fetch(url);
    const text = await res.text();
    setDiagramXML(text);
  }

  function handleFileSelect(e) {
    e.stopPropagation();
    e.preventDefault();
    var files = e.dataTransfer.files;
    var file = files[0];
    var reader = new FileReader();
    reader.readAsText(file);
    reader.onload = function (e) {
      var xml = e.target.result;
      setDiagramXML(xml);
    };
  }

  function handleDragOver(e) {
    e.stopPropagation();
    e.preventDefault();
    e.dataTransfer.dropEffect = "copy"; // Explicitly show this is a copy.
  }

  function createNew() {
    fetchDiagram(defaultDiagram);
  }

  useEffect(() => {
    dragRef.current.addEventListener("drop", handleFileSelect, false);
    dragRef.current.addEventListener("dragover", handleDragOver, false);
  }, []);

  return (
    <Layout>
      {diagramXML ? (
        <ReactBpmnModeler diagram={diagramXML} />
      ) : (
        <WelcomeScreen ref={dragRef}>
          Drop BPMN diagram from your desktop or{" "}
          <a onClick={createNew}> create a new diagram </a>
          to get started.
        </WelcomeScreen>
      )}
    </Layout>
  );
}

const BpmnWrapper = styled.div``;
const WelcomeScreen = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 50px);
  font-size: 18px;
`;

Modeler.propTypes = {};

export default Modeler;
