import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useParams } from "react-router-dom";
import { Form, Row, Col, Input, Select, Button, Skeleton, message } from "antd";

// Services
import { GetInfoGroup } from "../../../../services/groups";

const Properties = () => {
  const [t] = useTranslation();
  const { id } = useParams();
  const [form] = Form.useForm();
  const [groupInfo, setInfoGroup] = useState({});
  const [loading, setLoading] = useState(true);

  const getGroup = async () => {
    setLoading(true);

    try {
      const response = await GetInfoGroup(id);

      setInfoGroup(response.data);
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  useEffect(() => {
    getGroup();
  }, []);

  if (loading) {
    return <Skeleton active />;
  }

  return (
    <Form layout="vertical" initialValues={groupInfo} form={form}>
      <Row gutter={[20]}>
        <Col span={12}>
          <Form.Item
            label={t("Group name")}
            name="Name"
            rules={[
              {
                required: true,
                message: "Please input group name!",
              },
            ]}
          >
            <Input />
          </Form.Item>
        </Col>

        <Col span={24}>
          <Row>
            <Col flex="auto"></Col>
            <Col>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  {t("save")}
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Col>
      </Row>
    </Form>
  );
};

Properties.propTypes = {};

export default Properties;
