import React, { useState } from "react";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";
import { Modal, Row, Col, Input, Button } from "antd";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import styled from "styled-components";
//import { tableColumns } from "../../mockData";

// Components
import ColumnList from "../ColumnList";

// Helpers
import { reorder, move } from "../../helpers/dnd";

export default function ColumnsModal({
  isModalVisible,
  onSave,
  onCancel,
  modalColumns,
}) {
  const [t] = useTranslation();
  // table columns props
  const [columns, setColumns] = useState(modalColumns);
  const [selectedColumns, setSelectedColumns] = useState([]);
  const [columnFilterText, setColumnFilterText] = useState("");
  const getList = (id) => (id == "droppable" ? columns : selectedColumns);

  //filter
  const onColumnFilterChange = (filterText) => {
    setColumnFilterText(filterText);
    let filteredColumns = modalColumns.filter((column) => {
      return (
        column.title.toLowerCase().includes(filterText.toLowerCase()) ||
        column.dataIndex.toLowerCase().includes(filterText.toLowerCase())
      );
    });
    setColumns(filteredColumns);
  };

  const onDragEnd = (result) => {
    const { source, destination } = result;

    // dropped outside the list
    if (!destination) {
      return;
    }

    if (source.droppableId === destination.droppableId) {
      const items = reorder(
        getList(source.droppableId),
        source.index,
        destination.index
      );

      if (source.droppableId === "droppable2") {
        setSelectedColumns(items);
      } else {
        setSelectedColumns(items);
      }
    } else {
      const result = move(
        getList(source.droppableId),
        getList(destination.droppableId),
        source,
        destination
      );
      setColumns(result.droppable);
      setSelectedColumns(result.droppable2);
    }
  };
  return (
    <StyledModal
      title={t("columnSelection")}
      visible={isModalVisible}
      onOk={() => onSave(selectedColumns)}
      onCancel={onCancel}
      maskClosable={false}
    >
      <DragDropContext onDragEnd={onDragEnd}>
        <Row>
          <Col span={8}>
            <ColumnList
              left
              header={<HeaderTitle>{t("categories")}</HeaderTitle>}
            >
              <Column>1</Column>
              <Column>Test</Column>
              <Column>Test</Column>
            </ColumnList>
          </Col>
          <Col span={8}>
            <ColumnList
              header={
                <Input
                  placeholder={t("Search")}
                  value={columnFilterText}
                  onChange={(e) => onColumnFilterChange(e.target.value)}
                />
              }
            >
              <Droppable droppableId="droppable">
                {(provided) => (
                  <div ref={provided.innerRef} style={{ height: "100%" }}>
                    {columns.map((item, index) => (
                      <Draggable
                        key={item.title}
                        draggableId={item.title}
                        index={index}
                      >
                        {(provided) => (
                          <Column
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            {item.title}
                          </Column>
                        )}
                      </Draggable>
                    ))}
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </ColumnList>
          </Col>
          <Col span={8}>
            <ColumnList
              header={
                <>
                  <HeaderTitle>{t("selectedColumns")}</HeaderTitle>
                  <Button>{t("clear")}</Button>
                </>
              }
            >
              <Droppable droppableId="droppable2">
                {(provided) => (
                  <div ref={provided.innerRef} style={{ height: "100%" }}>
                    {selectedColumns.map((item, index) => (
                      <Draggable
                        key={item.title}
                        draggableId={item.title}
                        index={index}
                      >
                        {(provided) => (
                          <Column
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            {item.title}
                          </Column>
                        )}
                      </Draggable>
                    ))}
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </ColumnList>
          </Col>
        </Row>
      </DragDropContext>
    </StyledModal>
  );
}

ColumnsModal.propTypes = {
  isModalVisible: PropTypes.bool,
  onSave: PropTypes.func,
  onCancel: PropTypes.func,
  modalColumns: PropTypes.array,
};
const StyledModal = styled(Modal)`
  width: 60% !important;
`;

const HeaderTitle = styled.h3`
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 0;
`;

const Column = styled.h4`
  border: 1px solid ${({ theme }) => theme.lightGray};
  padding: 10px;
  background-color: ${({ theme }) => theme.lightGray};
  border-radius: 3px;
  transition: background-color 0.3s;
  cursor: pointer;
  :hover {
    background-color: ${({ theme }) => theme.white};
  }
  :nth-last-child() {
    border-bottom: none;
  }
`;
