import React, { useEffect, useState } from "react";
import { Button, Col, Row, Skeleton } from "antd";
import styled from "styled-components";
import PermissionTable from "../../../../components/Table/PermissonTable";
import { useTranslation } from "react-i18next";

// Mock Data
import { permissonTableData } from "../../../../mockData";

const columns2 = [
  {
    title: "Grup İsmi",
    dataIndex: "name",
    // eslint-disable-next-line react/display-name
    render: () => <span>Ürün Tedarik</span>,
    width: "60%",
  },
];

export default function UserGroupPermissions() {
  const [loading, setLoading] = useState(true);
  const [t] = useTranslation();

  const getData = () => {
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  };

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <LeftTabsContentWrapper>
      <Row justify="end" gutter={8}>
        <Col>
          <Button type="primary">{t("savepermissions")}</Button>
        </Col>

        <Col>
          <Button type="primary">{t("getgrouppermissions")}</Button>
        </Col>
      </Row>
      {loading && skeletonWrapper()}
      {!loading && (
        <PermissionTable
          columns={columns2}
          dataSource={permissonTableData}
          pagination={false}
        />
      )}
    </LeftTabsContentWrapper>
  );
}

const LeftTabsContentWrapper = styled.div`
  padding: 10px;
`;
