import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Tabs, Form, Skeleton, Button, Row, Col, message } from "antd";
import { useHistory, useParams } from "react-router-dom";

const { TabPane } = Tabs;

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import Content from "../../../components/Content";

import { Properties, History } from "./tabs";

import { getFamily } from "../../../services/families";

export default function Families() {
  let history = useHistory();
  const { id } = useParams();
  const [t] = useTranslation();
  const [loading, setLoading] = useState(true);
  const [family, setFamily] = useState(null);

  const fetchFamily = async () => {
    const res = await getFamily(id);
    setFamily(res.data.Value);
    setLoading(false);
  };
  const onFinish = () => message.success(t("Success"));
  useEffect(() => {
    fetchFamily();
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("Families"), path: "/families" },
          ]}
        />
      </Container>
      <Container>
        <Content
          title={t("EditFamily")}
          actionTitle={t("Delete")}
          onClick={() => console.log("deleted")}
        >
          {loading ? (
            <Skeleton active />
          ) : (
            <Form layout="vertical" initialValues={family} onFinish={onFinish}>
              <Tabs type="card">
                <TabPane tab={t("Properties")} key="1">
                  <Properties family={family} />
                </TabPane>
                <TabPane tab={t("Attributes")} key="2">
                  Content of Tab Pane 2
                </TabPane>
                <TabPane tab={t("History")} key="3">
                  <History />
                </TabPane>
              </Tabs>
              <Form.Item>
                <Row>
                  <Col flex="auto"></Col>
                  <Col>
                    <Button htmlType="submit">{t("Save")}</Button>
                  </Col>
                </Row>
              </Form.Item>
            </Form>
          )}
        </Content>
      </Container>
    </DefaultLayout>
  );
}
