import React from "react";
import {
  Row,
  Col,
  Tabs,
  Skeleton,
  Form,
  Input,
  Select,
  Button,
  Switch,
  Space,
} from "antd";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";

function Properties({ rules, newAttrType, next, prev }) {
  const [t] = useTranslation();
  const {
    AttributeType,
    ItemType,
    ItemTypes,
    AttributeGroupList,
  } = newAttrType;
  return (
    <>
      <Form.Item
        label={t("Code")}
        name="Code"
        rules={[
          {
            required: true,
            message: `${t("PleaseInputYour")} ${t("Code")}!`,
          },
        ]}
      >
        <Input></Input>
      </Form.Item>
      <Form.Item
        label={t("AttributeType")}
        name="AttributeType"
        rules={[
          {
            required: true,
            message: `${t("PleaseInputYour")} ${t("AttributeType")}!`,
          },
        ]}
      >
        <Input disabled />
      </Form.Item>
      <Form.Item
        label={t("ResourceType")}
        name="ItemType"
        rules={[{ required: true }]}
      >
        <Select place="Seçiniz...">
          {ItemTypes.map((itemType) => {
            return (
              <Select.Option key={itemType.Id} value={itemType.Id}>
                {itemType.Label}
              </Select.Option>
            );
          })}
        </Select>
      </Form.Item>

      <Form.Item
        label={t("AttributeGroup")}
        name="SelectedAttributeGroupId"
        rules={[
          {
            required: true,
            message: `${t("PleaseInputYour")} ${t("AttributeGroup")}!`,
          },
        ]}
      >
        <Select place="Seçiniz...">
          {AttributeGroupList.map((attrGroup) => {
            return (
              <Select.Option key={attrGroup.Id} value={attrGroup.Id}>
                {attrGroup.Label}
              </Select.Option>
            );
          })}
        </Select>
      </Form.Item>
      <Form.Item label={t("Localizable")} name="Localizable">
        <Switch />
      </Form.Item>
      <Form.Item label={t("Filter")} name="IsPictogram">
        <Switch></Switch>
      </Form.Item>
    </>
  );
}

Properties.propTypes = {
  rules: PropTypes.arrayOf(PropTypes.string),
  newAttrType: PropTypes.any,
  next: PropTypes.func,
  prev: Properties.func,
};
export default Properties;
