import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Tabs, Skeleton, Modal, message } from "antd";
import { useParams, useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import Content from "../../../components/Content";
import Properties from "../editRole/tabs/properties";
import Users from "../editRole/tabs/users";
import Permissions from "../editRole/tabs/permissions";

// Services
import { RolesUsers, GetInfoRole } from "../../../services/roles";

export default function EditRole() {
  const [t] = useTranslation();
  const { id } = useParams();
  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const [roleInfo, setInfoRole] = useState({});

  const getRolesInfo = async () => {
    try {
      const response = await GetInfoRole(id);

      setInfoRole(response.data);
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  useEffect(() => {
    getRolesInfo();
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("System") },
            { label: t("Edit Role"), path: `/editRole/${id}` },
          ]}
        ></BreadcrumbNav>
      </Container>

      <Container>
        <Content title="Edit role">
          {loading && skeletonWrapper()}
          {!loading && roleInfo && (
            <Tabs type="card" defaultActiveKey="1">
              <Tabs.TabPane tab={t("General")} key="1">
                <Properties roleInfo={roleInfo} />
              </Tabs.TabPane>

              <Tabs.TabPane tab={t("Permissions")} key="2">
                <Permissions roleInfo={roleInfo} />
              </Tabs.TabPane>

              <Tabs.TabPane tab={t("Users")} key="3">
                <Users />
              </Tabs.TabPane>
            </Tabs>
          )}
        </Content>
      </Container>
    </DefaultLayout>
  );
}
