import React from "react";
import { Form, Row, Col, Select, Input, Button } from "antd";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";

export default function Properties({ categories, data }) {
  const [t] = useTranslation();
  const { Code, ItemType, Label } = data;

  return (
    <Form
      layout="vertical"
      name="basic"
      initialValues={{ Code, resourceType: ItemType, tr_TR: Label }}
    >
      <TitleWrapper>{t("GeneralProperties")}</TitleWrapper>
      <Row gutter={16} style={{ width: "50%" }}>
        <Col span={14}>
          <Form.Item
            rules={[
              {
                required: true,
                message: `${t("PleaseInputYour")} ${t("Code")}!`,
              },
            ]}
            label={t("Code")}
            name="Code"
          >
            <Input id="attribute-groups-code" />
          </Form.Item>
        </Col>
        <Col span={14}>
          <Form.Item label={t("ResourceType")} name="resourceType">
            <Select id="attribute-groups-resource-type">
              {categories.map(({ Id, Label }) => (
                <Select.Option key={Id} value={Id}>
                  {Label}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
        </Col>
      </Row>
      <TitleWrapper>{t("GroupValues")}</TitleWrapper>
      <Row gutter={16} style={{ width: "50%" }}>
        <Col span={14}>
          <Form.Item label={t("Locale_EN")} name="en_US">
            <Input id="attribute-groups-translation-en" />
          </Form.Item>
        </Col>

        <Col span={14}>
          <Form.Item label={t("Locale_RU")} name="ru_RU">
            <Input id="attribute-groups-translation-ru" />
          </Form.Item>
        </Col>

        <Col span={14}>
          <Form.Item
            id="attribute-groups-translation-tr"
            label={t("Locale_TR")}
            name="tr_TR"
          >
            <Input />
          </Form.Item>
        </Col>
        <Col span={14}>
          <Form.Item>
            <Button
              id="attribute-group-save-properties"
              type="primary"
              style={{ width: 200 }}
            >
              {t("Save")}
            </Button>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

const TitleWrapper = styled.p`
  background-color: #fff;
  padding: 10px 20px;
`;
Properties.propTypes = {
  categories: PropTypes.any,
  data: PropTypes.any,
};
