import React, { useState, useEffect, useRef } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import BpmnModeler from "bpmn-js/lib/Modeler";
import propertiesPanelModule from "bpmn-js-properties-panel";
import propertiesProviderModule from "bpmn-js-properties-panel/lib/provider/camunda";
import camundaModdleDescriptor from "camunda-bpmn-moddle/resources/camunda";
import "bpmn-js/dist/assets/diagram-js.css";
import "bpmn-js/dist/assets/bpmn-font/css/bpmn-embedded.css";
import "bpmn-js-properties-panel/dist/assets/bpmn-js-properties-panel.css";

function ReactBpmnModeler({ diagram }) {
  const [diagramXML, setDiagramXML] = useState(null);
  const [bpmnViewer, setBpmnViewer] = useState(null);

  let containerRef = useRef();
  let propertiesRef = useRef();

  async function onSave() {
    const { xml } = await bpmnViewer.saveXML();
    var encodedData = encodeURIComponent(xml);
    console.log("svg", encodedData);

    const link = document.createElement("a");
    if (xml) {
      link.setAttribute(
        "href",
        `data:application/bpmn20-xml;charset=UTF-8, ${encodedData},`
      );
      link.setAttribute("download", "sss.bpmn");

      link.click();
    }
  }

  useEffect(() => {
    const bpmnViewerInstance = new BpmnModeler({
      container: containerRef.current,
      propertiesPanel: {
        parent: propertiesRef.current,
      },
      additionalModules: [propertiesPanelModule, propertiesProviderModule],
      moddleExtensions: {
        camunda: camundaModdleDescriptor,
      },
    });

    bpmnViewerInstance.importXML(diagram);

    setBpmnViewer(bpmnViewerInstance);

    return () => {
      bpmnViewerInstance.destroy();
    };
  }, []);

  return (
    <Wrapper>
      <Diagram ref={containerRef} />
      <Properties ref={propertiesRef} />
      <Save onClick={onSave}>Downloand</Save>
    </Wrapper>
  );
}

ReactBpmnModeler.propTypes = {
  diagram: PropTypes.string,
};

const Wrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  position: relative;
  .content {
    position: relative;
    width: 100%;
    height: 100%;

    > .message {
      width: 100%;
      height: 100%;
      text-align: center;
      display: table;

      font-size: 16px;
      color: #111;

      .note {
        vertical-align: middle;
        text-align: center;
        display: table-cell;
      }

      &.error {
        .details {
          max-width: 500px;
          font-size: 12px;
          margin: 20px auto;
          text-align: left;
          color: #bd2828;
        }

        pre {
          border: solid 1px #bd2828;
          background: #fefafa;
          padding: 10px;
          color: #bd2828;
        }
      }
    }
    &:not(.with-error) .error,
    &.with-error .intro,
    &.with-diagram .intro {
      display: none;
    }

    .canvas {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .canvas,
    .properties-panel-parent {
      display: none;
    }

    &.with-diagram {
      .canvas,
      .properties-panel-parent {
        display: block;
      }
    }
  }

  .buttons {
    position: fixed;
    bottom: 20px;
    left: 20px;

    padding: 0;
    margin: 0;
    list-style: none;

    > li {
      display: inline-block;
      margin-right: 10px;

      > a {
        background: #ddd;
        border: solid 1px #666;
        display: inline-block;
        padding: 5px;
      }
    }

    a {
      opacity: 0.3;
    }

    a.active {
      opacity: 1;
    }
  }
`;

const Diagram = styled.div`
  height: calc(100vh - 50px);
`;

const Properties = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 260px;
  z-index: 10;
  border-left: 1px solid #ccc;
  overflow: auto;
  &:empty {
    display: none;
  }
  > .djs-properties-panel {
    padding-bottom: 70px;
    min-height: 100%;
  }
`;

const Save = styled.button`
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 9999;
`;
export default ReactBpmnModeler;
