import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { Check, Share, Passive, Progress, Approved, Draft } from "../../icons";

const Status = ({ children, title }) => {
  return (
    <StatusIconWrapper>
      <StatusTitle>{title}</StatusTitle>
      {children}
    </StatusIconWrapper>
  );
};

const StatusIcon = ({ status }) => {
  switch (status) {
    case 0:
      return (
        <Status title="Onaylı">
          <Approved />
        </Status>
      );
    case 1:
      return (
        <Status title="Çalışıyor">
          <Progress />
        </Status>
      );

    case 2:
      return (
        <Status title="Taslak">
          <Draft />
        </Status>
      );
    case 3:
      return (
        <Status title="Pasif">
          <Passive />
        </Status>
      );
    default:
      <span>-</span>;
  }
};

const StatusIconWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const StatusTitle = styled.span`
  font-size: 12px;
  color: #4d4d4d;
  margin-right: 10px;
  font-weight: 300;
`;

Status.propTypes = {
  children: PropTypes.ReactNode,
  title: PropTypes.string,
};

StatusIcon.propTypes = {
  status: PropTypes.number,
};

export default StatusIcon;
