import React from "react";
import { Form, Input } from "antd";
import { useTranslation } from "react-i18next";

function Values(props) {
  const [t] = useTranslation();

  return (
    <Form.List name="Translations">
      {(fields) => (
        <>
          <Form.Item label={t("Locale_EN")} name="en-US">
            <Input />
          </Form.Item>
          <Form.Item label={t("Locale_RU")} name="ru-RU">
            <Input />
          </Form.Item>
          <Form.Item
            label={t("Locale_TR")}
            name="tr-TR"
            rules={[{ required: true }]}
          >
            <Input />
          </Form.Item>
        </>
      )}
    </Form.List>
  );
}

export default Values;
