import { privateAxiosInstance } from "./api";
import { GET_DATA_USERS, GET_DATA_USER_INFO } from "../consts/api";

export async function UsersList(filter = {}) {
  return await privateAxiosInstance.post(GET_DATA_USERS, filter);
}

export async function GetInfoUser(id) {
  return await privateAxiosInstance.get(`${GET_DATA_USER_INFO}?id=${id}`);
}
