import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Card } from "antd";
import { useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import Content from "../../../components/Content";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import DefaultTable from "../../../components/Table/DefaultTable";

// Services
import { UsersList } from "../../../services/userManagement";

export default function UserManage() {
  let history = useHistory();
  const { t } = useTranslation();
  const [users, setUserList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });
  const getColumns = () => [
    {
      title: t("UserName"),
      dataIndex: "UserName",
      key: "UserName",
    },
    {
      title: t("Email"),
      dataIndex: "Email",
      key: "Email",
    },
    {
      title: t("FirstName"),
      dataIndex: "FirstName",
      key: "FirstName",
    },
    {
      title: t("LastName"),
      dataIndex: "LastName",
      key: "LastName",
    },
    {
      title: t("CreatedAt"),
      dataIndex: "CreatedAt",
      key: "CreatedAt",
    },
    {
      title: t("UpdatedAt"),
      dataIndex: "UpdatedAt",
      key: "UpdatedAt",
    },
    {
      title: t("Status"),
      dataIndex: "Status",
      key: "StatusA",
      // eslint-disable-next-line react/display-name
      render(text, record) {
        return <label>{record.UpdatedAt ? "Enabled" : "Disabled"}</label>;
      },
    },
    {
      title: t("Session Date"),
      dataIndex: "SessionStarted",
      key: "SessionStarted",
    },
    {
      title: t("Online"),
      dataIndex: "Status",
      key: "StatusActive",
      // eslint-disable-next-line react/display-name
      render(text, record) {
        let sessionDate = new Date(record.SessionStarted);
        let currentDate = new Date();
        // eslint-disable-next-line prettier/prettier

        let beforeCurrentDate = new Date(currentDate);
        beforeCurrentDate.setMinutes(currentDate.getMinutes() - 1);

        let afterCurrentDate = new Date(currentDate);
        afterCurrentDate.setMinutes(currentDate.getMinutes() + 1);

        if (
          sessionDate >= beforeCurrentDate &&
          sessionDate <= afterCurrentDate
        ) {
          return <label>Online</label>;
        } else {
          return <label>Offline</label>;
        }
      },
    },
  ];

  const getResourceUsers = async (pagination) => {
    const { PageSize, CurrentPage, data } = pagination;
    setLoading(true);
    try {
      const result = await UsersList({
        PageSize,
        CurrentPage: CurrentPage - 1,
      });
      setUserList(result.data.data);
      setPagination({ ...data, total: result.data.recordsTotal });
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getResourceUsers({
      PageSize: pageSize,
      CurrentPage: current,
      data: pagination,
    });
  };

  const onEdit = ({ Id }) => {
    history.push(`/system/userManage/${Id}`);
  };

  useEffect(() => {
    getResourceUsers({
      PageSize: 10,
      CurrentPage: 1,
      data: pagination,
    });
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("UserManagement"), path: "/userManagement" },
          ]}
        ></BreadcrumbNav>
      </Container>
      <Container>
        <Content title={t("UserManagement")}>
          <Row>
            <Col span={24}>
              <DefaultTable
                rowSelection
                columns={getColumns()}
                onEdit={onEdit}
                dataSource={users}
                loading={loading}
                style={{ marginTop: "15px" }}
                scroll={{ x: "1000px" }}
                actions
                pagination={pagination}
                onChange={handleTableChange}
              />
            </Col>
          </Row>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
