import React, { useState, useEffect } from "react";
import { Row, Col, Checkbox, Skeleton } from "antd";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

// Components
import SummaryItem from "../../../../components/turkcell/SummaryItem";

// Services
import { getItem } from "../../../../services/turkcell/items";

const Associations = () => {
  const { id } = useParams();
  const [t] = useTranslation();
  const [associations, setAssociations] = useState([]);
  const [loading, setLoading] = useState(true);
  const [associationFilters, setAssociationFilters] = useState([
    "CLAUSE_CLAUSE",
    "CLAUSE_ALTERNATE",
    "CLAUSE_PARENT_CHILD",
  ]);

  const getAssociations = () => {
    return associations.filter(({ Code }) => associationFilters.includes(Code));
  };

  const onChange = ({ target }) => {
    const { value, checked } = target;
    const filters = checked
      ? [...associationFilters, value]
      : associationFilters.filter((filter) => filter != value);
    setAssociationFilters(filters);
  };

  useEffect(() => {
    async function fetchData() {
      const res = await getItem(id);
      setAssociations(res.data.Value.Associations);
      setLoading(false);
    }
    fetchData();
  }, []);

  const associationList = getAssociations();
  return (
    <Row gutter={[10, 10]}>
      <Col span={24}>
        <Title>Taraflar {">"} Damga Vergisi</Title>
      </Col>
      <Col span={24}>
        <Checkbox
          id="CLAUSE_CLAUSE"
          checked={associationFilters.includes("CLAUSE_CLAUSE")}
          value="CLAUSE_CLAUSE"
          onChange={onChange}
        >
          {t("Clause")}
        </Checkbox>
        <Checkbox
          id="CLAUSE_ALTERNATE"
          checked={associationFilters.includes("CLAUSE_ALTERNATE")}
          value="CLAUSE_ALTERNATE"
          onChange={onChange}
        >
          {`${t("AltClause")} ${t("Clause")}`}
          Ikame Madde
        </Checkbox>
        <Checkbox
          id="CLAUSE_PARENT_CHILD"
          checked={associationFilters.includes("CLAUSE_PARENT_CHILD")}
          value="CLAUSE_PARENT_CHILD"
          onChange={onChange}
        >
          {t("ChildClause")}
        </Checkbox>
      </Col>
      <Col span={24}>
        {loading && <Skeleton active />}
        {associationList.map((association, index) => (
          <SummaryItem
            key={index}
            bgColor={index % 2 == 0 ? "#fafafa" : "#fff"}
            item={association}
          />
        ))}
        {associationList.length === 0 && !loading && (
          <NotFound>{t("NoFoundAssociatedClauses")}</NotFound>
        )}
      </Col>
    </Row>
  );
};
const Title = styled.h3`
  font-size: 18px;
  color: #253342;
  font-weight: bold;
`;
const NotFound = styled.p`
  text-align: center;
  margin-top: 50px;
`;
export default Associations;
