import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import Groups from "../pages/system/groups";
// import NewRole from "../pages/system/newRole";
import EditRole from "../pages/system/editGroup";

// Components
import PrivateRoute from "../components/PrivateRoute";

const GroupsRoute = ({ match: { url } }) => (
  <>
    <Switch>
      {/* <PrivateRoute path={`${url}/newGroup`} component={NewRole} /> */}
      <PrivateRoute path={`${url}/:id`} component={EditRole} />
      <PrivateRoute path={`${url}`} component={Groups} />
    </Switch>
  </>
);

GroupsRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};

export default GroupsRoute;
