import React, { useState } from "react";
import { Row, Col, message, Tooltip, Form } from "antd";
import PropTypes from "prop-types";
import styled from "styled-components";
import { useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import "react-quill/dist/quill.snow.css";

//Components
import Table from "../../../../components/turkcell/ContractTable";
import RichText from "../../../../components/turkcell/Form/RichText";
import Select from "../../../../components/turkcell/Form/Select";
import InputWrapper from "../../../../components/turkcell/Form/InputWrapper";

// Helpers
import { getDetailTableColumns } from "../../../../helpers/table";
import {
  getAttributeElement,
  getAttributesObject,
} from "../../../../helpers/attribute";

import { getItems } from "../../../../services/turkcell/items";
import { getCategory } from "../../../../store/selectors/category";

export default function DetailTab({ attributes, item, onLike, tags }) {
  const [t] = useTranslation();
  let { id, categoryId } = useParams();
  const lang = useSelector((state) => state.localization.lang);
  const category = getCategory(categoryId);
  const tagDefaultValue = item.Associations
    ? item.Associations.map((item) => item.ItemId)
    : [];

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(`@${text}@`);
    message.success(`${text} kopyalandi!`);
  };

  return (
    <Row gutter={[0, 10]}>
      {id != "create" && (
        <Col span={24}>
          <Table
            columns={getDetailTableColumns(category, onLike, t)}
            data={item.Associations && item.Associations}
          />
        </Col>
      )}
      <Col span={24}>
        <Row gutter={[30, 10]}>
          <Col span={16}>
            <Form.Item name="MADDE_METNI">
              <RichText id="MADDE_METNI" />
            </Form.Item>
          </Col>
          <Col span={8}>
            <AttributeCodeWrapper>
              <CodeTitle>{t("Attributes")}</CodeTitle>
              {attributes &&
                attributes.map(({ Code, Label, Translations }) => (
                  <Tooltip title={t("ClickToCopy")} key={Code}>
                    <CodeText onClick={() => copyToClipboard(Code)}>
                      {Translations[lang] ? Translations[lang] : Code}
                    </CodeText>
                  </Tooltip>
                ))}
            </AttributeCodeWrapper>
          </Col>
        </Row>
      </Col>
      <Col span={24}>
        <AttributeWrapper>
          <Row gutter={[30, 10]}>
            {attributes &&
              attributes
                .filter((attr) => attr.Group == "CLAUSE_INFO")
                .map((attr) => {
                  if (attr.Code != "MADDE_METNI") {
                    return (
                      <Col span={8} key={attr.Code}>
                        <InputWrapper
                          label={attr.Translations[lang] || attr.Code}
                        >
                          {getAttributeElement(
                            attr,
                            getAttributesObject(attributes)
                          )}
                        </InputWrapper>
                      </Col>
                    );
                  }
                })}
            <Col span={8}>
              <InputWrapper label="Tags">
                <FormItem name="Associations">
                  <Select
                    defaultValue={tagDefaultValue}
                    mode="multiple"
                    options={tags}
                    valueAttribute="Id"
                    labelAttribute="Code"
                  ></Select>
                </FormItem>
              </InputWrapper>
            </Col>
          </Row>
        </AttributeWrapper>
      </Col>
    </Row>
  );
}

const AttributeCodeWrapper = styled.div`
  display: flex;
  flex-direction: column;
  border: 1px solid #dee3ed;
  border-radius: 8px;
  padding: 10px;
  padding-left: 15px;
  min-height: 200px;
`;

const CodeText = styled.span`
  margin-bottom: 10px;
  font-weight: bold;
  cursor: pointer;
`;

const CodeTitle = styled.h4`
  color: #8e9fad;
  font-size: 12px;
`;

const AttributeWrapper = styled.div``;

const FormItem = styled(Form.Item)`
  margin-bottom: 0;
`;
DetailTab.propTypes = {
  attributes: PropTypes.any,
  tags: PropTypes.any,
  item: PropTypes.any,
  onLike: PropTypes.func,
};
