import React, { useEffect, useState } from "react";
import { Row, Col, Button, Form, Tabs, Input, Skeleton, Modal } from "antd";
import styled from "styled-components";
import SearchSelect from "../../../../components/SearchSelect";
import { treeSelectData } from "../../../../mockData";
import { GlobalOutlined } from "@ant-design/icons";
import { useTranslation } from "react-i18next";

export default function Attributes() {
  const [t] = useTranslation();
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [contentLoading, setContentLoading] = useState(true);
  const [activeKey, setActiveKey] = useState(null);
  const [isVisible, setIsVisible] = useState(false);

  const modalVisible = () => {
    setIsVisible(true);
  };

  const handleOk = () => {
    setIsVisible(false);
  };

  const handleCancel = () => {
    setIsVisible(false);
  };

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  const dataRequest = () => {
    setTimeout(() => {
      setData([
        {
          id: 1,
          name: "Açıklama",
        },
        {
          id: 2,
          name: "Kategori Bilgileri",
        },
        {
          id: 3,
          name: "Ürün Bilgi Fişi",
        },
        {
          id: 4,
          name: "Ebat Bilgileri",
        },
        {
          id: 5,
          name: "Teknik Bilgiler",
        },
      ]);

      setActiveKey(1);
      setLoading(false);
      contentDataRequest();
    }, 1500);
  };

  const contentDataRequest = () => {
    console.log(activeKey);

    setTimeout(() => {
      setContentLoading(false);
    }, 1500);
  };

  const onChangeHandleTab = (key) => {
    setContentLoading(true);
    setActiveKey(key);
    contentDataRequest();
  };

  useEffect(() => {
    dataRequest();
  }, []);

  return (
    <>
      <Modal
        title={t("chooselanguage")}
        visible={isVisible}
        onCancel={handleCancel}
        footer={[
          <Button key="back" onClick={handleCancel}>
            {t("cancel")}
          </Button>,
          <Button key="submit" type="primary" onClick={handleOk}>
            {t("save")}
          </Button>,
        ]}
      >
        <FormWrapper
          layout="vertical"
          name="basic"
          initialValues={{ remember: true }}
        >
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item label="English" name="username">
                <Input />
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item label="Spanish" name="username">
                <Input />
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item label="French" name="username">
                <Input />
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item label="Portuguese" name="username">
                <Input />
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item label="Turkish" name="username">
                <Input />
              </Form.Item>
            </Col>
          </Row>
        </FormWrapper>
      </Modal>
      {loading && skeletonWrapper()}
      {!loading && (
        <LeftTabsWrapper onChange={onChangeHandleTab} tabPosition={"left"}>
          {data.map((item) => (
            <>
              <Tabs.TabPane tab={item.name} key={item.id}>
                {contentLoading && skeletonWrapper()}
                {!contentLoading && (
                  <FormWrapper
                    layout="vertical"
                    name="basic"
                    initialValues={{ remember: true }}
                  >
                    <Row>
                      <Col span={8}>
                        <Form.Item label="Sales Price" name="username">
                          <Input.Group compact>
                            <Input style={{ width: "80%" }} />
                            <Button onClick={modalVisible}>
                              <GlobalOutlined />
                            </Button>
                          </Input.Group>
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row>
                      <Col span={8}>
                        <Form.Item
                          label="Marketing Description"
                          name="username"
                        >
                          <Input.Group compact>
                            <Input style={{ width: "80%" }} />
                            <Button onClick={modalVisible}>
                              <GlobalOutlined />
                            </Button>
                          </Input.Group>
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row>
                      <Col span={8}>
                        <Form.Item label="SAPCategory" name="username">
                          <Input.Group compact>
                            <Input style={{ width: "80%" }} />
                            <Button onClick={modalVisible}>
                              <GlobalOutlined />
                            </Button>
                          </Input.Group>
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row>
                      <Col span={8}>
                        <Form.Item label="ZOEM" name="username">
                          <Input.Group compact>
                            <Input style={{ width: "80%" }} />
                            <Button onClick={modalVisible}>
                              <GlobalOutlined />
                            </Button>
                          </Input.Group>
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row>
                      <Col span={8}>
                        <Form.Item label="YLKN123" name="username">
                          <Input.Group compact>
                            <Input style={{ width: "80%" }} />
                            <Button onClick={modalVisible}>
                              <GlobalOutlined />
                            </Button>
                          </Input.Group>
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row>
                      <Col span={8}>
                        <Form.Item label="Hob Type (Energy)" name="username">
                          <SearchSelect data={treeSelectData}></SearchSelect>
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row>
                      <Col span={8}>
                        <Form.Item label="Cam Rengi" name="username">
                          <SearchSelect data={treeSelectData}></SearchSelect>
                        </Form.Item>
                      </Col>
                    </Row>

                    <Row>
                      <Col span={4}>
                        <Button style={{ width: "100%" }} type="primary">
                          Kaydet
                        </Button>
                      </Col>
                    </Row>
                  </FormWrapper>
                )}
              </Tabs.TabPane>
            </>
          ))}
        </LeftTabsWrapper>
      )}
    </>
  );
}

const FormWrapper = styled(Form)`
  padding: 20px 30px;
`;

const LeftTabsWrapper = styled(Tabs)`
  background-color: ${({ theme }) => theme.mainColor};
  min-height: 50vh;

  .ant-tabs-nav {
    width: 200px;
  }

  .ant-tabs-content-holder {
    background-color: ${({ theme }) => theme.bodyColor};
  }
`;
