import React from "react";
import { Table } from "antd";
import PropTypes from "prop-types";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import { Down, Up } from "../icons";
export default function ContractTable({ data, columns, loading, ...props }) {
  const { location, push } = useHistory();
  const rowSelection = {
    onChange: (selectedRowKeys) => {
      push(`${location.pathname}/${selectedRowKeys[0]}`);
    },
    getCheckboxProps: (record) => ({
      name: record.MADDE_ADI,
    }),
  };

  const expandIcon = ({ expanded, onExpand, record }) => {
    if (!record.Associations) {
      return "";
    }
    return expanded ? (
      <Up
        style={{ color: "#2855ac", cursor: "pointer", marginRight: 15 }}
        onClick={(e) => onExpand(record, e)}
      />
    ) : (
      <Down
        style={{ color: "#2855ac", cursor: "pointer", marginRight: 15 }}
        onClick={(e) => onExpand(record, e)}
      />
    );
  };

  return (
    <StyledTable
      {...props}
      rowSelection={{
        type: "radio",
        ...rowSelection,
      }}
      expandable={{
        rowExpandable: (record) => record.name !== "Not Expandable",
        expandIcon,
      }}
      rowKey="Id"
      columns={columns}
      dataSource={data}
      loading={loading}
    />
  );
}

const StyledTable = styled(Table)`
  font-weight: 600;
  .ant-table-expanded-row {
    .ant-table-cell:first-child {
      padding: 0;
    }
  }
`;
ContractTable.propTypes = {
  data: PropTypes.any,
  columns: PropTypes.any,
  loading: PropTypes.bool,
};
