import React, { useState, useEffect } from "react";
import { Tabs, Skeleton } from "antd";
import styled from "styled-components";
import TreeView from "../../../../components/TreeView";

// Mock Data
import { treeData } from "../../../../mockData";

export default function Categories() {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [contentLoading, setContentLoading] = useState(true);
  const [activeKey, setActiveKey] = useState(null);

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  const dataRequest = () => {
    setTimeout(() => {
      setData([
        {
          id: 1,
          name: "Almanya SCHOBER",
        },
        {
          id: 2,
          name: "TR Site Ürünleri",
        },
        {
          id: 3,
          name: "Ürünler",
        },
        {
          id: 4,
          name: "Engelli",
        },
        {
          id: 5,
          name: "Ürün Gramı",
        },
      ]);

      setActiveKey(1);
      setLoading(false);
      contentDataRequest();
    }, 1500);
  };

  const contentDataRequest = () => {
    console.log(activeKey);

    setTimeout(() => {
      setContentLoading(false);
    }, 1500);
  };

  const onChangeHandleTab = (key) => {
    setContentLoading(true);
    setActiveKey(key);
    contentDataRequest();
  };

  useEffect(() => {
    dataRequest();
  }, []);

  return (
    <>
      {loading && skeletonWrapper()}
      {!loading && (
        <LeftTabsWrapper onChange={onChangeHandleTab} tabPosition={"left"}>
          {data.map((item) => (
            <Tabs.TabPane tab={item.name} key={item.id}>
              {contentLoading && skeletonWrapper()}
              {!contentLoading && (
                <TreeView
                  defaultExpandedKeys={["0-0"]}
                  checkable
                  treeData={treeData}
                />
              )}
            </Tabs.TabPane>
          ))}
        </LeftTabsWrapper>
      )}
    </>
  );
}

const LeftTabsWrapper = styled(Tabs)`
  background-color: ${({ theme }) => theme.mainColor};
  min-height: 50vh;

  .ant-tabs-nav {
    width: 200px;
  }

  .ant-tabs-content-holder {
    background-color: ${({ theme }) => theme.bodyColor};
  }
`;
