import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import {
  Row,
  Col,
  Steps,
  Skeleton,
  Form,
  Space,
  Card,
  Button,
  message,
} from "antd";
import styled from "styled-components";
import { useParams } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import ItemLine from "../../../components/Shared/ItemLine";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import CreateBar from "../../../components/Shared/CreateBar";
import Properties from "./tabs/properties";
import Permissions from "./tabs/permissions";
import Users from "./tabs/users";

export default function NewRole() {
  const [t] = useTranslation();
  const [form] = Form.useForm();
  const [step, setStep] = useState(0);
  const [values, setValues] = useState({});

  const getContent = () => {
    switch (step) {
      case 0:
        return <Properties />;
      case 1:
        return <Permissions />;
      default:
        return <Users />;
    }
  };

  const next = () => {
    form
      .validateFields()
      .then((formValues) => {
        setValues({
          ...values,
          ...formValues,
        });
        setStep(step + 1);
      })
      .catch((err) => console.log(err));
  };

  const prev = () => {
    if (step != 0) {
      setStep(step - 1);
    }
  };

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[{ label: "System" }, { label: "Roles", path: "/roles" }]}
        />
      </Container>

      <ItemLine>
        <Container>
          <CreateBar title={t("createRoles")} />
        </Container>
      </ItemLine>

      <Container>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Card>
              <StyledSteps current={step}>
                <Steps.Step key={0} title={t("Properties")} />
                <Steps.Step key={1} title={t("Permissions")} />
                <Steps.Step key={2} title={t("Users")} />
              </StyledSteps>

              <StepContent>
                <Form layout="vertical" form={form}>
                  {getContent()}
                  <Form.Item>
                    <Row>
                      <Col flex="auto" />
                      <Col>
                        <Space align="center">
                          {step != 0 && (
                            <Button type="primary" onClick={prev}>
                              Previous
                            </Button>
                          )}
                          {step != 2 && (
                            <Button type="primary" onClick={next}>
                              Next
                            </Button>
                          )}
                          {step == 2 && (
                            <Button type="primary" htmlType="submit">
                              Send
                            </Button>
                          )}
                        </Space>
                      </Col>
                    </Row>
                  </Form.Item>
                </Form>
              </StepContent>
            </Card>
          </Col>
        </Row>
      </Container>
    </DefaultLayout>
  );
}

const StyledSteps = styled(Steps)`
  padding: 20px;
`;

const StepContent = styled.div`
  padding: 20px;
`;
