import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import Templates from "../pages/turkcell/templates";
import Create from "../pages/turkcell/create";
// Components
import PrivateRoute from "../components/PrivateRoute";

const TemplateRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute path={`${url}/:categoryId/create`} component={Create} />
      <PrivateRoute path={`${url}/:categoryId?`} component={Templates} />
    </Switch>
  </>
);

TemplateRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default TemplateRoute;
