import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { Input, Tabs } from "antd";
import ReactBpmn from "react-bpmn";

const { TabPane } = Tabs;

import Layout from "../../components/Layouts/Layout";
import Container from "../../components/Layouts/Container";
import Content from "../../components/Content";
import BreadcrumbNav from "../../components/BreadcrumbNav";
import DefaultTable from "../../components/Table/DefaultTable";

const TaskDetail = () => {
  const [t] = useTranslation();
  const [filters, setFilters] = useState({});

  const navs = [{ label: t("TaskList") }, { label: t("TaskDetail") }];
  const columns = [
    {
      title: t("ActivityInstanceId"),
    },
    {
      title: t("Time"),
    },
    {
      title: t("TaskId"),
    },
    {
      title: t("VariableName"),
    },
    {
      title: t("Value"),
    },
    {
      title: t("FieldId"),
    },
    {
      title: t("FieldValue"),
    },
  ];

  const fethData = () => console.log("fetch");
  const handleChangeFilter = (e) => {
    const { value, name } = e.target;
    setFilters({ ...filters, [name]: value });
  };
  return (
    <Layout>
      <Container>
        <BreadcrumbNav navs={navs} />
        <Content title={t("TaskDetail")}>
          <Tabs tabPosition="left">
            <TabPane tab={t("Form")} key="1">
              Form
            </TabPane>
            <TabPane tab={t("History")} key="2">
              <DefaultTable columns={columns} />
            </TabPane>
            <TabPane tab={t("Diagram")} key="3">
              <ReactBpmn url="/test.bpmn" />
            </TabPane>
          </Tabs>
        </Content>
      </Container>
    </Layout>
  );
};

export default TaskDetail;
