import React, { useState, useEffect } from "react";
import { Row, Col, Checkbox, message, Input } from "antd";
import { CheckOutlined } from "@ant-design/icons";
import { useTranslation } from "react-i18next";
import DefaultTable from "../../components/Table/DefaultTable";
// Services
import { getSettingsItemTypes, settingSaveChange } from "../../services/items";

// Components
import DefaultLayout from "../../components/Layouts/Default";
import Container from "../../components/Layouts/Container";
import BreadcrumbNav from "../../components/BreadcrumbNav";
import Content from "../../components/Content";

export default function ItemTypes() {
  const { t } = useTranslation();
  const [typeGroups, setTypeGroups] = useState([]);
  const [itemTypes, setItemTypes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [inputValue, setInputValue] = useState("");

  const getColumns = () => [
    {
      title: t("Entity"),
      dataIndex: "Code",
    },
    {
      title: t("Type"),
      render: () => t("Standart"),
    },
    {
      title: t("ShowItem"),
      dataIndex: "ShowItem",
      align: "center",
      render(data, row) {
        return (
          <Checkbox
            checked={data}
            onChange={async (event) =>
              settingSaveChangeChecked(
                event.target.checked,
                `ShowScreen_${row.Code}`
              )
            }
          ></Checkbox>
        );
      },
    },
    {
      title: t("ShowCategoryItem"),
      dataIndex: "ShowCategoryItem",
      align: "center",
      render(data, row) {
        return (
          <Checkbox
            checked={data}
            onChange={async (event) =>
              settingSaveChangeChecked(
                event.target.checked,
                `ShowScreen_${row.Code}Categories`
              )
            }
          ></Checkbox>
        );
      },
    },
  ];

  const getItemTypes = async () => {
    try {
      const result = await getSettingsItemTypes();
      const value = result.data.Value;
      setItemTypes(value);
      setLoading(false);
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  };

  const settingSaveChangeChecked = async (checked, Code) => {
    try {
      setLoading(true);
      const reqData = {
        Code,
        Value: checked ? "true" : "false",
      };
      const result = await settingSaveChange(reqData);
      console.log(result);
      message.success(t(result.data.Value.Message));
      getItemTypes();
    } catch (err) {
      message.error("err");
    }
  };

  const handleOnEdit = async (data) => {
    alert(data.Id);
  };

  useEffect(() => {
    getItemTypes();
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("ItemTypes"), path: "/settings/itemtypes" },
          ]}
        ></BreadcrumbNav>
      </Container>
      <Container>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Content
              id="itemType"
              title={t("ItemTypes")}
              actionTitle={t("Save")}
            >
              <DefaultTable
                filters={
                  <>
                    <Input
                      placeholder="Entity"
                      onChange={(event) => {
                        setInputValue(event.target.value);
                      }}
                      value={inputValue}
                      name="Code"
                    />
                  </>
                }
                loading={loading}
                dataSource={itemTypes.filter((item) =>
                  item.Code.toLowerCase().includes(inputValue.toLowerCase())
                )}
                onReset={() => setInputValue("")}
                columns={getColumns()}
                actions
                onEdit={handleOnEdit}
              />
            </Content>
          </Col>
        </Row>
      </Container>
    </DefaultLayout>
  );
}
