import React from "react";
import ReactBpmn from "react-bpmn";
import styled from "styled-components";
import { Tabs, Input } from "antd";
import { useTranslation } from "react-i18next";
const { TabPane } = Tabs;

import Layout from "../../components/Layouts/Layout";
import Content from "../../components/Content";
import Container from "../../components/Layouts/Container";
import BreadcrumbNav from "../../components/BreadcrumbNav";
import DefaultTable from "../../components/Table/DefaultTable";

const ProcessDefinition = () => {
  const [t] = useTranslation();
  const navs = [{ label: t("BPMN") }, { label: t("Dashboard") }];
  const runTimeColumns = [
    { title: t("StartTime") },
    { title: t("ProcessDefinitionName") },
    { title: t("ProcessDefinitionKey") },
    { title: t("State") },
  ];
  const historyColumns = [
    { title: t("Id") },
    { title: t("StartTime") },
    { title: t("EndTime") },
    { title: t("BusinessKey") },
    { title: t("User") },
    { title: t("State") },
  ];

  return (
    <Layout>
      <Container>
        <BreadcrumbNav navs={navs} />
        <Content title={t("ProcessDefinition")}>
          <BpmnWrapper>
            <ReactBpmn url="/test.bpmn" />
          </BpmnWrapper>
          <Tabs onChange={() => console.log("sd")} type="card">
            <TabPane tab={t("RunTime")} key="1">
              <DefaultTable
                columns={runTimeColumns}
                filters={
                  <>
                    <Input
                      placeholder="Start Time"
                      style={{ width: 200, marginRight: 10 }}
                    />
                    <Input
                      placeholder="State"
                      style={{ width: 200, marginRight: 10 }}
                    />
                  </>
                }
              />
            </TabPane>
            <TabPane tab={t("History")} key="2">
              <DefaultTable columns={historyColumns} />
            </TabPane>
          </Tabs>
        </Content>
      </Container>
    </Layout>
  );
};

const BpmnWrapper = styled.div`
  margin-bottom: 20px;
  .react-bpmn-diagram-container {
    height: 300px;
  }
`;

export default ProcessDefinition;
