import React, { useState, useEffect } from "react";
import styled, { css } from "styled-components";
import { Menu, Spin } from "antd";
import { UpWhite, DownWhite } from "../icons";
import { useHistory, useParams, Link } from "react-router-dom";
import { useSelector } from "react-redux";

import logo from "../../assets/images/logo.png";

import { DEFAULT_CATEGORY } from "../../consts";

export default function Sidebar() {
  const history = useHistory();
  const { categoryId } = useParams();
  const categories = useSelector((state) => state.categories.data);
  const loading = useSelector((state) => state.categories.loading);
  const lang = useSelector((state) => state.localization.lang);

  const handleClick = (e) => {
    console.log("click ", e, categories);
  };

  const handleRoute = (data) => {
    const { id } = data;
    history.push({
      pathname: `/contracts/${id}`,
      state: data,
    });
  };

  return (
    <Wrapper>
      {loading ? (
        <SpinWrapper>
          <StyledSpin tip="Loading..." />
        </SpinWrapper>
      ) : (
        <StyledMenu
          onClick={handleClick}
          mode="inline"
          defaultOpenKeys={[
            categories.children &&
              categories.children.length &&
              categories.children[0].id,
          ]}
          inlineCollapsed={false}
          expandIcon={({ isOpen }) => (isOpen ? <UpIcon /> : <DownIcon />)}
        >
          <LogoWrapper to="/">
            <Logo src={logo} />
          </LogoWrapper>
          {categories.children &&
            categories.children.map(({ id, text, translations, children }) => (
              <StyledSubMenu
                key={id}
                title={translations[lang] ? translations[lang] : text}
              >
                {children.map((sub) => (
                  <MenuItem key={sub.id} onClick={() => handleRoute(sub)}>
                    {sub.translations[lang] ? sub.translations[lang] : sub.text}
                  </MenuItem>
                ))}
              </StyledSubMenu>
            ))}
        </StyledMenu>
      )}
    </Wrapper>
  );
}

const Wrapper = styled.div`
  width: 256px;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: ${({ theme }) => theme.turkcell.mainColor};
`;
const StyledMenu = styled(Menu)`
  background-color: ${({ theme }) => theme.turkcell.mainColor};
`;
const StyledSubMenu = styled(Menu.SubMenu)`
  color: white;
  font-size: 16px;
  font-weight: 600;
  .ant-menu-submenu-title {
    :hover {
      color: rgba(255, 255, 255, 0.7);
    }
  }
  .ant-menu-sub {
    background-color: #172b58;
    color: white;
    .ant-menu-item-selected {
      background-color: #0f1e3f;
      color: white;
      width: 100% !important;
      &::after {
        content: none;
      }
    }
  }
`;
const MenuItem = styled(Menu.Item)``;

const LogoWrapper = styled(Link)`
  padding: 15px;
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  padding-left: 24px;
`;
const Logo = styled.img`
  width: 50px;
  height: 50px;
`;
const iconStyles = css`
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s;
`;

const UpIcon = styled(UpWhite)`
  ${iconStyles}
`;
const DownIcon = styled(DownWhite)`
  ${iconStyles}
`;

const SpinWrapper = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 20px;
`;

const StyledSpin = styled(Spin)`
  color: white;
`;
