import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import AssociationTypes from "../pages/settings/associationTypes";
import EditAssociationType from "../pages/settings/editAssociationType/index";

// Components
import PrivateRoute from "../components/PrivateRoute";

const AssociationRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute path={`${url}/:id`} component={EditAssociationType} />
      <PrivateRoute path={`${url}`} component={AssociationTypes} />
    </Switch>
  </>
);

AssociationRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default AssociationRoute;
