import React, { useRef, useState } from "react";
import PropTypes from "prop-types";
import { Modal, Tabs, Form, Input, Select } from "antd";
import { useTranslation } from "react-i18next";
//import styled from "styled-components";
const { TabPane } = Tabs;
const { Option } = Select;

// Components
import TreeView from "../TreeView";
// Mock data
import { treeData } from "../../mockData";

export default function CreateProductModal({ isModalVisible, onCancel }) {
  const [category, setCategory] = useState(null);

  const [t] = useTranslation();
  const formRef = useRef(null);

  const onChange = () => {
    console.log("select change");
  };

  const onFinish = (values) => {
    alert(JSON.stringify({ category, ...values }));
  };

  const handleOk = () => {
    formRef.current.submit();
  };

  return (
    <>
      <Modal
        title="Create new - Product"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={onCancel}
        maskClosable={false}
      >
        <Tabs defaultActiveKey="1">
          <TabPane tab={t("infoItem")} key="1">
            <Form layout="vertical" onFinish={onFinish} ref={formRef}>
              <Form.Item
                label={t("uniqueCode")}
                name="uniqueCode"
                rules={[
                  {
                    required: true,
                    message: `Please input your ${t("uniqueCode")}!`,
                  },
                ]}
              >
                <Input />
              </Form.Item>
              <Form.Item
                label={t("family")}
                name="family"
                rules={[
                  {
                    required: true,
                    message: `Please input your ${t("family")}!`,
                  },
                ]}
              >
                <Select
                  showSearch
                  placeholder="Select a person"
                  optionFilterProp="children"
                  onChange={onChange}
                  filterOption={(input, option) =>
                    option.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                >
                  <Option value="jack">Jack</Option>
                  <Option value="lucy">Lucy</Option>
                  <Option value="tom">Tom</Option>
                </Select>
              </Form.Item>
            </Form>
          </TabPane>
          <TabPane tab={t("categories")} key="2">
            <Tabs tabPosition="left">
              <TabPane tab="Category 1" key="1">
                <TreeView
                  treeData={treeData}
                  onClickTree={(data) => setCategory(data[0])}
                />
              </TabPane>
              <TabPane tab="Category 2" key="2">
                <TreeView treeData={treeData} />
              </TabPane>
              <TabPane tab="Category 3" key="3">
                <TreeView treeData={treeData} />
              </TabPane>
            </Tabs>
          </TabPane>
        </Tabs>
      </Modal>
    </>
  );
}

CreateProductModal.propTypes = {
  isModalVisible: PropTypes.bool.isRequired,
  onCancel: PropTypes.func,
};
