import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";
import { useParams } from "react-router-dom";
import { Row, Col, Form, Button, Checkbox, Input, message } from "antd";

// Services
import { EditRoleModel } from "../../../../services/roles";

const Permissions = ({ roleInfo }) => {
  const [t] = useTranslation();
  const { id } = useParams();
  const [form] = Form.useForm();

  const Permissions = roleInfo.Permissions.sort((a, b) =>
    a.Label.localeCompare(b.Label)
  );

  const defaultPermissions = roleInfo.Permissions.reduce((result, item) => {
    if (item.IsSelected) {
      result.push(item.Label);
    }
    return result;
  }, []);

  const [checkedList, setCheckedList] = React.useState(defaultPermissions);

  const onChange = (list) => {
    setCheckedList(list);
  };

  const onFinish = async (values) => {
    message.loading({ content: "Loading...", key: "onFinish" });

    try {
      await EditRoleModel({
        "role.id": id,
        "role.name": roleInfo.Name,
        Users: roleInfo.Users.reduce((result, item) => {
          result.push(item.Id);

          return result;
        }, []),
        UnSelectedUsers: [],
        Permissions: checkedList,
        ConfigurationPermissions:
          roleInfo.Configuration == null
            ? JSON.stringify([])
            : JSON.stringify(roleInfo.Configuration),
      });

      message.success({ content: "Saved!", key: "onFinish" });
    } catch (err) {
      message.error({ content: "There is error!", key: "onFinish" });
    }
  };

  useEffect(() => {}, []);

  return (
    <Form layout="horizontal" form={form} name="basic" onFinish={onFinish}>
      <Checkbox.Group defaultValue={defaultPermissions} onChange={onChange}>
        <Row>
          {Permissions.map((item) => (
            <>
              <Col span={6}>
                <Checkbox value={item.Label}>{item.Label}</Checkbox>
              </Col>
            </>
          ))}
        </Row>
      </Checkbox.Group>

      <Row>
        <Col span={24}>
          <Row>
            <Col flex="auto"></Col>
            <Col>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  {t("save")}
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Col>
      </Row>
    </Form>
  );
};

Permissions.propTypes = {
  roleInfo: PropTypes.any,
};

export default Permissions;
