import React, { useState, useEffect } from "react";
import { Tabs, Checkbox, Tag, Skeleton } from "antd";
import styled from "styled-components";
import DefaultTable from "../../../../components/Table/DefaultTable";

// Mock Data
import { tableData } from "../../../../mockData";

const columns1 = [
  {
    title: "ID",
    dataIndex: "",
    // eslint-disable-next-line react/display-name
    render: () => <span>1</span>,
  },
  {
    title: "",
    dataIndex: "",
    // eslint-disable-next-line react/display-name
    render: () => <Checkbox></Checkbox>,
  },
  {
    title: "İlişkili",
    dataIndex: "age",
    // eslint-disable-next-line react/display-name
    render: () => <Tag color="green">Evet</Tag>,
  },
  {
    title: "Kod",
    dataIndex: "address",
    filterMultiple: false,
    // eslint-disable-next-line react/display-name
    render: () => <span>Mock Data</span>,
  },
  {
    title: "Açıklama",
    dataIndex: "address",
    filterMultiple: false,
    // eslint-disable-next-line react/display-name
    render: () => <span>Mock Data</span>,
  },
  {
    title: "Aile",
    dataIndex: "address",
    filterMultiple: false,
    // eslint-disable-next-line react/display-name
    render: () => <span>DIGITAL_ASSET</span>,
  },
  {
    title: "Statü",
    dataIndex: "age",
    // eslint-disable-next-line react/display-name
    render: () => <Tag color="green">Aktif</Tag>,
  },
];

export default function RelationShips() {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [contentLoading, setContentLoading] = useState(true);
  const [activeKey, setActiveKey] = useState(null);

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  const dataRequest = () => {
    setTimeout(() => {
      setData([
        {
          id: 1,
          name: "Ürün - Dijital Varlık",
        },
        {
          id: 2,
          name: "Web Görselleri",
        },
        {
          id: 3,
          name: "Ürün Detay Görselleri",
        },
        {
          id: 4,
          name: "Ürün Detay Videoları",
        },
        {
          id: 5,
          name: "WEB TR DOKUMANLARI",
        },
      ]);

      setActiveKey(1);
      setLoading(false);
      contentDataRequest();
    }, 1500);
  };

  const contentDataRequest = () => {
    console.log(activeKey);

    setTimeout(() => {
      setContentLoading(false);
    }, 1500);
  };

  const onChangeHandleTab = (key) => {
    setContentLoading(true);
    setActiveKey(key);
    contentDataRequest();
  };

  useEffect(() => {
    dataRequest();
  }, []);

  return (
    <>
      {loading && skeletonWrapper()}
      {!loading && (
        <LeftTabsWrapper onChange={onChangeHandleTab} tabPosition={"left"}>
          {data.map((item) => (
            <Tabs.TabPane tab={item.name} key={item.id}>
              <LeftTabsContentWrapper>
                {contentLoading && skeletonWrapper()}
                {!contentLoading && (
                  <DefaultTable
                    columns={columns1}
                    dataSource={tableData}
                    pagination={false}
                  />
                )}
              </LeftTabsContentWrapper>
            </Tabs.TabPane>
          ))}
        </LeftTabsWrapper>
      )}
    </>
  );
}

const LeftTabsContentWrapper = styled.div`
  padding: 10px;
`;

const LeftTabsWrapper = styled(Tabs)`
  background-color: ${({ theme }) => theme.mainColor};
  min-height: 50vh;

  .ant-tabs-nav {
    width: 200px;
  }

  .ant-tabs-content-holder {
    background-color: ${({ theme }) => theme.bodyColor};
  }
`;
