import React from "react";
import { Button } from "antd";
import styled from "styled-components";
import PropTypes from "prop-types";

export default function CustomButton({ title, btnType, ...props }) {
  return (
    <StyledButton {...props} btnType={btnType}>
      {title}
    </StyledButton>
  );
}

const StyledButton = styled(Button)`
  width: 160px;
  padding: 7px 18px 8px;
  border-radius: 27.5px;
  margin-left: 10px;
  font-size: 14px;
  line-height: 1;
  ${({ btnType }) => {
    if (btnType == "white") {
      return ` 
        background-color: white;
        border: solid 1px #253342;
        color: #253342;
      `;
    } else if (btnType == "yellow") {
      return `
        background-color: #ffc900;
        color: #253342;
        border: solid 1px #ffc900;

        `;
    } else {
      return `
          border: solid 1px #2855ac;
          background-color: #2855ac;
          color: white;
        `;
    }
  }};
`;

CustomButton.propTypes = {
  title: PropTypes.string,
  btnType: PropTypes.string,
};
