import React from "react";
import { useTranslation } from "react-i18next";
import { Modal, Button, Form, Input, message } from "antd";
import PropTypes from "prop-types";

import { createAssociationType } from "../../services/assocations";

const CreateAssociationType = ({ isModalVisible, onCancel }) => {
  const [t] = useTranslation();
  const [form] = Form.useForm();

  const handleSave = () => {
    form.submit();
  };

  const onFinish = async ({ Code }) => {
    message.loading({ content: "Loading...", key: "createAssociation" });
    await createAssociationType(Code);
    message.success({
      content: "Association Type oluşturuldu...",
      key: "createAssociation",
    });
    form.resetFields();
    onCancel();
  };

  return (
    <Modal
      visible={isModalVisible}
      title={t("createnewassociationtype")}
      okButtonProps={null}
      onCancel={onCancel}
      footer={[
        <Button key="submit" type="primary" onClick={handleSave}>
          Save
        </Button>,
      ]}
    >
      <Form layout="vertical" form={form} onFinish={onFinish}>
        <Form.Item
          label="Code"
          name="Code"
          rules={[{ required: true, message: "Please input your code!" }]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  );
};

CreateAssociationType.propTypes = {
  isModalVisible: PropTypes.bool.isRequired,
  onCancel: PropTypes.func,
};

export default CreateAssociationType;
