import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Input, Space, message } from "antd";
import { useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import Content from "../../../components/Content";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import DefaultTable from "../../../components/Table/DefaultTable";

// Components
import { RolesList } from "../../../services/roles";

export default function Roles() {
  let history = useHistory();
  const { t } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [roles, setRolesList] = useState([]);
  const [filters, setFilters] = useState({});
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });

  const getColumns = () => [
    {
      title: t("Name"),
      dataIndex: "Name",
      key: "Name",
    },
  ];

  const getResourceRoles = async (pagination) => {
    const { PageSize, CurrentPage, data } = pagination;
    setLoading(true);

    try {
      const result = await RolesList({
        PageSize,
        CurrentPage: CurrentPage - 1,
        ...filters,
      });

      setRolesList(result.data.data);
      setPagination({ ...data, total: result.data.recordsTotal });
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getResourceRoles({
      PageSize: pageSize,
      CurrentPage: current,
      ...filters,
    });
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  const onEdit = ({ Id }) => {
    history.push(`/system/roles/${Id}`);
  };

  const onNew = () => {
    history.push(`/system/roles/newRole`);
  };

  useEffect(() => {
    getResourceRoles({
      PageSize: 10,
      CurrentPage: 1,
    });
  }, [filters]);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[{ label: t("System") }, { label: t("Roles"), path: "#" }]}
        ></BreadcrumbNav>
      </Container>
      <Container>
        <Content
          title={t("Roles")}
          actionTitle={t("CreateNew")}
          onClick={onNew}
        >
          <Row>
            <Col span={24}>
              <DefaultTable
                columns={getColumns()}
                onReset={() => setFilters({})}
                dataSource={roles}
                loading={loading}
                style={{ marginTop: "15px" }}
                scroll={{ x: "1000px" }}
                actions
                onEdit={onEdit}
                pagination={pagination}
                onChange={handleTableChange}
                filters={
                  <Space size={10}>
                    <Input
                      name="RoleName"
                      placeholder={t("Name")}
                      value={filters.RoleName}
                      style={{ width: 200 }}
                      onChange={(val) => filterChange(val)}
                    />
                  </Space>
                }
              />
            </Col>
          </Row>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
