import React, { useState, useEffect } from "react";
import { Row, Col, Button, Carousel, Skeleton } from "antd";
import { DownloadOutlined } from "@ant-design/icons";
import styled from "styled-components";

import CarouselImage1 from "../../../../assets/images/carousel-1.jpeg";
import CarouselImage2 from "../../../../assets/images/carousel-2.jpeg";

export default function Preview() {
  const [loading, setLoading] = useState(true);

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  const dataRequest = () => {
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  };

  useEffect(() => {
    dataRequest();
  }, []);

  return (
    <>
      {loading && skeletonWrapper()}
      {!loading && (
        <Row gutter={[0, 16]} justify="space-between">
          <Col span={8}>
            <DownloadWrapper>
              <Button
                style={{ width: "100%" }}
                type="primary"
                icon={<DownloadOutlined></DownloadOutlined>}
              >
                Lorem İpsumm
              </Button>
              <Button
                style={{ width: "100%" }}
                type="primary"
                icon={<DownloadOutlined></DownloadOutlined>}
              >
                Lorem İpsumm
              </Button>
            </DownloadWrapper>
          </Col>
          <Col span={14}>
            <Carousel dots autoplay arrows>
              <CarouselItem>
                <img src={CarouselImage1}></img>
              </CarouselItem>
              <CarouselItem>
                <img src={CarouselImage2}></img>
              </CarouselItem>
            </Carousel>
          </Col>
        </Row>
      )}
    </>
  );
}

const DownloadWrapper = styled.div`
  background-color: #fff;
  padding: 10px 20px;

  button {
    &:first-child {
      margin-bottom: 10px;
    }
  }
`;

const CarouselItem = styled.div`
  height: 350px;
  backgorund-color: red;
  display: flex;
  align-items: center;
`;
