import { Check } from "../components/icons";
import StatusIcon from "../components/Table/Columns/Status";
import Actions from "../components/Table/Columns/Actions";

const columnStyles = {
  color: "#4d4d4d",
  textAlign: "center",
  border: "1px solid white",
};

const CheckIcon = styled(Check)`
  color: ${({ theme }) => theme.turkcell.mainColor};
  font-size: 24px;
`;

