export const getDetailTableColumns = (category, onLike, t) => {
  const { id, code } = category;

  return [
    {
      title: `${code} ${t("Clauses")}`,
      dataIndex: "MADDE_ADI",
    },
    {
      title: t("Required"),
      dataIndex: "MADDE_ZORUNLU",
      textWrap: "word-break",
      width: 100,
      ellipsis: true,
      render(text, record) {
        return {
          props: {
            style: {
              ...columnStyles,
              backgroundColor: `rgba(231, 208, 215, ${
                record["MADDE_ZORUNLU"] ? "1" : 0.4
              })`,
            },
          },
          children: record["MADDE_ZORUNLU"] ? <CheckIcon /> : "",
        };
      },
    },
    {
      title: t("Editable"),
      textWrap: "word-break",
      width: 100,
      ellipsis: true,
      dataIndex: "MADDE_DUZENLENEBILIR",
      render: (text, record) => {
        return {
          props: {
            style: {
              ...columnStyles,
              backgroundColor: `rgba(221, 231, 208, ${
                record["MADDE_DUZENLENEBILIR"] ? "1" : 0.4
              })`,
            },
          },
          children: record["MADDE_DUZENLENEBILIR"] ? <CheckIcon /> : "",
        };
      },
    },
    {
      title: t("Commitment"),
      dataIndex: "MADDE_TAAHHUT",
      textWrap: "word-break",
      width: 100,
      ellipsis: true,
      render(text, record) {
        return {
          props: {
            style: {
              ...columnStyles,
              backgroundColor: `rgba(208, 212, 231, ${
                record["MADDE_TAAHHUT"] ? "1" : 0.4
              })`,
            },
          },
          children: record["MADDE_TAAHHUT"] ? <CheckIcon /> : "",
        };
      },
    },
    {
      title: t("Revised"),
      dataIndex: "MADDE_REVIZYON",
      textWrap: "word-break",
      width: 100,
      ellipsis: true,
      render(text, record) {
        return {
          props: {
            style: {
              ...columnStyles,
              backgroundColor: `rgba(208, 221, 231, ${
                record["MADDE_REVIZYON"] ? "1" : 0.4
              })`,
            },
          },
          children: record["MADDE_REVIZYON"] ? <CheckIcon /> : "",
        };
      },
    },
    {
      title: "",
      width: 100,
      render(row) {
        return <Actions row={row} categoryId={id} onLike={onLike} />;
      },
    },
  ];
};

export const getColumnsKeys = (columns) =>
  columns.map((column) => column.dataIndex).filter((column) => column);

export const getContractTableColumns = (category, onLike, t) => {
  const { id } = category;

  return [
    {
      title: `${category?.code} ${t("Clauses")}`,
      dataIndex: "MADDE_ADI",
    },
    {
      title: t("AltClause"),
      dataIndex: "",
      width: 100,
      ellipsis: true,
      align: "center",
      sorter: (a, b) => {
        const first =
          a.Associations && a.Associations.length
            ? a.Associations[0].Items.length
            : 0;
        const second =
          b.Associations && b.Associations.length
            ? b.Associations[0].Items.length
            : 0;
        return first - second;
      },
      render({ Associations }) {
        return Associations && Associations.length
          ? Associations[0].Items.length
          : 0;
      },
    },
    {
      title: t("Status"),
      dataIndex: "ItemStatusesId",
      width: 100,
      ellipsis: true,
      align: "center",
      sorter: (a, b) => {
        return !a || !b ? 0 : a - b;
      },
      render(id) {
        return <StatusIcon status={id} />;
      },
    },
    {
      title: "",
      width: 100,
      render(row) {
        return <Actions row={row} categoryId={id} onLike={onLike} />;
      },
    },
  ];
};

