import { privateAxiosInstance } from "./api";
import {
  ITEMTYPEGROUPS,
  ITEMTYPES,
  RESOURCETYPEGROUPS,
  GET_ACTIONS,
  SETTING_SAVE_CHANGE,
  GET_SETTINGS_ITEM_TYPES,
} from "../consts/api";

export async function ItemTypes() {
  return await privateAxiosInstance.get(ITEMTYPES);
}

export async function ItemTypeGroups() {
  return await privateAxiosInstance.get(ITEMTYPEGROUPS);
}

export async function ResourceTypeGroups() {
  return await privateAxiosInstance.get(RESOURCETYPEGROUPS);
}

export async function getActions(type) {
  return await privateAxiosInstance.post(`${GET_ACTIONS}?itemTypeName=${type}`);
}

export async function settingSaveChange(data) {
  return await privateAxiosInstance.post(SETTING_SAVE_CHANGE, [data]);
}

export async function getSettingsItemTypes() {
  return await privateAxiosInstance.get(GET_SETTINGS_ITEM_TYPES);
}
