import { useSelector } from "react-redux";

export const getCategory = (selectedCategoryId) => {
  return useSelector((state) => {
    const { children, ...data } = state.categories.data;
    let categories = [data];
    if (children) {
      children.forEach((category) => {
        categories = [...categories, category, ...category.children];
      });
      return categories.find((category) => category.id == selectedCategoryId);
    }
    return {};
  });
};
