import React, { useState, useEffect } from "react";
import { Tabs, Card, Form, message, Skeleton } from "antd";
import styled from "styled-components";
import { useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router-dom";

// Components
import Layout from "../../../components/turkcell/Layout";
import Content from "../../../components/turkcell/Content";
import Button from "../../../components/turkcell/Button";
import ActionsModal from "../../../components/Modal/ActionsModal";
import {
  Detail,
  AssociationTemplate,
  History,
  Associations,
  Access,
} from "./tabs";

// Helpers
import {
  updateItemReqData,
  createItemReqData,
  getAttirbuteValue,
  getAssociationWithAttributes,
  getItemAttributesByKey,
} from "../../../helpers/attribute";

// Services
import {
  getItem,
  createItem,
  toggleFavItem,
} from "../../../services/turkcell/items";
import { getAttributes } from "../../../services/turkcell/attributes";
import { getUsers } from "../../../services/users";
import { getCategory } from "../../../store/selectors/category";
import { getItems } from "../../../services/turkcell/items";

const { TabPane } = Tabs;

export default function ItemDetail() {
  let history = useHistory();
  const [form] = Form.useForm();
  const { id, categoryId } = useParams();
  const [t] = useTranslation();
  const [activeKey, setActiveKey] = useState("1");
  const [loading, setLoading] = useState(true);
  const [item, setItem] = useState({});
  const [attributes, setAttributes] = useState([]);
  const [createLoading, setCreateLoading] = useState(false);
  const [tags, setTags] = useState([]);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const category = getCategory(categoryId);

  const navs = [
    {
      label: t("Home"),
      path: "/",
    },
    {
      label: category?.code,
      path: `/contracts/${categoryId}`,
    },
    {
      label: `${t("Clause")} ${id == "create" ? t("Create") : t("Detail")}`,
    },
  ];

  const handleChange = async (key) => {
    try {
      await form.validateFields();
      setActiveKey(key);
    } catch (e) {
      e.errorFields.forEach((err) => {
        message.error(err.errors[0]);
      });
    }
  };

  const onFinish = async (values) => {
    const reqData =
      id == "create"
        ? createItemReqData(attributes, values, categoryId)
        : updateItemReqData(item, attributes, values);
    try {
      setCreateLoading(true);
      await createItem(reqData);
      setCreateLoading(false);
      message.success(t("Success"));
      if (id == "create") {
        history.push(`/contracts/${categoryId}`);
      }
    } catch (err) {
      setCreateLoading(false);
    }
  };

  const getInitialValues = () => {
    if (attributes) {
      return attributes.reduce((acc, curr) => {
        const { Code } = curr;
        acc[Code] = getAttirbuteValue(curr, getItemAttributesByKey(item));
        return acc;
      }, {});
    }
    return {};
  };

  const handleOnLike = async ({ Id, IsFavorite }) => {
    message.loading({ content: t("Loading"), key: "fav" });
    await toggleFavItem(Id);
    const Associations = item.Associations.map((assocation) => {
      if (assocation.Id == Id) {
        assocation.IsFavorite = !assocation.IsFavorite;
      }
      return assocation;
    });
    message.success({
      content: IsFavorite ? t("RemovedFromFavorites") : t("AddedToFavorites"),
      key: "fav",
    });
    setItem({
      ...item,
      Associations,
    });
  };

  useEffect(() => {
    if (attributes.length) {
      form.setFieldsValue(getInitialValues());
      console.log(getInitialValues());
    }
  }, [attributes, item, id]);

  useEffect(() => {
    async function fetchData() {
      const attributesRes = await getAttributes({ itemType: "Clause" });
      const itemsRes = await getItems({
        ChannelId: 2,
        ItemType: "Tag",
        SelectedCategoryId: 74,
        familyId: 26,
      });
      let attributes = attributesRes.data.Value.data;
      if (attributes.some(({ Type }) => Type == "User List")) {
        const usersRes = await getUsers();
        attributes = attributes.map((attr) => {
          if (attr.Type == "User List") {
            attr.AttributeOptions = usersRes.data.data;
          }
          return attr;
        });
      }
      if (id != "create") {
        const itemRes = await getItem(id);
        const item = itemRes.data.Value;
        const associations = item.Associations;
        item.Associations = getAssociationWithAttributes(associations);
        setItem(item);
      }
      setAttributes(attributes);
      setTags(itemsRes.data.data);
      setLoading(false);
    }
    fetchData();
  }, []);

  return (
    <Layout>
      <Content navs={navs}>
        <ActionsModal
          isModalVisible={isModalVisible}
          onCancel={() => setIsModalVisible(false)}
        />
        <Card>
          <Form form={form} onFinish={onFinish}>
            {loading ? (
              <>
                <Skeleton active />
                <Skeleton active />
                <Skeleton active />
              </>
            ) : (
              <StyledTab activeKey={activeKey} onChange={handleChange}>
                <TabPane
                  tab={
                    <TabTitleWrapper>
                      <Title>{t("Detail")}</Title>
                    </TabTitleWrapper>
                  }
                  key="1"
                >
                  <Detail
                    attributes={attributes}
                    item={item}
                    onLike={handleOnLike}
                    tags={tags}
                  />
                </TabPane>
                <TabPane
                  tab={
                    <TabTitleWrapper>
                      <Title>{t("Access")}</Title>
                    </TabTitleWrapper>
                  }
                  key="2"
                >
                  <Access attributes={attributes} />
                </TabPane>
                {id != "create" && (
                  <>
                    <TabPane
                      tab={
                        <TabTitleWrapper>
                          <Title>{t("AssociatedClauses")}</Title>
                        </TabTitleWrapper>
                      }
                      key="3"
                    >
                      <Associations />
                    </TabPane>
                    <TabPane
                      tab={
                        <TabTitleWrapper>
                          <Title>{t("AssociatedTemplates")}</Title>
                        </TabTitleWrapper>
                      }
                      key="4"
                    >
                      <AssociationTemplate />
                    </TabPane>
                    <TabPane
                      tab={
                        <TabTitleWrapper>
                          <Title>{t("AssociatedContracts")}</Title>
                        </TabTitleWrapper>
                      }
                      key="5"
                    >
                      Content of Tab Pane 5
                    </TabPane>
                    <TabPane
                      tab={
                        <TabTitleWrapper>
                          <Title>Kullanım Yönlendirme</Title>
                        </TabTitleWrapper>
                      }
                      key="6"
                    >
                      Content of Tab Pane 6
                    </TabPane>
                    <TabPane
                      tab={
                        <TabTitleWrapper>
                          <Title>{t("History")}</Title>
                        </TabTitleWrapper>
                      }
                      key="7"
                    >
                      <History />
                    </TabPane>
                  </>
                )}
              </StyledTab>
            )}
            <ButtonsWrapper>
              <Button
                id="send-approve"
                title={t("SendtoApprove")}
                btnType="yellow"
                onClick={() => setIsModalVisible(true)}
              />
              <Button
                id="clause-save"
                title={t("Save")}
                htmlType="submit"
                loading={createLoading}
              />
            </ButtonsWrapper>
          </Form>
        </Card>
      </Content>
    </Layout>
  );
}
const StyledTab = styled(Tabs)`
  .ant-tabs-ink-bar {
    height: 4px !important;
    background-color: #00bafc;
  }
  .ant-tabs-tab-active {
    span {
      color: rgba(37, 51, 66, 1) !important;
    }
  }
`;
const ButtonsWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
`;

const TabTitleWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 120px;
`;
const Title = styled.span`
  font-size: 12px;
  color: rgba(37, 51, 66, 0.4);
  font-weight: bold;
  margin-top: 10px;
`;
