import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import Contracts from "../pages/turkcell/items";
import ItemDetail from "../pages/turkcell/itemDetail";
import Summary from "../pages/turkcell/summary";

// Components
import PrivateRoute from "../components/PrivateRoute";

const ContractRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute
        path={`${url}/:categoryId/:id/summary`}
        component={Summary}
      />
      <PrivateRoute path={`${url}/:categoryId/:id`} component={ItemDetail} />
      <PrivateRoute path={`${url}/:categoryId?`} component={Contracts} />
    </Switch>
  </>
);

ContractRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default ContractRoute;
