import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Select } from "antd";
const { Option } = Select;

export default function CustomSelect({
  onChange,
  options,
  valueAttribute,
  labelAttribute,
  ...props
}) {
  return (
    <StyledSelect {...props} onChange={onChange} placeholder="Seçiniz">
      {options.map((opt) => (
        <Option key={opt[valueAttribute]} value={opt[valueAttribute]}>
          {opt[labelAttribute]}
        </Option>
      ))}
    </StyledSelect>
  );
}

const StyledSelect = styled(Select)`
  height: 24px;
  .ant-select-selector {
    border: 0px !important;
    padding: 0px !important;
    border-color: inherit !important;
    box-shadow: none !important;
  }
  .ant-select-selection-placeholder {
    left: 0px !important;
  }
`;
CustomSelect.defaultProps = {
  labelAttribute: "tr_TR",
  valueAttribute: "Value",
};

CustomSelect.propTypes = {
  onChange: PropTypes.func,
  options: PropTypes.any,
  valueAttribute: PropTypes.string.isRequired,
  labelAttribute: PropTypes.string.isRequired,
};
