import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import Modeler from "../pages/bpmn/modeler";
import Dashboard from "../pages/bpmn/dashboard";
import History from "../pages/bpmn/history";
import ProcessDefinition from "../pages/bpmn/processDefinition";
import TaskList from "../pages/bpmn/taskList";
import TaskDetail from "../pages/bpmn/taskDetail";
import ProcessInstance from "../pages/bpmn/instance";

// Components
import PrivateRoute from "../components/PrivateRoute";

const AssociationRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute
        path={`${url}/processInstance`}
        component={ProcessInstance}
      />
      <PrivateRoute path={`${url}/taskDetail`} component={TaskDetail} />
      <PrivateRoute path={`${url}/taskList`} component={TaskList} />
      <PrivateRoute
        path={`${url}/processDefinition/:id?`}
        component={ProcessDefinition}
      />
      <PrivateRoute path={`${url}/history`} component={History} />
      <PrivateRoute path={`${url}/dashboard`} component={Dashboard} />
      <PrivateRoute path={`${url}/modeler`} component={Modeler} />
    </Switch>
  </>
);

AssociationRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default AssociationRoute;
