import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import UserManage from "../pages/system/userManage";
import EditUser from "../pages/system/editUser";

// Components
import PrivateRoute from "../components/PrivateRoute";

const UsersRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute path={`${url}/:id`} component={EditUser} />
      <PrivateRoute path={`${url}`} component={UserManage} />
    </Switch>
  </>
);

UsersRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default UsersRoute;
