import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Card, Input, Space, message } from "antd";
import { useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../components/Layouts/Default";
import Container from "../../components/Layouts/Container";
import BreadcrumbNav from "../../components/BreadcrumbNav";
import DefaultTable from "../../components/Table/DefaultTable";
import CreateFamily from "../../components/Modal/CreateFamily";
import Content from "../../components/Content";

import { getFamilies, deleteFamily } from "../../services/families";

export default function Families() {
  let history = useHistory();
  const [t] = useTranslation();
  const [loading, setLoading] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [families, setFamilies] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });
  const [filters, setFilters] = useState({});
  const [columns, setColumns] = useState([
    {
      title: t("Code"),
      dataIndex: "Code",
      key: "Code",
    },
    {
      title: t("Label"),
      dataIndex: "Label",
      key: "Label",
    },
    {
      title: t("ResourceType"),
      dataIndex: "ResourceType",
      key: "Type",
    },
  ]);

  const handleCreate = (data) => {
    setFamilies([data, ...families]);
    setIsModalVisible(false);
  };
  const onDeleteMultiple = async (attributeIdList) => {
    console.log("deleteList", attributeIdList);
  };

  const onDelete = async ({ Id }) => {
    try {
      message.loading({ content: t("loading"), key: "deleteFamily" });
      await deleteFamily(Id);
      const familyList = families.filter((family) => {
        return family.Id !== Id;
      });
      setFamilies(familyList);
      message.success({
        content: t("deleteFamilySuccessMessage"),
        key: "deleteFamily",
        duration: 4,
      });
    } catch (e) {
      message.error({
        content: e.message || e.Message,
        key: "deleteFamily",
        duration: 4,
      });
    }
  };

  const fetchFamilies = async (pagination) => {
    const { PageSize, CurrentPage } = pagination;
    setLoading(true);
    try {
      const res = await getFamilies({
        PageSize,
        CurrentPage: CurrentPage,
        ...filters,
      });
      const data = res.data.Value.Result;
      setPagination({
        current: CurrentPage + 1,
        pageSize: PageSize,
        total: data.recordsTotal,
      });
      setFamilies(data.data);
      setLoading(false);
    } catch (e) {
      setLoading(false);
    }
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await fetchFamilies({
      PageSize: pageSize,
      CurrentPage: current - 1,
    });
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  useEffect(() => {
    fetchFamilies({
      PageSize: 10,
      CurrentPage: 0,
    });
  }, [filters]);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("Families"), path: "/families" },
          ]}
        />
      </Container>
      <Container>
        <Content
          title={t("FamilyOverview")}
          actionTitle={t("CreateNew")}
          onClick={() => setIsModalVisible(true)}
        >
          <CreateFamily
            isModalVisible={isModalVisible}
            onCancel={() => setIsModalVisible(false)}
            onCreate={handleCreate}
          />
          <Row style={{ marginTop: "20px" }}>
            <Col span={24}>
              <DefaultTable
                onDeleteMultiple={onDeleteMultiple}
                onReset={() => setFilters({})}
                onRefresh={() =>
                  fetchFamilies({
                    PageSize: 10,
                    CurrentPage: 0,
                  })
                }
                onDelete={onDelete}
                actions
                onEdit={({ Id }) => history.push(`/families/${Id}`)}
                loading={loading}
                dataSource={families}
                columns={columns}
                pagination={pagination}
                onChange={handleTableChange}
                filters={
                  <Space size={10}>
                    <Input
                      name="Code"
                      placeholder={t("Code")}
                      value={filters.Code}
                      style={{ width: 200 }}
                      onChange={(val) => filterChange(val)}
                    />
                    <Input
                      name="Label"
                      placeholder={t("Label")}
                      value={filters.Label}
                      style={{ width: 200 }}
                      onChange={(val) => filterChange(val)}
                    />
                  </Space>
                }
              />
            </Col>
          </Row>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
