import { privateAxiosInstance } from "./api";
import {
  GET_DATA_GROUPS,
  GET_INFO_GROUP,
  GET_GROUP_USERS,
} from "../consts/api";

export async function GroupsList(filter = {}) {
  return await privateAxiosInstance.post(GET_DATA_GROUPS, filter);
}

export async function GetInfoGroup(id) {
  return await privateAxiosInstance.get(`${GET_INFO_GROUP}?id=${id}`);
}

export async function GroupUsers(data) {
  return await privateAxiosInstance.post(GET_GROUP_USERS, data);
}
