import { SET_LANG } from "../consts/localization";

const initialState = {
  lang: localStorage.getItem("lang") ? localStorage.getItem("lang") : "tr-TR",
};

export default function alertReducer(state = initialState, action) {
  const { type, lang } = action;
  switch (type) {
    case SET_LANG: {
      return {
        ...state,
        lang,
      };
    }
    default:
      return state;
  }
}
