import React, { useState } from "react";
import PropTypes from "prop-types";
import { message } from "antd";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router-dom";

// Components
import DefaultTable from "../../../../components/Table/DefaultTable";

// Services
import { removeAttributeGroup } from "../../../../services/attributes";

export default function Attributes({ attributes }) {
  const { t } = useTranslation();
  const [dataSource, setDataSource] = useState(attributes);
  const history = useHistory();
  const columns = [
    {
      title: t("Code"),
      dataIndex: "Code",
    },
    {
      title: t("Type"),
      dataIndex: "AttributeType",
    },
    {
      title: "",
      dataIndex: "",
    },
  ];

  const deleteHandler = async ({ Id }) => {
    const res = await removeAttributeGroup(Id);
    const { Value, StatusCode } = res.data;
    if (StatusCode == 200) {
      setDataSource(dataSource.filter((data) => data.Id != Id));
      message.success(Value.Message);
    }
  };

  return (
    <DefaultTable
      columns={columns}
      dataSource={dataSource}
      rowKey="id"
      onEdit={({ Id }) => history.push(`/attributes/${Id}`)}
      onDelete={deleteHandler}
      actions
    />
  );
}

Attributes.propTypes = {
  attributes: PropTypes.any,
};
