import React, { useState, useEffect } from "react";
import { Row, Col, Form } from "antd";

import PropTypes from "prop-types";
//Components
import InputWrapper from "../../../../components/turkcell/Form/InputWrapper";

//Helpers
import {
  getAttributeElement,
  getAttributesObject,
} from "../../../../helpers/attribute";

const Access = ({ attributes }) => {
  return (
    <Row gutter={[20, 20]}>
      <Col col={24}>
        <h3>TARAFLAR {">"} Damga Vergisi</h3>
      </Col>
      <Col span={24}>
        <Row gutter={[20, 20]}>
          {attributes
            .filter((attr) => attr.Group == "CLAUSE_ACCESS")
            .map((attr, index) => (
              <Col span={8} key={index}>
                <InputWrapper label={attr.Code}>
                  {getAttributeElement(attr, getAttributesObject(attributes))}
                </InputWrapper>
              </Col>
            ))}
        </Row>
      </Col>
    </Row>
  );
};

Access.propTypes = {
  attributes: PropTypes.any,
};
export default Access;
