import React, { useEffect, useState } from "react";
import { Row, Col, Card, Skeleton } from "antd";
import styled from "styled-components";
import { MainTabs } from "../services/menu";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
// Components
import DefaultLayout from "../components/Layouts/Default";
import Container from "../components/Layouts/Container";

export default function HomePage() {
  const [loading, setLoading] = useState(true);
  const [tabs, setTabs] = useState([]);
  const lang = useSelector((state) => state.localization.lang);

  const getMainTabs = async () => {
    try {
      const result = await MainTabs();
      setTabs(result.data.Value);
      setLoading(false);
    } catch (err) {
      setTimeout(() => {
        setLoading(false);
      }, 1000);
    }
  };

  useEffect(() => {
    getMainTabs();
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <Row align="middle" style={{ height: "calc(100vh - 50px)" }}>
          <Col span={18} offset={3}>
            <Row gutter={[20, 20]}>
              {loading ? (
                <>
                  <Col span={12}>
                    <Skeleton active width={200} />
                  </Col>
                  <Col span={12}>
                    <Skeleton active />
                  </Col>
                  <Col span={12}>
                    <Skeleton active />
                  </Col>
                  <Col span={12}>
                    <Skeleton active />
                  </Col>
                </>
              ) : (
                <>
                  <Col span={12}>
                    <Link to={tabs[0].Value.url}>
                      <CustomCard img={tabs[0].Value.imagepath}>
                        <CustomCardTitle>
                          {tabs[0].Value.Translations[lang]}
                        </CustomCardTitle>
                      </CustomCard>
                    </Link>
                  </Col>

                  <Col span={12}>
                    <Link to={tabs[1].Value.url}>
                      <CustomCard img={tabs[1].Value.imagepath}>
                        <CustomCardTitle>
                          {tabs[1].Value.Translations[lang]}
                        </CustomCardTitle>
                      </CustomCard>
                    </Link>
                  </Col>
                  <Col span={12}>
                    <Link to={tabs[2].Value.url}>
                      <CustomCard img={tabs[2].Value.imagepath}>
                        <CustomCardTitle>
                          {tabs[2].Value.Translations[lang]}
                        </CustomCardTitle>
                      </CustomCard>
                    </Link>
                  </Col>
                  <Col span={12}>
                    <Link to={tabs[3].Value.url}>
                      <CustomCard img={tabs[3].Value.imagepath}>
                        <CustomCardTitle>
                          {tabs[3].Value.Translations[lang]}
                        </CustomCardTitle>
                      </CustomCard>
                    </Link>
                  </Col>
                </>
              )}
            </Row>
          </Col>
        </Row>
      </Container>
    </DefaultLayout>
  );
}

const CustomCard = styled(Card)`
  height: 200px;
  overflow: hidden;

  .ant-card-body {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: flex-end;
    transition: all 0.3s ease-in-out;

    &:hover:before {
      transform: scale(1.1);
      transition: all 0.3s ease-in-out;
    }

    &:before {
      content: " ";
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      background: url(${(props) => props.img}) no-repeat center center;
      background-size: 100%;
      background-size: cover;
    }
  }
`;

const CustomCardTitle = styled.p`
  color: white;
  font-size: 1.5rem;
  margin: 0;
  z-index: 99;
`;
