import React from "react";
import { useDispatch } from "react-redux";
import { Form, Row, Col, message, Space } from "antd";
import styled from "styled-components";
import { EyeInvisibleOutlined, EyeTwoTone } from "@ant-design/icons";
import logo from "../assets/images/logo.png";
//Components
import Button from "../components/turkcell/Button";
import InputWrapper from "../components/turkcell/Form/InputWrapper";
import Input from "../components/turkcell/Form/Input";

// Services
import { login } from "../services/auth";
// Actions
import { loginAction } from "../store/actions/auth";

export default function LoginPage() {
  const dispatch = useDispatch();

  const onFinish = async (values) => {
    try {
      message.loading({ content: "Loading..", key: "login" });
      const user = await login(values);
      message.success({
        content: "Giriş Başarılı!",
        key: "login",
        duration: 1,
      });
      dispatch(loginAction(user));
    } catch (error) {
      message.error({ content: error.message, key: "login" });
    }
  };

  return (
    <Row>
      <FixedBackground>
        <Col span={8}>
          <LoginWrapper>
            <LogoWrapper>
              <Logo src={logo} />
              <LogoTitle>ContractApp</LogoTitle>
            </LogoWrapper>
            <Form
              name="basic"
              layout="vertical"
              initialValues={{ username: "admin", password: "admin1" }}
              onFinish={onFinish}
            >
              <Space direction="vertical" size={20} style={{ width: "100%" }}>
                <InputWrapper label="Kullanıcı Adı" style={{ borderRadius: 8 }}>
                  <FormItem
                    name="username"
                    rules={[
                      { required: true, message: "Kullanıcı adı giriniz !" },
                    ]}
                  >
                    <Input id="login-username" placeholder="Kullanıcı Adı" />
                  </FormItem>
                </InputWrapper>
                <InputWrapper label="Şifre" style={{ borderRadius: 8 }}>
                  <FormItem
                    name="password"
                    rules={[{ required: true, message: "Şifre giriniz !" }]}
                  >
                    <Input
                      id="login-password"
                      type="password"
                      placeholder="Şifre"
                      iconRender={(visible) =>
                        visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />
                      }
                    />
                  </FormItem>
                </InputWrapper>

                <FormItem>
                  <Button
                    id="login-button"
                    htmlType="submit"
                    title="Giriş"
                    style={{ margin: 0 }}
                  />
                </FormItem>
              </Space>
            </Form>
          </LoginWrapper>
        </Col>
      </FixedBackground>
    </Row>
  );
}
const LoginWrapper = styled.div`
  border: solid 1px rgba(151, 151, 151, 0.2);
  background-color: white;
  padding: 40px 60px;
  border-radius: 4px;
`;

const FormItem = styled(Form.Item)`
  margin-bottom: 0;
  height: 24px;
  p {
    margin: 0;
  }
  button {
    width: 100%;
  }
`;

const FixedBackground = styled.div`
  background-color: #f6f7fb;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const LogoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 30px;
`;
const Logo = styled.img`
  width: 45px;
  height: 45px;
`;
const LogoTitle = styled.h3`
  font-size: 18px;
  font-weight: bold;
  color: #263170;
  margin: 0;
  margin-top: 5px;
`;
