import React from "react";
import { Row, Col, Space } from "antd";
import styled from "styled-components";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";

const HistoryItem = ({ bgColor, history }) => {
  const [t] = useTranslation();
  const {
    UserName,
    Comment,
    OldValues,
    NewValues,
    LoggedAt,
    Version,
  } = history;

  return (
    <Row
      gutter={[10, 10]}
      style={{ backgroundColor: bgColor, padding: 10, paddingLeft: 20 }}
    >
      <Col span={24}>
        <Space size={10}>
          <Title>{UserName}</Title>
          <Title>{LoggedAt}</Title>
          <Title>
            {t("Version")}: {Version}
          </Title>
        </Space>
      </Col>
      <Col span={24}>
        <Title>{t("OldValues")}:</Title>
        <Content>{OldValues.map((val) => val.Label).join()}</Content>
      </Col>
      <Col span={24}>
        <Title>{t("NewValues")}:</Title>
        <Content>{NewValues.map((val) => val.Label).join()}</Content>
      </Col>
      <Col span={24}>
        <Title>{t("Comments")}:</Title>
        <Content>{Comment}</Content>
      </Col>
    </Row>
  );
};

HistoryItem.propTypes = {
  bgColor: PropTypes.string,
  history: PropTypes.any,
};

const Title = styled.span`
  color: #253342;
  font-size: 14px;
  font-weight: bold;
`;
const Content = styled.p`
  color: #4d4d4d;
`;
export default HistoryItem;
