import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Tabs, Skeleton, Modal, message } from "antd";
import { useParams, useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import Content from "../../../components/Content";
import Properties from "../editGroup/tabs/properties";
import Users from "../editGroup/tabs/users";

export default function EditGroup() {
  const [t] = useTranslation();
  const { id } = useParams();
  const history = useHistory();

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("System") },
            { label: t("Edit Group"), path: `/EditGroup/${id}` },
          ]}
        ></BreadcrumbNav>
      </Container>

      <Container>
        <Content title="Edit Group">
          <Tabs type="card" defaultActiveKey="1">
            <Tabs.TabPane tab={t("General")} key="1">
              <Properties />
            </Tabs.TabPane>

            <Tabs.TabPane tab={t("Users")} key="2">
              <Users />
            </Tabs.TabPane>

            <Tabs.TabPane tab={t("Columns")} key="3"></Tabs.TabPane>
          </Tabs>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
