import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import Attributes from "../pages/settings/attributes";
import NewAttribute from "../pages/settings/newAttribute/index";
import EditAttribute from "../pages/settings/editAttribute/index";

// Components
import PrivateRoute from "../components/PrivateRoute";

const AttributeRoute = ({ match: { url } }) => (
  <Switch>
    <PrivateRoute path={`${url}/create/:type`} component={NewAttribute} />
    <PrivateRoute path={`${url}/:attributeId`} component={EditAttribute} />
    <PrivateRoute path={`${url}`} exact component={Attributes} />
  </Switch>
);

AttributeRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default AttributeRoute;
