import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Card, Input, Space, message } from "antd";
import { useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../components/Layouts/Default";
import Container from "../../components/Layouts/Container";
import ItemLine from "../../components/Shared/ItemLine";
import BreadcrumbNav from "../../components/BreadcrumbNav";
import CreateBar from "../../components/Shared/CreateBar";
import DefaultTable from "../../components/Table/DefaultTable";
import CreateAttribute from "../../components/Modal/CreateAttribute";
import Content from "../../components/Content";

import { Getattributes, DeleteAttribute } from "../../services/attributes";

export default function Attributes() {
  let history = useHistory();
  const [t] = useTranslation();
  const [loading, setLoading] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [attributesList, setAttributesList] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });
  const [filters, setFilters] = useState({});
  const [columns, setColumns] = useState([
    {
      title: t("Code"),
      dataIndex: "Code",
      key: "Code",
    },
    {
      title: t("Label"),
      dataIndex: "Label",
      key: "Label",
    },
    {
      title: t("Type"),
      dataIndex: "Type",
      key: "Type",
    },
    {
      title: t("Localizable"),
      dataIndex: "Localizable",
      key: "Localizable",
      render({ Localizable }) {
        return Localizable ? t("Yes") : t("No");
      },
    },
    {
      title: t("Group"),
      dataIndex: "Group",
      key: "Group",
    },
    {
      title: t("ResourceType"),
      dataIndex: "ItemType",
      key: "ItemType",
    },
  ]);

  const onEdit = (record) => {
    history.push(`/attributes/${record.Id}`);
  };

  const onDeleteMultiple = async (attributeIdList) => {
    console.log("deleteList", attributeIdList);
  };

  const onDelete = async (attr) => {
    try {
      message.loading({ content: t("loading"), key: "deleteAttributes" });
      await DeleteAttribute(attr.Id);
      const attributeList = attributesList.filter((attribute) => {
        return attribute.Id !== attr.Id;
      });
      setAttributesList(attributeList);
      message.success({
        content: t("deleteAttributesSuccessMessage"),
        key: "deleteAttributes",
        duration: 4,
      });
    } catch (e) {
      message.error({
        content: e.message || e.Message,
        key: "deleteAttributes",
        duration: 4,
      });
    }
  };

  const getAttr = async (pagination) => {
    const { PageSize, CurrentPage } = pagination;
    setLoading(true);
    try {
      const {
        data: { Value },
      } = await Getattributes({
        PageSize,
        CurrentPage: CurrentPage,
        ...filters,
      });
      setPagination({
        current: CurrentPage + 1,
        pageSize: PageSize,
        total: Value.recordsTotal,
      });
      setAttributesList(Value.data);
      setLoading(false);
    } catch (e) {
      setLoading(false);
    }
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getAttr({
      PageSize: pageSize,
      CurrentPage: current - 1,
    });
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  useEffect(() => {
    getAttr({
      PageSize: 10,
      CurrentPage: 0,
    });
  }, [filters]);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("Attributes"), path: "/attributes" },
          ]}
        />
      </Container>
      <Container>
        <Content
          id="attributes"
          title={t("AttributesOverview")}
          actionTitle={t("CreateNew")}
          onClick={() => setIsModalVisible(true)}
        >
          <CreateAttribute
            isModalVisible={isModalVisible}
            onCancel={() => setIsModalVisible(false)}
          />
          <Row style={{ marginTop: "20px" }}>
            <Col span={24}>
              <DefaultTable
                onDeleteMultiple={onDeleteMultiple}
                onReset={() => setFilters({})}
                onRefresh={() =>
                  getAttr({
                    PageSize: 10,
                    CurrentPage: 0,
                  })
                }
                onDelete={onDelete}
                actions
                loading={loading}
                dataSource={attributesList}
                columns={columns}
                onEdit={onEdit}
                pagination={pagination}
                onChange={handleTableChange}
                filters={
                  <Space size={10}>
                    <Input
                      id="filter-attribute-code"
                      name="Code"
                      placeholder={t("Code")}
                      value={filters.Code}
                      style={{ width: 200 }}
                      onChange={(val) => filterChange(val)}
                    />
                    <Input
                      id="filter-attribute-label"
                      name="Label"
                      placeholder={t("Label")}
                      value={filters.Label}
                      style={{ width: 200 }}
                      onChange={(val) => filterChange(val)}
                    />
                  </Space>
                }
              />
            </Col>
          </Row>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
