import { privateAxiosInstance } from "./api";
import {
  GET_DATA_ROLES,
  GET_DATA_ROLE_INFO,
  GET_ROLE_USERS,
  EDIT_ROLE_MODEL,
} from "../consts/api";

export async function RolesList(filter = {}) {
  return await privateAxiosInstance.post(GET_DATA_ROLES, filter);
}

export async function GetInfoRole(id) {
  return await privateAxiosInstance.get(`${GET_DATA_ROLE_INFO}?id=${id}`);
}

export async function RolesUsers(data) {
  return await privateAxiosInstance.post(GET_ROLE_USERS, data);
}

export async function EditRoleModel(data) {
  return await privateAxiosInstance.post(EDIT_ROLE_MODEL, data);
}
