import { privateAxiosInstance } from "./api";
import {
  GET_ASSOCATION_TYPES,
  GET_ASSOCATION_TYPE,
  GET_ASSOCATION_ITEM_TYPES,
  UPDATE_ASSOCATION_TYPE,
  DELETE_ASSOCATION_TYPE,
  CREATE_ASSOCATION_TYPE,
} from "../consts/api";

export async function getAssocationTypes(filters) {
  return await privateAxiosInstance.post(GET_ASSOCATION_TYPES, {
    ...filters,
    IsPagingEnabled: false,
  });
}

export async function getAssocationType(id) {
  return await privateAxiosInstance.get(`${GET_ASSOCATION_TYPE}?id=${id}`);
}

export async function getItemList(id) {
  return await privateAxiosInstance.get(`${GET_ASSOCATION_ITEM_TYPES}`);
}

export async function updateAssocationType(data) {
  return await privateAxiosInstance.post(`${UPDATE_ASSOCATION_TYPE}`, data);
}

export async function deleteAssocationType(id) {
  return await privateAxiosInstance.post(`${DELETE_ASSOCATION_TYPE}`, { id });
}

export async function createAssociationType(Code) {
  return await privateAxiosInstance.post(
    `${CREATE_ASSOCATION_TYPE}?code=${Code}`
  );
}
