import React from "react";
import {
  Row,
  Col,
  Tabs,
  Skeleton,
  Form,
  Input,
  Select,
  Button,
  Switch,
  Space,
} from "antd";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";

function Properties({ next, prev }) {
  const [t] = useTranslation();
  return (
    <>
      <Form.Item
        label={t("Role name")}
        name="RoleName"
        rules={[
          {
            required: true,
            message: `${t("PleaseInputYour")} ${t("Role name")}!`,
          },
        ]}
      >
        <Input></Input>
      </Form.Item>
    </>
  );
}

Properties.propTypes = {
  next: PropTypes.func,
  prev: Properties.func,
};

export default Properties;
