import React, { useState } from "react";
import { Row, Col, Button, Form, Input, Modal } from "antd";
import { CheckOutlined } from "@ant-design/icons";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

export default function Attributes() {
  const [isVisible, setIsVisible] = useState(false);
  const [t] = useTranslation();

  const modalVisible = () => {
    setIsVisible(true);
  };

  const handleOk = () => {
    setIsVisible(false);
  };

  const handleCancel = () => {
    setIsVisible(false);
  };

  return (
    <Row gutter={16}>
      <Modal
        title={t("UploadVariant")}
        visible={isVisible}
        onCancel={handleCancel}
        footer={[
          <Button key="back" type="primary" onClick={handleOk}>
            {t("upload")}
          </Button>,
          <Button key="submit" type="primary" onClick={handleOk}>
            {t("damimg")}
          </Button>,
        ]}
      ></Modal>
      <Col span={12}>
        <LabelWrapper>Genel Özellikler</LabelWrapper>
        <Form layout="vertical" name="basic" initialValues={{ remember: true }}>
          <FormContent>
            <Col span={12}>
              <Form.Item
                label="Kod"
                name="username"
                rules={[
                  {
                    required: true,
                    message: "Please input your username!",
                  },
                ]}
              >
                <Input />
              </Form.Item>
            </Col>
          </FormContent>
        </Form>
      </Col>
      <Col span={12}>
        <LabelWrapper>Resim</LabelWrapper>
        <Row gutter={16}>
          <Col>
            <Button
              onClick={modalVisible}
              icon={<CheckOutlined></CheckOutlined>}
              type="primary"
            >
              Add Img
            </Button>
          </Col>
          <Col>
            <Button
              onClick={modalVisible}
              icon={<CheckOutlined></CheckOutlined>}
              type="primary"
            >
              Add Second Img
            </Button>
          </Col>
        </Row>
      </Col>
      <Col span={24}>
        <LabelWrapper>Translations</LabelWrapper>
        <Form layout="vertical" name="basic" initialValues={{ remember: true }}>
          <FormContent gutter={16}>
            <Col span={8}>
              <Row>
                <Col span={24}>
                  <Form.Item label="English Label" name="username">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item label="English Description" name="username">
                    <Input.TextArea />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
            <Col span={8}>
              <Row>
                <Col span={24}>
                  <Form.Item label="Spanish Label" name="username">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item label="Spanish Description" name="username">
                    <Input.TextArea />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
            <Col span={8}>
              <Row>
                <Col span={24}>
                  <Form.Item label="French Label" name="username">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item label="French Description" name="username">
                    <Input.TextArea />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
            <Col span={8}>
              <Row>
                <Col span={24}>
                  <Form.Item label="Portuguese Label" name="username">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item label="Portuguese Description" name="username">
                    <Input.TextArea />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
            <Col span={8}>
              <Row>
                <Col span={24}>
                  <Form.Item label="Turkish Label" name="username">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item label="Turkish Description" name="username">
                    <Input.TextArea />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
          </FormContent>
        </Form>
      </Col>
    </Row>
  );
}

const LabelWrapper = styled.p`
  background-color: #fff;
  padding: 10px 20px;
`;
const FormContent = styled(Row)`
  padding: 0 15px;
`;
