import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useParams } from "react-router-dom";

// Components
import Layout from "../../components/turkcell/Layout";
import Content from "../../components/turkcell/Content";
import SummaryItem from "../../components/turkcell/SummaryItem";
// Services
import { getItem } from "../../services/turkcell/items";

const navs = [
  {
    label: "Anasayfa",
  },
  {
    label: "B2B",
  },
  {
    label: "Madde Özet",
  },
];
const summary = () => {
  const { id } = useParams();
  const [associations, setAssociations] = useState([]);
  useEffect(() => {
    async function fetchData() {
      const res = await getItem(id);
      setAssociations(res.data.Value.Associations);
    }
    fetchData();
  }, []);
  return (
    <Layout>
      <Content navs={navs}>
        <Summaries size={20} direction="vertical">
          {associations.map((item, index) => (
            <SummaryItem
              key={index}
              bgColor={index % 2 == 0 ? "white" : "#fafafa"}
              item={item}
            />
          ))}
        </Summaries>
      </Content>
    </Layout>
  );
};

const Summaries = styled.div`
  background-color: white;
  border: solid 1px rgba(151, 151, 151, 0.2);
`;
export default summary;
