import React from "react";
import styled from "styled-components";
import { Select, Button } from "antd";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";

const { Option } = Select;

export default function TableActionsBar({ onExport, onDelete, count }) {
  const [t] = useTranslation();
  return (
    <Wrapper>
      <Count>
        {count} {t("itemSelected")}
      </Count>
      <Select defaultValue="selected" style={{ width: 120 }}>
        <Option value="selected">{t("selected")}</Option>
        <Option value="all">{t("all")}</Option>
      </Select>
      <Button onClick={onDelete}>{t("delete")}</Button>
      <Button onClick={onExport}>{t("export")}</Button>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  column-gap: 10px;
  background-color: white;
  padding: 20px 0;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
`;
const Count = styled.span`
  font-size: 16px;
`;
TableActionsBar.propTypes = {
  onExport: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  count: PropTypes.number,
};
