import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useParams } from "react-router-dom";
import { Form, Row, Col, Input, Select, Button, Skeleton, message } from "antd";

import { GetInfoUser } from "../../../../services/userManagement";

const Properties = () => {
  const [t] = useTranslation();
  const { id } = useParams();
  const [form] = Form.useForm();
  const [userInfo, setInfoUser] = useState({});
  const [loading, setLoading] = useState(true);

  const getUser = async () => {
    setLoading(true);

    try {
      const response = await GetInfoUser(id);

      setInfoUser(response.data);
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  useEffect(() => {
    getUser();
  }, []);

  if (loading) {
    return <Skeleton active />;
  }

  return (
    <Form layout="vertical" initialValues={userInfo} form={form}>
      <Row gutter={[20]}>
        <Col span={12}>
          <Form.Item
            label={t("Status")}
            name="Status"
            rules={[
              {
                required: true,
                message: t("Please input your status!"),
              },
            ]}
          >
            <Select>
              <Select.Option key="Active" value="Active">
                {t("Active")}
              </Select.Option>
              <Select.Option key="Inactive" value="Inactive">
                {t("Inactive")}
              </Select.Option>
            </Select>
          </Form.Item>

          <Form.Item
            label={t("Locale")}
            name="Locale"
            rules={[
              {
                required: true,
                message: t("Please input your locale!"),
              },
            ]}
          >
            <Select>
              <Select.Option key="en-US" value="en-US">
                {t("English")}
              </Select.Option>
              <Select.Option key="tr-TR" value="tr-TR">
                {t("Türkçe")}
              </Select.Option>
            </Select>
          </Form.Item>

          <Form.Item
            label="Email"
            name="Email"
            rules={[
              {
                required: true,
                message: t("Please input group Email!"),
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label={t("Phone Number")}
            name="PhoneNumber"
            rules={[
              {
                required: true,
                message: t("Please input group Phone Number!"),
              },
            ]}
          >
            <Input />
          </Form.Item>
        </Col>

        <Col span={12}>
          <Form.Item
            label={t("Username")}
            name="Username"
            rules={[
              {
                required: true,
                message: t("Please input group Username!"),
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label={t("First Name")}
            name="FirstName"
            rules={[
              {
                required: true,
                message: t("Please input group first name!"),
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label={t("Last Name")}
            name="LastName"
            rules={[
              {
                required: true,
                message: t("Please input group last name!"),
              },
            ]}
          >
            <Input />
          </Form.Item>
        </Col>

        <Col span={24}>
          <Row>
            <Col flex="auto"></Col>
            <Col>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  {t("save")}
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Col>
      </Row>
    </Form>
  );
};

Properties.propTypes = {};

export default Properties;
