import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Select, Row, Col, Form } from "antd";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";

const { Option } = Select;

// Components
import Input from "./Form/Input";
import InputWrapper from "./Form/InputWrapper";
import CustomSelect from "./Form/Select";
import CustomButton from "./Button";
import CustomPanel from "./CustomPanel";

// Services
import { getAttributes } from "../../services/turkcell/attributes";
import { getUsers } from "../../services/users";

const letters = [
  "A",
  "B",
  "C",
  "Ç",
  "D",
  "E",
  "F",
  "G",
  "Ğ",
  "H",
  "I",
  "İ",
  "J",
  "K",
  "L",
  "M",
  "N",
  "O",
  "Ö",
  "P",
  "R",
  "S",
  "Ş",
  "T",
  "U",
  "Ü",
  "V",
  "Y",
  "Z",
];
export default function Filter({ onFinish, onClear }) {
  const [t] = useTranslation();
  const [selectedLetter, setSelectedLetter] = useState("all");
  const [attributes, setAttributes] = useState({});

  const [form] = Form.useForm();

  useEffect(() => {
    async function fetchData() {
      const res = await getAttributes({ itemType: "ContractTemplate" });
      let attributes = res.data.Value.data;
      if (attributes.some(({ Type }) => Type == "User List")) {
        const usersRes = await getUsers();
        attributes = attributes.map((attr) => {
          if (attr.Type == "User List") {
            attr.AttributeOptions = usersRes.data.data;
          }
          return attr;
        });
      }
      const objKeyData = attributes.reduce((acc, curr) => {
        acc[curr.Code] = curr;

        return acc;
      }, {});

      setAttributes(objKeyData);
    }
    fetchData();
  }, []);

  function handleChange(value) {
    console.log(`selected ${value}`);
  }
  function deneme() {
    alert("yunus");
  }
  function handleClear() {
    form.resetFields();
    setSelectedLetter("all");
    onClear();
  }
  function getAttributeOpt(code) {
    if (attributes[code]) {
      return attributes[code].AttributeOptions;
    }
    return [];
  }
  const onFinishHandler = (values) => {
    let modifyValues = { ...values };
    if (selectedLetter && selectedLetter != "all") {
      modifyValues = {
        ...values,
        MADDE_ADI: `${selectedLetter}%`,
      };
    }
    onFinish(modifyValues);
  };
  return (
    <CustomPanel label={t("Filter")}>
      <Form name="basic" onFinish={onFinishHandler} form={form}>
        <Row>
          <Col span={24}>
            <Letters>
              <Letter
                active={selectedLetter == "all"}
                onClick={() => setSelectedLetter("all")}
              >
                {t("All")}
              </Letter>
              {letters.map((letter) => (
                <Letter
                  active={selectedLetter == letter}
                  onClick={() => setSelectedLetter(letter)}
                  key={letter}
                >
                  {letter}
                </Letter>
              ))}
              <StyledSelect
                id="letter-lang"
                defaultValue="tr"
                onChange={handleChange}
              >
                <Option value="tr">TR</Option>
                <Option value="en">EN</Option>
              </StyledSelect>
            </Letters>
          </Col>
          <Col span={24}>
            <Row gutter={[30, 10]}>
              <Col span={8}>
                <InputWrapper label={t("TemplateOwner")}>
                  <StyledFormItem name="SABLON_SAHIBI">
                    <CustomSelect
                      id="filter-template-owner"
                      options={getAttributeOpt("SABLON_SAHIBI")}
                      placeholder="Seçiniz"
                      valueAttribute="Id"
                      labelAttribute="UserName"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
              <Col span={8}>
                <InputWrapper label={t("TemplateManagement")}>
                  <StyledFormItem name="SABLON_MUDURLUK">
                    <CustomSelect
                      id="filter-template-management"
                      options={getAttributeOpt("SABLON_MUDURLUK")}
                      placeholder="Seçiniz"
                      valueAttribute="Value"
                      labelAttribute="tr_TR"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
              <Col span={8}>
                <InputWrapper label={t("TemplateDirector")}>
                  <StyledFormItem name="SABLON_DIREKTORLUK">
                    <CustomSelect
                      id="filter-template-director"
                      options={getAttributeOpt("SABLON_DIREKTORLUK")}
                      placeholder="Seçiniz"
                      valueAttribute="Value"
                      labelAttribute="tr_TR"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
              <Col span={8}>
                <InputWrapper label={t("TemplateBuyingCategory")}>
                  <StyledFormItem name="SABLON_SATINALMA_KATEGORISI">
                    <CustomSelect
                      id="filter-buying-category"
                      options={getAttributeOpt("SABLON_SATINALMA_KATEGORISI")}
                      placeholder="Seçiniz"
                      valueAttribute="Value"
                      labelAttribute="tr_TR"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
              <Col span={8}>
                <InputWrapper label={t("TemplateType")}>
                  <StyledFormItem name="SABLON_TURU">
                    <CustomSelect
                      id="filter-template-type"
                      options={getAttributeOpt("SABLON_TURU")}
                      placeholder="Seçiniz"
                      valueAttribute="Value"
                      labelAttribute="tr_TR"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
              <Col span={8}>
                <InputWrapper label={t("TemplateType2")}>
                  <StyledFormItem name="SABLON_TIPI">
                    <CustomSelect
                      id="filter-template-type2"
                      options={getAttributeOpt("SABLON_TIPI")}
                      placeholder="Seçiniz"
                      valueAttribute="Value"
                      labelAttribute="tr_TR"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
              <Col span={8}>
                <InputWrapper label={t("TemplateLang")}>
                  <StyledFormItem name="SABLON_DILI">
                    <CustomSelect
                      id="filter-template-lang"
                      options={getAttributeOpt("SABLON_DILI")}
                      placeholder="Seçiniz"
                      valueAttribute="Value"
                      labelAttribute="tr_TR"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
              <Col span={8}>
                <InputWrapper label={t("TemplateCompanyName")}>
                  <StyledFormItem name="SABLON_SIRKET_ADI">
                    <CustomSelect
                      id="filter-company-name"
                      options={getAttributeOpt("SABLON_SIRKET_ADI")}
                      placeholder="Seçiniz"
                      valueAttribute="Value"
                      labelAttribute="tr_TR"
                    />
                  </StyledFormItem>
                </InputWrapper>
              </Col>
            </Row>
          </Col>
          <Col span={24}>
            <Form.Item>
              <ButtonWrapper>
                <CustomButton
                  id="filter-clear"
                  title={t("Clear")}
                  btnType="white"
                  onClick={handleClear}
                />
                <CustomButton
                  id="filter-submit"
                  title={t("Filter")}
                  htmlType="submit"
                  onClick={deneme}
                />
              </ButtonWrapper>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </CustomPanel>
  );
}

const Letter = styled.span`
  padding: 0px 8px;
  text-align: center;
  font-size: 16px;
  color: #4d4d4d;
  cursor: pointer;
  transition: all 0.1s;
  ${({ active, theme }) =>
    active &&
    `
    color: ${theme.turkcell.mainColor};
    border-bottom: 3px solid ${theme.turkcell.mainColor};
  `};
`;

const StyledSelect = styled(Select)`
  margin-left: 20px;
`;

const Letters = styled.div`
  margin-bottom: 10px;
`;

const ButtonWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
`;
const StyledFormItem = styled(Form.Item)`
  margin-bottom: 0px;
`;

Filter.propTypes = {
  onFinish: PropTypes.func,
  onClear: PropTypes.func,
};
