import React, { useState } from "react";
import styled from "styled-components";
import { Row, Col, Tabs } from "antd";

// Components
import DefaultLayout from "../../components/Layouts/Default";
import Container from "../../components/Layouts/Container";
import ItemLine from "../../components/Shared/ItemLine";
import CreateBar from "../../components/Shared/CreateBar";
import TreeView from "../../components/TreeView";
import DefaultTable from "../../components/Table/DefaultTable";
import CreateProductModal from "../../components/Modal/CreateProductModal";
import BreadcrumbNav from "../../components/BreadcrumbNav";

// Mock data
import {
  treeSelectData,
  treeData,
  tableData,
  tableColumns,
} from "../../mockData";

export default function Items() {
  const [isModalVisible, setIsModalVisible] = useState(false);
  function onTreeSelectChange() {
    alert("Tree select changed");
  }
  function handleClickTree(x) {
    console.log("x", x);
    alert("Click Tree");
  }
  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[{ label: "Enrich" }, { label: "Items", path: "#" }]}
        />
      </Container>
      <CreateProductModal
        isModalVisible={isModalVisible}
        onCancel={() => setIsModalVisible(false)}
      />
      <ItemLine>
        <Container>
          <CreateBar
            title="Ürün Önizleme"
            clickEvent={() => setIsModalVisible(true)}
          />
        </Container>
      </ItemLine>
      <Container>
        <Row>
          <Col span={5}>
            <TreeWrapper>
              <TreeView
                select
                selectData={treeSelectData}
                treeData={treeData}
                onSelectChange={onTreeSelectChange}
                onClickTree={handleClickTree}
              />
            </TreeWrapper>
          </Col>
          <Col>
            <GutterLine></GutterLine>
          </Col>
          <Col span={18}>
            <TabWrapper>
              <Tabs type="card">
                <Tabs.TabPane tab="Liste" key="1">
                  <DefaultTable
                    columns={tableColumns}
                    dataSource={tableData}
                    actions
                  />
                </Tabs.TabPane>
                <Tabs.TabPane tab="Liste 2" key="2">
                  Liste 2
                </Tabs.TabPane>
                <Tabs.TabPane tab="Liste 3" key="3">
                  Liste 3
                </Tabs.TabPane>
              </Tabs>
            </TabWrapper>
          </Col>
        </Row>
      </Container>
    </DefaultLayout>
  );
}

const TabWrapper = styled.div`
  padding: 20px;
`;
const TreeWrapper = styled.div`
  min-height: calc(100vh - 150px);
`;

const GutterLine = styled.div`
  background-color: ${({ theme }) => theme.mainColor};
  height: 100%;
  width: 10px;
`;
