import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { Row, Col, Skeleton, Tabs, Select } from "antd";
import { UnorderedListOutlined } from "@ant-design/icons";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Content from "../../../components/Content";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import Container from "../../../components/Layouts/Container";
import CreateAttributeGroup from "../../../components/Modal/CreateAttributeGroup";

// Tabs
import Properties from "./tabs/Properties";
import Attributes from "./tabs/Attributes";
import UserGroupPermission from "./tabs/UserGroupPermisson";
import UserPermission from "./tabs/UserGroupPermisson";
import History from "./tabs/History";

// Services
import {
  getAttributeGroups,
  getAttributeGroupList,
  getAttributeGroup,
} from "../../../services/attributes";

export default function AttributeGroups() {
  const [t] = useTranslation();
  const [list, setList] = useState([]);
  const [groupCategories, setGroupCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [listLoading, setLoading] = useState(true);
  const [attributeGroup, setAttributeGroup] = useState({});
  const [attributeGroupLoading, setAttributeGroupLoading] = useState(true);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const getAttributeGroupHandle = async (id) => {
    setAttributeGroupLoading(true);
    const res = await getAttributeGroup(id);
    setAttributeGroup(res.data.Value.Result);
    setAttributeGroupLoading(false);
  };

  const isNewVisible = () => {};

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  const updateCategoryList = async (value) => {
    setLoading(true);
    const res = await getAttributeGroupList(value);
    setList(res.data.Value.Result);
    setLoading(false);
  };

  const handleCreate = () => {
    updateCategoryList(selectedCategory);
    setIsModalVisible(false);
  };

  useEffect(() => {
    updateCategoryList(selectedCategory);
  }, [selectedCategory]);

  useEffect(() => {
    getAttributeGroups().then((res) => {
      const { AttributeGroupList, ItemTypeList } = res.data.Value.Result;
      setList(AttributeGroupList);
      setGroupCategories(ItemTypeList);
      getAttributeGroupHandle(AttributeGroupList[0].Id);
      setLoading(false);
    });
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("AttributeGroups"), path: "/AttributeGroups" },
          ]}
        ></BreadcrumbNav>
      </Container>
      <Container>
        <Content
          id="attribute-groups"
          title={t("AttributeGroups")}
          actionTitle={t("CreateNew")}
          onClick={() => setIsModalVisible(true)}
        >
          <Row>
            <CreateAttributeGroup
              isModalVisible={isModalVisible}
              onCancel={() => setIsModalVisible(false)}
              categories={groupCategories}
              onCreate={handleCreate}
            />
            <Col span={4}>
              <ListWrapper>
                {groupCategories.length > 0 && (
                  <Select
                    id="attributep-groups-category-select"
                    style={{ width: "100%" }}
                    defaultValue={selectedCategory}
                    onChange={setSelectedCategory}
                  >
                    <Select.Option>All</Select.Option>
                    {groupCategories.map(({ Label }) => (
                      <Select.Option key={Label} value={Label}>
                        {Label}
                      </Select.Option>
                    ))}
                  </Select>
                )}

                {listLoading && skeletonWrapper()}
                {!listLoading &&
                  list.map(({ Id, key, Label }) => (
                    <ListItem
                      onClick={() => getAttributeGroupHandle(Id)}
                      key={key}
                    >
                      <UnorderedListOutlined style={{ fontSize: "1rem" }} />
                      <p>{Label}</p>
                    </ListItem>
                  ))}
              </ListWrapper>
            </Col>

            <Col>
              <GutterLine></GutterLine>
            </Col>
            <Col span={18}>
              <TabWrapper>
                {attributeGroupLoading ? (
                  skeletonWrapper()
                ) : (
                  <Tabs type="card" style={{ width: "100%" }}>
                    <Tabs.TabPane tab={t("GeneralProperties")} key="1">
                      <Properties
                        data={attributeGroup}
                        categories={groupCategories}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane tab={t("Attributes")} key="2">
                      <Attributes attributes={attributeGroup.Attributes} />
                    </Tabs.TabPane>
                    <Tabs.TabPane tab={t("UserGroupPermissions")} key="3">
                      <UserGroupPermission />
                    </Tabs.TabPane>
                    <Tabs.TabPane tab={t("UserPermissions")} key="4">
                      <UserPermission />
                    </Tabs.TabPane>
                    <Tabs.TabPane tab={t("History")} key="5">
                      <History />
                    </Tabs.TabPane>
                  </Tabs>
                )}
              </TabWrapper>
            </Col>
          </Row>
        </Content>
      </Container>
    </DefaultLayout>
  );
}

const GutterLine = styled.div`
  background-color: ${({ theme }) => theme.mainColor};
  height: 100%;
  width: 10px;
`;

const ListWrapper = styled.div`
  min-height: calc(100vh - 150px);
  padding: 20px 10px 0px 20px;
`;

const ListItem = styled.div`
  display: flex;
  align-items: flex-start;
  margin-bottom: 15px;
  cursor: pointer;
  &:nth-child(2) {
    margin-top: 15px;
  }
  p {
    margin-bottom: 0px;
    word-break: break-word;
    font-size: 14px;
    padding-left: 10px;
    line-height: 1;
  }
`;

const TabWrapper = styled.div`
  padding: 20px;
`;
