import React from "react";
import PropTypes from "prop-types";
import styled, { css } from "styled-components";
import { StarOutlined, StarFilled } from "@ant-design/icons";
import { message } from "antd";

import { Check, Share } from "../../icons";

function Actions({ row, categoryId, onLike }) {
  const { Id } = row;
  const onShare = () => {
    navigator.clipboard.writeText(
      `${process.env.REACT_APP_API_URL}contracts/${categoryId}/${Id}`
    );
    message.success("Bağlantı linki kopylandı!");
  };

  return (
    <ActionsWrapper>
      <ShareIcon onClick={onShare} />
      {row.IsFavorite ? (
        <StarIconFilled onClick={() => onLike(row)} />
      ) : (
        <StarIcon onClick={() => onLike(row)} />
      )}
    </ActionsWrapper>
  );
}

const ActionsWrapper = styled.div`
  display: flex;
  justify-content: space-between;
`;

const actionIconStyles = css`
  font-size: 20px;
  color: ${({ theme }) => theme.turkcell.mainColor};
  cursor: pointer;
`;

const ShareIcon = styled(Share)`
  ${actionIconStyles}
`;

const StarIcon = styled(StarOutlined)`
  ${actionIconStyles}
  color: #ffc900;
`;

const StarIconFilled = styled(StarFilled)`
  ${actionIconStyles}
  color: #ffc900;
`;

Actions.propTypes = {
  row: PropTypes.any,
  categoryId: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  onLike: PropTypes.func,
};

export default Actions;
