import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";
import { useParams } from "react-router-dom";
import { Form, Row, Col, Input, Select, Button, Skeleton, message } from "antd";

import {
  getAssocationType,
  getItemList,
  updateAssocationType,
  deleteAssocationType,
} from "../../../../services/assocations";

const Properties = () => {
  const [t] = useTranslation();
  const { id } = useParams();
  const [form] = Form.useForm();
  const [associationType, setAssociationType] = useState({});
  const [types, setTypes] = useState([]);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    async function fetchData() {
      const res = await getAssocationType(id);
      const typeRes = await getItemList();
      setTypes(typeRes.data.Value.ItemTypes);
      setAssociationType(res.data.Value);
      setLoading(false);
    }
    fetchData();
  }, []);
  const onFinish = (values) => {
    message.loading({ content: "Loading...", key: "update-association" });
    try {
      const res = updateAssocationType({
        Id: id,
        ...values,
      });
      message.success({ content: "Kayıt Edildi!", key: "update-association" });
    } catch (err) {
      message.error({ content: err.message, key: "update-association" });
    }
  };
  if (loading) {
    return <Skeleton active />;
  }
  return (
    <Form
      layout="vertical"
      initialValues={associationType}
      form={form}
      onFinish={onFinish}
    >
      <Row gutter={[20]}>
        <Col span={12}>
          <Form.Item
            label="Code"
            name="Code"
            rules={[
              {
                required: true,
                message: "Please input your code!",
              },
            ]}
          >
            <Input id="edit-association-code" />
          </Form.Item>
          <Form.Item
            label="Primary ItemType"
            name="PrimaryItemType"
            rules={[
              {
                required: true,
                message: "Please input your code!",
              },
            ]}
          >
            <Select id="edit-association-item-type">
              {types.map(({ Label, Id }) => (
                <Select.Option key={Id} value={Id}>
                  {Label}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            label="Secondary ItemType"
            name="SecondaryItemType"
            rules={[
              {
                required: true,
                message: "Please input your code!",
              },
            ]}
          >
            <Select id="edit-association-secondary-item-type">
              {types.map(({ Label, Id }) => (
                <Select.Option key={Id} value={Id}>
                  {Label}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item
            label="English"
            initialValue={associationType.en_US}
            name={["Translations", "en-US"]}
          >
            <Input id="edit-association-translations-en" />
          </Form.Item>
          <Form.Item
            label="Turkish"
            initialValue={associationType.tr_TR}
            name={["Translations", "tr-TR"]}
          >
            <Input id="edit-association-translations-tr" />
          </Form.Item>
        </Col>
        <Col span={24}>
          <Row>
            <Col flex="auto"></Col>
            <Col>
              <Form.Item>
                <Button
                  id="edit-association-save"
                  type="primary"
                  htmlType="submit"
                >
                  {t("save")}
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Col>
      </Row>
    </Form>
  );
};

Properties.propTypes = {};

export default Properties;
