import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import {
  Row,
  Col,
  Steps,
  Skeleton,
  Form,
  Space,
  Card,
  Button,
  message,
} from "antd";
import styled from "styled-components";
import { useParams, useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import ItemLine from "../../../components/Shared/ItemLine";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import CreateBar from "../../../components/Shared/CreateBar";
import { Properties, Values, Rules } from "./tabs";
// Services
import {
  GetAttributesType,
  GetValidationParameters,
  CreateAttributeByModel,
} from "../../../services/attributes";

export default function NewAttribute() {
  const [t] = useTranslation();
  const { type } = useParams();
  const [form] = Form.useForm();
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [rules, setRules] = useState([]);
  const [newAttrType, setNewAttrType] = useState(null);
  const [step, setStep] = useState(0);
  const [values, setValues] = useState({});

  const getContent = () => {
    switch (step) {
      case 0:
        return <Properties newAttrType={newAttrType} />;
      case 1:
        return <Rules rules={rules} />;
      default:
        return <Values />;
    }
  };
  const createAttribute = async (attribute) => {
    try {
      await CreateAttributeByModel(attribute);
    } catch (e) {
      console.log(e);
    }
  };
  const next = () => {
    form
      .validateFields()
      .then((formValues) => {
        setValues({
          ...values,
          ...formValues,
        });
        setStep(step + 1);
      })
      .catch((err) => console.log(err));
  };
  const prev = () => {
    if (step != 0) {
      setStep(step - 1);
    }
  };
  const onFinish = async () => {
    setLoading(true);
    try {
      await createAttribute(values);
      form.resetFields();
      setStep(0);
      message.success("Attribute Başarıyla oluşturuldu");
      history.push("/attributes");
      setLoading(false);
    } catch (err) {
      message.success(err);
      setLoading(false);
    }
  };
  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  useEffect(() => {
    async function fetchData() {
      const attributeTypeRes = await GetAttributesType(type);
      const parameterRes = await GetValidationParameters(type);
      setRules(parameterRes.data.Value);
      setNewAttrType(attributeTypeRes.data.Value);
      setLoading(false);
    }
    fetchData();
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: "Settings" },
            { label: "Attributes", path: "/attributes" },
          ]}
        />
      </Container>
      <ItemLine>
        <Container>
          <CreateBar title={t("createAttribute")} />
        </Container>
      </ItemLine>
      <Container>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Card>
              <StyledSteps current={step}>
                <Steps.Step key={0} title={t("properties")} />
                <Steps.Step key={1} title={t("rules")} />
                <Steps.Step key={2} title={t("values")} />
              </StyledSteps>
              <StepContent>
                <Form
                  layout="vertical"
                  form={form}
                  onFinish={onFinish}
                  initialValues={{ AttributeType: type }}
                >
                  {loading ? skeletonWrapper() : getContent()}

                  <Form.Item>
                    <Row>
                      <Col flex="auto" />
                      <Col>
                        <Space align="center">
                          {step != 0 && (
                            <Button type="primary" onClick={prev}>
                              Geri
                            </Button>
                          )}
                          {step != 2 && (
                            <Button type="primary" onClick={next}>
                              İleri
                            </Button>
                          )}
                          {step == 2 && (
                            <Button type="primary" htmlType="submit">
                              Kaydet
                            </Button>
                          )}
                        </Space>
                      </Col>
                    </Row>
                  </Form.Item>
                </Form>
              </StepContent>
            </Card>
          </Col>
        </Row>
      </Container>
    </DefaultLayout>
  );
}

const StyledSteps = styled(Steps)`
  padding: 20px;
`;
const StepContent = styled.div`
  padding: 20px;
`;
