import React, { useState, useEffect } from "react";
import {
  Row,
  Col,
  Tree,
  Tabs,
  Button,
  Form,
  Input,
  Skeleton,
  Modal,
} from "antd";
import styled from "styled-components";
import { DownOutlined, CloudOutlined, DeleteOutlined } from "@ant-design/icons";
import { useTranslation } from "react-i18next";

// Components
import DefaultLayout from "../../components/Layouts/Default";
import Container from "../../components/Layouts/Container";
import ItemLine from "../../components/Shared/ItemLine";
import CreateBar from "../../components/Shared/CreateBar";
import SearchSelect from "../../components/SearchSelect";
import BreadcrumbNav from "../../components/BreadcrumbNav";

// Tabs
import Attributes from "./tabs/attributes";
import UserGroupPermission from "../enrich/editItem/tabs/userGroupPermission";
import UserPermission from "../enrich/editItem/tabs/userPermissions";
import History from "../enrich/editItem/tabs/history";

// Mock Data
import { treeData, treeSelectData } from "../../mockData";

export default function Categories() {
  const [t] = useTranslation();
  const [isNewCategory, setIsNewCategory] = useState(false);
  const [treeLoading, setTreeLoading] = useState(false);
  const [attributesLoading, setAttributesLoading] = useState(false);
  const [activeKey, setActiveKey] = useState("1");
  const [isVisibleDelete, setIsVisibleDelete] = useState(false);

  const deleteModalVisible = () => {
    setIsVisibleDelete(true);
  };

  const handleDeleteOk = () => {
    setIsVisibleDelete(false);
  };

  const handleDeleteCancel = () => {
    setIsVisibleDelete(false);
  };

  const skeletonWrapper = () => {
    return <Skeleton paragraph={{ rows: 16 }} active />;
  };

  const onChangeTabHandle = (key) => {
    setActiveKey(key);
  };

  const getAttributesRequest = () => {
    setActiveKey("1");
    setAttributesLoading(true);
    setTimeout(() => {
      setAttributesLoading(false);
    }, 1500);
  };

  const getTreeRequest = () => {
    setTreeLoading(true);
    setTimeout(() => {
      setTreeLoading(false);
    }, 1500);
  };

  const changeIsNewCategory = () => {
    setIsNewCategory(true);
  };

  useEffect(() => {
    getTreeRequest();
  }, []);

  return (
    <DefaultLayout>
      <Modal
        title={t("deleteconfirmation")}
        visible={isVisibleDelete}
        onCancel={handleDeleteCancel}
        footer={[
          <Button key="back" onClick={handleDeleteCancel}>
            {t("cancel")}
          </Button>,
          <Button key="submit" type="danger" onClick={handleDeleteOk}>
            {t("delete")}
          </Button>,
        ]}
      >
        <p>{t("Areyousurewanttodeletethiscategory")}</p>
      </Modal>

      <Container>
        <BreadcrumbNav
          navs={[
            { label: "Categories", path: "/categories" },
            { label: "Product Categories" },
          ]}
        ></BreadcrumbNav>
      </Container>

      <ItemLine>
        <Container>
          <Row justify={"space-between"}>
            <Col span={6}>
              <CreateBar
                clickEvent={changeIsNewCategory}
                title={t("productpreview")}
              />
            </Col>
            <Col span={6}>
              <Row justify={"end"} align={"middle"} style={{ height: "100%" }}>
                {!isNewCategory && (
                  <Button
                    onClick={deleteModalVisible}
                    type="danger"
                    icon={<DeleteOutlined />}
                  >
                    {t("delete")}
                  </Button>
                )}
              </Row>
            </Col>
          </Row>
        </Container>
      </ItemLine>

      <Container>
        <Row>
          <Col span={5}>
            <TreeWrapper>
              <SelectWrapper>
                <SearchSelect
                  onChange={getTreeRequest}
                  data={treeSelectData}
                  label={t("enterkeyword")}
                />
              </SelectWrapper>
              {treeLoading && skeletonWrapper()}
              {!treeLoading && (
                <TreeItem
                  switcherIcon={<DownOutlined />}
                  showIcon={true}
                  treeData={treeData}
                  defaultExpandAll
                  onSelect={getAttributesRequest}
                />
              )}
            </TreeWrapper>
          </Col>

          <Col>
            <GutterLine></GutterLine>
          </Col>

          {isNewCategory && (
            <Col span={18}>
              <TabWrapper>
                <Tabs type="card">
                  <Tabs.TabPane tab={t("attributes")} key="5">
                    <Row gutter={16}>
                      <Col span={12}>
                        <LabelWrapper>Genel Özellikler</LabelWrapper>
                        <Form
                          layout="vertical"
                          name="basic"
                          initialValues={{ remember: true }}
                        >
                          <FormContent>
                            <Col span={12}>
                              <Form.Item
                                label="Kod"
                                name="username"
                                rules={[
                                  {
                                    required: true,
                                    message: "Please input your username!",
                                  },
                                ]}
                              >
                                <Input />
                              </Form.Item>
                            </Col>
                          </FormContent>
                        </Form>
                      </Col>
                      <Col span={12}>
                        <LabelWrapper>Grup Değerleri</LabelWrapper>
                      </Col>
                      <Col span={12}>
                        <LabelWrapper>Resim</LabelWrapper>
                        <Row gutter={16}>
                          <Col>
                            <Button
                              icon={<CloudOutlined></CloudOutlined>}
                              type="primary"
                            >
                              Yükle
                            </Button>
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                  </Tabs.TabPane>
                </Tabs>
              </TabWrapper>
            </Col>
          )}
          {!isNewCategory && (
            <Col span={18}>
              <TabWrapper>
                <Tabs
                  activeKey={activeKey}
                  onChange={onChangeTabHandle}
                  type="card"
                >
                  <Tabs.TabPane tab={t("attributes")} key="1">
                    {attributesLoading && skeletonWrapper()}
                    {!attributesLoading && <Attributes />}
                  </Tabs.TabPane>
                  <Tabs.TabPane tab={t("usergrouppermission")} key="2">
                    <UserGroupPermission />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab={t("userpermission")} key="3">
                    <UserPermission />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab={t("history")} key="4">
                    <History />
                  </Tabs.TabPane>
                </Tabs>
              </TabWrapper>
            </Col>
          )}
        </Row>
      </Container>
    </DefaultLayout>
  );
}

const FormContent = styled(Row)`
  padding: 0 15px;
`;

const LabelWrapper = styled.p`
  background-color: #fff;
  padding: 10px 20px;
`;

const TreeItem = styled(Tree.DirectoryTree)`
  margin-top: 10px;
  background-color: transparent;

  .ant-tree-switcher {
    background-color: transparent;
  }
`;

const TreeWrapper = styled.div`
  min-height: 100vh;
`;

const SelectWrapper = styled.div`
  background-color: ${({ theme }) => theme.white};
  border-bottom: 1px solid ${({ theme }) => theme.mainColor};
  padding: 10px;
`;

const GutterLine = styled.div`
  background-color: ${({ theme }) => theme.mainColor};
  height: 100%;
  width: 10px;
`;

const TabWrapper = styled.div`
  padding: 20px;
`;
