import { privateAxiosInstance } from "./api";
import {
  CHANGEPASSWORD,
  GETCURRENTUSERINFO,
  UPDATEACCOUNTDETAILS,
} from "../consts/api";

export async function UpdateAccountDetail(account) {
  return await privateAxiosInstance.post(UPDATEACCOUNTDETAILS, account);
}
export async function ChangePassword(passwords) {
  return await privateAxiosInstance.post(CHANGEPASSWORD, passwords);
}

export async function GetCurrentUserInfo() {
  return await privateAxiosInstance.get(GETCURRENTUSERINFO);
}
