import React from "react";
import styled from "styled-components";
import { Form } from "antd";
import moment from "moment";

import Input from "../components/turkcell/Form/Input";
import Switch from "../components/turkcell/Form/Switch";
import Select from "../components/turkcell/Form/Select";
import DatePicker from "../components/turkcell/Form/DatePicker";

import { FAMILY_ID, ITEM_TYPE, DATE_FORMAT } from "../consts";

const StyledFormItem = styled(Form.Item)`
  margin-bottom: 0px !important;
`;
// Get options of simple or multiple select attirbute
const getAttrOptions = (attributes, code) => {
  if (attributes[code]) {
    return attributes[code].AttributeOptions;
  }
  return [];
};

// Get attribute element by type
export const getAttributeElement = (
  { Type, Code, is_required },
  attributes
) => {
  switch (Type) {
    case "Text":
      return (
        <StyledFormItem name={Code} rules={[{ required: is_required }]}>
          <Input id={Code} placeholder="Giriniz" />
        </StyledFormItem>
      );
    case "Date":
      return (
        <StyledFormItem name={Code}>
          <DatePicker
            id={Code}
            format="DD/MM/YYYY"
            rules={[{ required: is_required }]}
          />
        </StyledFormItem>
      );
    case "Bool":
      return (
        <StyledFormItem
          name={Code}
          valuePropName="checked"
          rules={[{ required: is_required }]}
        >
          <Switch id={Code} />
        </StyledFormItem>
      );
    case "Simple Select":
      return (
        <StyledFormItem name={Code} rules={[{ required: is_required }]}>
          <Select id={Code} options={getAttrOptions(attributes, Code)} />
        </StyledFormItem>
      );
    case "User List":
      return (
        <StyledFormItem name={Code} rules={[{ required: is_required }]}>
          <Select
            id={Code}
            options={getAttrOptions(attributes, Code)}
            valueAttribute="Id"
            labelAttribute="UserName"
          />
        </StyledFormItem>
      );
    case "Multiple Select":
      return (
        <StyledFormItem name={Code} rules={[{ required: is_required }]}>
          <Select
            id={Code}
            options={getAttrOptions(attributes, Code)}
            mode="multiple"
          />
        </StyledFormItem>
      );
    default:
      return;
  }
};

export const getAttributesObject = (attributes) => {
  return attributes.reduce((acc, curr) => {
    acc[curr.Code] = curr;
    return acc;
  }, {});
};

// Get attribute code by type
const getAttirbuteCodeByType = (attirbutes, type) => {
  return attirbutes.filter(({ Type }) => Type == type).map(({ Code }) => Code);
};
const formatFormDate = (attributes, values) => {
  const dates = getAttirbuteCodeByType(attributes, "Date");
  const newDates = {};
  dates.forEach((date) => {
    if (values[date]) {
      newDates[date] = values[date].format();
    }
  });
  return newDates;
};

// form attribute values object to attribute array
const attirbutesObjectToArray = (values) => {
  const AttirbuteKeys = Object.keys(values);
  const attributes = [];
  AttirbuteKeys.forEach((key) => {
    const Value = Array.isArray(values[key]) ? values[key].join() : values[key];
    if (typeof Value === "boolean" || Value) {
      attributes.push({
        Code: key,
        Value,
      });
    }
  });
  return attributes.filter(({ Value }) => Value != null);
};

// Get attribute array for create or update request data
const getAttirbuteArray = (attributes, values) => {
  const dateItems = formatFormDate(attributes, values);
  const attirbuteArray = attirbutesObjectToArray({
    ...values,
    ...dateItems,
  });
  return attirbuteArray;
};
// Item update request data
export const updateItemReqData = (item, attributes, values) => {
  const { FamilyId, Id } = item;
  const Associations = Array.isArray(values.Associations)
    ? values.Associations.map((item) => ({
        Code: "CLAUSE_TAG",
        ItemId: item,
        SortOrder: 0,
      }))
    : [];
  const reqData = {
    FamilyId,
    Associations,
    Attributes: getAttirbuteArray(attributes, values),
    Id,
  };
  return reqData;
};
// Item create request data
export const createItemReqData = (attributes, values, categoryId) => {
  const Attributes = getAttirbuteArray(attributes, values);
  const Associations = Array.isArray(values.Associations)
    ? values.Associations.map((item) => ({
        Code: "CLAUSE_TAG",
        ItemId: item,
        SortOrder: 0,
      }))
    : [];
  const reqData = {
    FamilyId: FAMILY_ID,
    Attributes,
    Associations,
    Categories: [
      {
        Id: categoryId,
      },
    ],
    itemType: ITEM_TYPE,
    IsEnabled: "true",
  };
  return reqData;
};

// If attribute value is empty return default value
export const getAttirbuteValue = ({ Code, Type }, attributeValues) => {
  const value = attributeValues[Code] && attributeValues[Code].Value;
  if (value) {
    if (Type == "Date") {
      return moment(value, DATE_FORMAT);
    }
    return value;
  }
  switch (Type) {
    case "Bool":
      return false;
    case "Multiple Select":
      return [];
    case "Date":
      return null;
    default:
      return null;
  }
};

export const getAssociationWithAttributes = (associations) => {
  return associations.map((item) => {
    const Attributes = item.Attributes.reduce((acc, curr) => {
      acc[curr.Code] = curr.Value;
      return acc;
    }, {});
    return {
      ...item,
      ...Attributes,
    };
  });
};

export const getItemAttributesByKey = (item) => {
  if (item.Attributes) {
    return item.Attributes.reduce((acc, curr) => {
      if (curr.Type == "Multiple Select") {
        if (acc[curr.Code] && Array.isArray(acc[curr.Code].Value)) {
          acc[curr.Code].Value.push(curr.Value);
        } else {
          acc[curr.Code] = {
            ...curr,
            Value: [curr.Value],
          };
        }
      } else {
        acc[curr.Code] = curr;
      }
      return acc;
    }, {});
  }
  return {};
};
