import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Space, Input, Select, Card } from "antd";
import { useParams } from "react-router-dom";

// Components
import DefaultTable from "../../../../components/Table/DefaultTable";

// Services
import { RolesUsers } from "../../../../services/roles";

const Users = () => {
  const [t] = useTranslation();
  const { id } = useParams();
  const [users, setUsersList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filters, setFilters] = useState({});
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });

  const getColumns = () => [
    {
      title: t("First Name"),
      dataIndex: "FirstName",
      key: "FirstName",
    },
    {
      title: t("Last Name"),
      dataIndex: "LastName",
      key: "LastName",
    },
    {
      title: t("User Name"),
      dataIndex: "Username",
      key: "Username",
    },
    {
      title: t("Email"),
      dataIndex: "Email",
      key: "Email",
    },
  ];

  const getRolesUsers = async (pagination) => {
    const { PageSize, CurrentPage, data } = pagination;
    setLoading(true);

    try {
      const result = await RolesUsers({
        GroupId: id,
        PageSize,
        CurrentPage: CurrentPage - 1,
        ...filters,
      });

      setUsersList(result.data.data);
      setPagination({ ...data, total: result.data.recordsTotal });
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  const filterChangeSelect = (filter) => {
    // const { target } = filter;
    // setFilters({ ...filters, [target.name]: target.value });
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getRolesUsers({
      PageSize: pageSize,
      CurrentPage: current,
      data: pagination,
    });
  };

  useEffect(() => {
    getRolesUsers({
      PageSize: 10,
      CurrentPage: 1,
    });
  }, [filters]);

  return (
    <Row>
      <Col span={24}>
        <DefaultTable
          columns={getColumns()}
          rowSelection
          onReset={() => setFilters({})}
          dataSource={users}
          loading={loading}
          style={{ marginTop: "15px" }}
          pagination={pagination}
          scroll={{ x: "1000px" }}
          onChange={handleTableChange}
          filters={
            <Space style={{ size: 10 }}>
              <Select
                name="hasproperty"
                placeholder={t("Has Group")}
                value={filters.hasproperty}
                style={{ width: 200 }}
                onChange={(val) => filterChangeSelect(val)}
              >
                <Select.Option value="">Has Group</Select.Option>
                <Select.Option value="Yes">Yes</Select.Option>
                <Select.Option value="No">No</Select.Option>
              </Select>

              <Input
                name="Firstname"
                placeholder={t("First Name")}
                value={filters.Firstname}
                style={{ width: 200 }}
                onChange={(val) => filterChange(val)}
              />

              <Input
                name="Lastname"
                placeholder={t("Last Name")}
                value={filters.Lastname}
                style={{ width: 200 }}
                onChange={(val) => filterChange(val)}
              />

              <Input
                name="Username"
                placeholder={t("User Name")}
                value={filters.Username}
                style={{ width: 200 }}
                onChange={(val) => filterChange(val)}
              />

              <Input
                name="Email"
                placeholder={t("Email")}
                value={filters.Email}
                style={{ width: 200 }}
                onChange={(val) => filterChange(val)}
              />
            </Space>
          }
        />
      </Col>
    </Row>
  );
};

Users.propTypes = {};

export default Users;
