import React from "react";
import { BrowserRouter as Router, Switch } from "react-router-dom";

// Pages
import Login from "../pages/login";
import ForgotPassword from "../pages/forgotPassword";
import Home from "../pages/home";
import ItemTypes from "../pages/settings/itemTypes";
import AttributeGroups from "../pages/settings/attributeGroups/index";
import Items from "../pages/enrich/items";
import EditItem from "../pages/enrich/editItem";
import Categories from "../pages/categories";
import Profile from "../pages/profile";
import Modeler from "../pages/bpmn/modeler";

// Routes
import ContractRoute from "./contracts";
import AttributeRoute from "./attributes";
import AssociationRoute from "./assocations";
import UsersRoute from "./users";
import RolesRoute from "./roles";
import GroupsRoute from "./groups";
import BpmnRoute from "./bpmn";
import FamilyRoute from "./families";
import TemplateRoute from "./templates";

// Components
import PrivateRoute from "../components/PrivateRoute";
import PublicRoute from "../components/PublicRoute";

export default function Routes() {
  return (
    <Router>
      <Switch>
        <PrivateRoute path="/templates" component={TemplateRoute} />
        <PrivateRoute path="/families" component={FamilyRoute} />
        <PrivateRoute path="/bpmn" component={BpmnRoute} />
        <PrivateRoute path="/enrich/edititem/:id" component={EditItem} />
        <PrivateRoute path="/enrich/items" component={Items} />
        <PrivateRoute path="/settings/itemtypes" component={ItemTypes} />
        <PrivateRoute path="/attributes" component={AttributeRoute} />
        <PrivateRoute path="/associations" component={AssociationRoute} />
        <PrivateRoute path="/system/userManage" component={UsersRoute} />
        <PrivateRoute path="/system/roles" component={RolesRoute} />
        <PrivateRoute path="/system/groups" component={GroupsRoute} />
        <PrivateRoute path="/attributegroups" component={AttributeGroups} />
        <PrivateRoute path="/categories/:itemtype" component={Categories} />
        <PrivateRoute path="/contracts" component={ContractRoute} />
        <PrivateRoute path="/profile" component={Profile} />
        <PublicRoute path="/login" component={Login} />
        <PublicRoute path="/forgotpassword" component={ForgotPassword} />
        <PrivateRoute exact path="/" component={Home} />
      </Switch>
    </Router>
  );
}
