import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";
import { useParams } from "react-router-dom";
import { Form, Row, Col, Input, Select, Button, Skeleton, message } from "antd";

// Services
import { EditRoleModel } from "../../../../services/roles";

const Properties = ({ roleInfo }) => {
  const [t] = useTranslation();
  const { id } = useParams();
  const { Name } = roleInfo;

  const onFinish = async (values) => {
    var arrayPermissions = roleInfo.Permissions.reduce((result, item) => {
      if (item.IsSelected) {
        result.push(item.Label);
      }
      return result;
    }, []);

    message.loading({ content: "Loading...", key: "onFinish" });

    try {
      await EditRoleModel({
        "role.id": id,
        "role.name": roleInfo.Name,
        Users: roleInfo.Permissions.reduce((result, item) => {
          result.push(item.Id);

          return result;
        }, []),
        UnSelectedUsers: [],
        Permissions: arrayPermissions,
        ConfigurationPermissions:
          roleInfo.Configuration == null
            ? JSON.stringify([])
            : JSON.stringify(roleInfo.Configuration),
      });

      message.success({ content: "Saved!", key: "onFinish" });
    } catch (err) {
      message.error({ content: "There is error!", key: "onFinish" });
    }
  };

  return (
    <Form
      layout="vertical"
      initialValues={{ Name }}
      name="basic"
      onFinish={onFinish}
    >
      <Row gutter={[20]}>
        <Col span={12}>
          <Form.Item
            id="Name-Role"
            label={t("Role name")}
            name="Name"
            rules={[
              {
                required: true,
                message: t("Please input role name!"),
              },
            ]}
          >
            <Input />
          </Form.Item>
        </Col>

        <Col span={24}>
          <Row>
            <Col flex="auto"></Col>
            <Col>
              <Form.Item>
                <Button id="save-edit-role" type="primary" htmlType="submit">
                  {t("save")}
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Col>
      </Row>
    </Form>
  );
};

Properties.propTypes = {
  roleInfo: PropTypes.any,
};

export default Properties;
