import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  Tabs,
  Form,
  Input,
  Button,
  Row,
  Col,
  Select,
  Card,
  message,
} from "antd";
import moment from "moment";

const { TabPane } = Tabs;
const { Option } = Select;

// Components
import DefaultLayout from "../components/Layouts/Default";
import BreadcrumbNav from "../components/BreadcrumbNav";
import Content from "../components/Content";
import Container from "../components/Layouts/Container";
import DatePicker from "../components/DatePicker";

// Services
import {
  GetCurrentUserInfo,
  UpdateAccountDetail,
  ChangePassword,
} from "../services/profile";

const languageConsts = ["en-US", "tr-TR", "ru-RU"];

export default function Profile() {
  const [t] = useTranslation();
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState({});

  const onFinishGeneral = async (values) => {
    try {
      setLoading(true);
      message.loading({ content: t("loading"), key: "updateProfile" });
      const {
        data: { Value },
      } = await UpdateAccountDetail(values);
      setUser(Value);
      setLoading(false);
      message.success({
        content: t("saveProfileSuccessMessage"),
        key: "updateProfile",
        duration: 4,
      });
    } catch (e) {
      setLoading(false);
      message.error({
        content: e.Message,
        key: "updateProfile",
        duration: 4,
      });
    }
  };

  const onFinishPassword = async (values) => {
    try {
      setLoading(true);
      message.loading({ content: t("Loading"), key: "changePassword" });
      await ChangePassword(values);
      form.resetFields();
      setLoading(false);
      message.success({
        content: t("PasswordChangeSuccess"),
        key: "changePassword",
        duration: 4,
      });
    } catch (e) {
      setLoading(false);
      message.error({
        content: e.Message,
        key: "changePassword",
        duration: 4,
      });
      console.log(e);
    }
  };

  const getUserInfo = async () => {
    try {
      setLoading(true);
      const {
        data: { Value },
      } = await GetCurrentUserInfo();
      const date = Value.Birth.split(" ");
      Value.Birth = date[0];
      setUser(Value);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.log(e);
    }
  };

  useEffect(() => {
    getUserInfo();
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav navs={[{ label: "User" }, { label: "Profile" }]} />
      </Container>
      <Container>
        <Content title={t("Profile")}>
          <Tabs defaultActiveKey="1">
            <TabPane tab={t("GeneralProperties")} key="1">
              <Row>
                <Col span={12}>
                  {user && user.Username && (
                    <Form layout="vertical" onFinish={onFinishGeneral}>
                      <Form.Item
                        label={t("Email")}
                        name="Email"
                        initialValue={user.Email}
                        rules={[
                          {
                            required: true,
                            type: "email",
                            message: `${t("PleaseInputYour")} ${t("Email")}!`,
                          },
                        ]}
                      >
                        <Input id="profile-email" />
                      </Form.Item>
                      <Form.Item
                        label={t("Username")}
                        name="Username"
                        initialValue={user.Username}
                        rules={[
                          {
                            required: true,
                            message: `${t("PleaseInputYour")} ${t(
                              "UserName"
                            )}!`,
                          },
                        ]}
                      >
                        <Input id="profile-username" />
                      </Form.Item>
                      <Form.Item
                        initialValue={user.Name}
                        label={t("Name")}
                        name="Name"
                        rules={[
                          {
                            required: true,
                            message: `${t("PleaseInputYour")} ${t("Name")}!`,
                          },
                        ]}
                      >
                        <Input id="profile-name" />
                      </Form.Item>
                      <Form.Item
                        initialValue={user.Surname}
                        label={t("LastName")}
                        name="Surname"
                        rules={[
                          {
                            required: true,
                            message: `${t("PleaseInputYour")} ${t("SurName")}!`,
                          },
                        ]}
                      >
                        <Input id="profile-lastname" />
                      </Form.Item>
                      <Form.Item
                        name="Birth"
                        label={t("Birthday")}
                        initialValue={moment(user.Birth, "DD-MM-YYYY")}
                        rules={[
                          {
                            required: true,
                            message: `${t("PleaseInputYour")} ${t(
                              "Birthday"
                            )}!`,
                          },
                        ]}
                      >
                        <DatePicker
                          id="profile-birthday"
                          format={"DD-MM-YYYY"}
                        />
                      </Form.Item>
                      <Form.Item
                        label={t("Language")}
                        initialValue={user.UI_Locale}
                        name="UI_Locale"
                        rules={[
                          {
                            required: true,
                            message: `${t("PleaseInputYour")} ${t(
                              "Language"
                            )}!`,
                          },
                        ]}
                      >
                        <Select id="profile-lang">
                          {languageConsts.map((language) => {
                            return (
                              <Option key={language} value={language}>
                                {language}
                              </Option>
                            );
                          })}
                        </Select>
                      </Form.Item>
                      <Form.Item>
                        <Button
                          id="profile-save"
                          loading={loading}
                          type="primary"
                          htmlType="submit"
                        >
                          {t("Save")}
                        </Button>
                      </Form.Item>
                    </Form>
                  )}
                </Col>
              </Row>
            </TabPane>
            <TabPane tab={t("ChangePassword")} key="2">
              <Row>
                <Col span={12}>
                  <Form
                    form={form}
                    layout="vertical"
                    onFinish={onFinishPassword}
                  >
                    <Form.Item
                      label={t("OldPassword")}
                      name="OldPassword"
                      rules={[
                        {
                          required: true,
                          message: `${t("PleaseInputYour")} ${t(
                            "OldPassword"
                          )}!`,
                        },
                      ]}
                    >
                      <Input.Password id="profile-old-password" />
                    </Form.Item>
                    <Form.Item
                      label={t("NewPassword")}
                      name="NewPassword"
                      rules={[
                        {
                          required: true,
                          message: `${t("PleaseInputYour")} ${t(
                            "NewPassword"
                          )}!`,
                        },
                      ]}
                    >
                      <Input.Password id="profile-new-password" />
                    </Form.Item>
                    <Form.Item
                      dependencies={["NewPassword"]}
                      label={t("ConfirmPassword")}
                      name="ConfirmPassword"
                      rules={[
                        {
                          required: true,
                          message: `${t("PleaseInputYour")} ${t(
                            "Confirm Password"
                          )}!`,
                        },
                        ({ getFieldValue }) => ({
                          validator(rule, value) {
                            if (
                              !value ||
                              getFieldValue("NewPassword") === value
                            ) {
                              return Promise.resolve();
                            }

                            return Promise.reject(
                              "The two passwords that you entered do not match!"
                            );
                          },
                        }),
                      ]}
                    >
                      <Input.Password id="profile-confirm-password" />
                    </Form.Item>
                    <Form.Item>
                      <Button
                        id="profile-save-password"
                        loading={loading}
                        type="primary"
                        htmlType="submit"
                      >
                        {t("Save")}
                      </Button>
                    </Form.Item>
                  </Form>
                </Col>
              </Row>
            </TabPane>
          </Tabs>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
