import React, { useState } from "react";
import ReactBpmn from "react-bpmn";
import styled from "styled-components";
import { Tabs, Input } from "antd";
import { useTranslation } from "react-i18next";
const { TabPane } = Tabs;

import Layout from "../../components/Layouts/Layout";
import Content from "../../components/Content";
import Container from "../../components/Layouts/Container";
import BreadcrumbNav from "../../components/BreadcrumbNav";
import DefaultTable from "../../components/Table/DefaultTable";

const Instance = () => {
  const [t] = useTranslation();
  const [filters, setFilters] = useState({});
  const navs = [{ label: t("BPMN") }, { label: t("Instance") }];
  const variablesColumns = [
    { title: t("Name") },
    { title: t("Value") },
    { title: t("Type") },
  ];
  const incidentsColumns = [
    { title: t("Message") },
    { title: t("TimeStamp") },
    { title: t("Activity") },
    { title: t("CauseProcessInstanceId") },
    { title: t("RootCauseProcessInstanceId") },
    { title: t("Type") },
  ];
  const taskColumns = [
    { title: t("Activity") },
    { title: t("Assignee") },
    { title: t("Owner") },
    { title: t("StartTime") },
    { title: t("EndTime") },
    { title: t("Priority") },
    { title: t("DelegationState") },
    { title: t("TaskKey") },
  ];

  const handleChangeFilter = (e) => {
    const { value, name } = e.target;
    setFilters({ ...filters, [name]: value });
  };
  return (
    <Layout>
      <Container>
        <BreadcrumbNav navs={navs} />
        <Content title={t("ProcessInstances")}>
          <BpmnWrapper>
            <ReactBpmn url="/test.bpmn" />
          </BpmnWrapper>
          <Tabs onChange={() => console.log("sd")} type="card">
            <TabPane tab={t("Variables")} key="1">
              <DefaultTable
                columns={variablesColumns}
                onReset={() => setFilters({})}
                filters={
                  <>
                    <Input
                      value={filters.Name}
                      name="Name"
                      placeholder="Name"
                      onChange={handleChangeFilter}
                      style={{ width: 200, marginRight: 10 }}
                    />
                    <Input
                      value={filters.Value}
                      name="Value"
                      placeholder="Value"
                      onChange={handleChangeFilter}
                      style={{ width: 200, marginRight: 10 }}
                    />
                  </>
                }
              />
            </TabPane>
            <TabPane tab={t("Incidents")} key="2">
              <DefaultTable columns={incidentsColumns} />
            </TabPane>
            <TabPane tab={t("UserTasks")} key="3">
              <DefaultTable columns={taskColumns} />
            </TabPane>
          </Tabs>
        </Content>
      </Container>
    </Layout>
  );
};

const BpmnWrapper = styled.div`
  margin-bottom: 20px;
  .react-bpmn-diagram-container {
    height: 300px;
  }
`;

export default Instance;
