import React from "react";
import { Tabs, Input, Form, Select, message } from "antd";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";

const { TabPane } = Tabs;
const Properties = ({ family }) => {
  const [t] = useTranslation();
  const { ItemTypes } = family;
  return (
    <Tabs defaultActiveKey="1" tabPosition="left">
      <TabPane tab={t("Translations")} key="1">
        <Form.List name="Translations">
          {() => (
            <>
              <Form.Item label={t("LocaleEN")} name="en-US">
                <Input />
              </Form.Item>
              <Form.Item label={t("LocaleTR")} name="tr-TR">
                <Input />
              </Form.Item>
            </>
          )}
        </Form.List>
      </TabPane>
      <TabPane tab={t("GeneralProperties")} key="2">
        <Form.Item label={t("Code")} name="Code">
          <Input />
        </Form.Item>
        <Form.Item label={t("ResourceType")} name="ItemType">
          <Select>
            {ItemTypes.map(({ Id, Label, IsSelected }) => (
              <Select.Option key={Id} value={Id} selected={IsSelected}>
                {Label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </TabPane>
    </Tabs>
  );
};

Properties.propTypes = {
  family: PropTypes.any,
};
export default Properties;
