import { privateAxiosInstance } from "./api";
import {
  GET_FAMILIES,
  CREATE_FAMILY,
  DELETE_FAMILY,
  GET_FAMILY,
} from "../consts/api";

export async function getFamilies(filter = {}) {
  return await privateAxiosInstance.post(GET_FAMILIES, filter);
}

export async function createFamily({ code, type }) {
  return await privateAxiosInstance.post(
    `${CREATE_FAMILY}?type=${type}&code=${code}`
  );
}

export async function deleteFamily(id) {
  return await privateAxiosInstance.post(`${DELETE_FAMILY}?id=${id}`);
}

export async function getFamily(id) {
  return await privateAxiosInstance.get(`${GET_FAMILY}?id=${id}`);
}
