import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Tabs, Skeleton, Modal, message } from "antd";
import { useParams, useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import Content from "../../../components/Content";
import Properties from "../editUser/tabs/properties";
import Password from "../editUser/tabs/password";
import History from "../editUser/tabs/history";

export default function EditUser() {
  const [t] = useTranslation();
  const { id } = useParams();
  const history = useHistory();

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("System") },
            { label: t("Edit User"), path: `/editUser/${id}` },
          ]}
        ></BreadcrumbNav>
      </Container>

      <Container>
        <Content title="Edit User">
          <Tabs type="card" defaultActiveKey="1">
            <Tabs.TabPane tab={t("General")} key="1">
              <Properties />
            </Tabs.TabPane>

            <Tabs.TabPane tab={t("Groups and Roles")} key="2"></Tabs.TabPane>

            <Tabs.TabPane tab={t("Change Password")} key="3">
              <Password />
            </Tabs.TabPane>
            <Tabs.TabPane tab={t("History")} key="4">
              <History />
            </Tabs.TabPane>
          </Tabs>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
