import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useParams } from "react-router-dom";
import { Form, Row, Col, Input, Select, Button, Skeleton, message } from "antd";

const Password = () => {
  const [t] = useTranslation();
  const { id } = useParams();
  const [form] = Form.useForm();
  return (
    <Form layout="vertical" form={form}>
      <Row gutter={[20]}>
        <Col span={12}>
          <Form.Item
            label={t("OldPassword")}
            name="OldPassword"
            rules={[
              {
                required: true,
                message: `${t("PleaseInputYour")} ${t("OldPassword")}!`,
              },
            ]}
          >
            <Input.Password />
          </Form.Item>

          <Form.Item
            label={t("NewPassword")}
            name="NewPassword"
            rules={[
              {
                required: true,
                message: `${t("PleaseInputYour")} ${t("NewPassword")}!`,
              },
            ]}
          >
            <Input.Password />
          </Form.Item>

          <Form.Item
            dependencies={["NewPassword"]}
            label={t("ConfirmPassword")}
            name="ConfirmPassword"
            rules={[
              {
                required: true,
                message: `${t("PleaseInputYour")} ${t("Confirm Password")}!`,
              },
              ({ getFieldValue }) => ({
                validator(rule, value) {
                  if (!value || getFieldValue("NewPassword") === value) {
                    return Promise.resolve();
                  }

                  return Promise.reject(
                    "The two passwords that you entered do not match!"
                  );
                },
              }),
            ]}
          >
            <Input.Password />
          </Form.Item>

          <Form.Item>
            <Button type="primary" htmlType="submit">
              {t("Save")}
            </Button>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
};

Password.propTypes = {};

export default Password;
