import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useParams } from "react-router-dom";
import { Skeleton } from "antd";

import HistoryItem from "../../../../components/turkcell/HistoryItem";
import { getItemHistory } from "../../../../services/turkcell/items";

const History = () => {
  const { id } = useParams();
  const [history, setHistory] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchData() {
      const res = await getItemHistory(id);
      setHistory(res.data.Value.data);
      setLoading(false);
    }
    fetchData();
  }, []);
  return (
    <>
      <Title>İşlem Tarihçesi</Title>
      {loading && <Skeleton active />}
      {history.map((item, index) => (
        <HistoryItem
          key={index}
          history={item}
          bgColor={index % 2 == 0 ? "#fafafa" : "#fff"}
        />
      ))}
    </>
  );
};

const Title = styled.h3`
  color: #253342;
  font-size: 18px;
  font-weight: bold;
  padding-left: 20px;
`;
export default History;
