import React, { useState } from "react";
import PropTypes from "prop-types";
import { Modal, Tabs, Form, Input, Select, message } from "antd";
import { useTranslation } from "react-i18next";

// Services
import { createAttributeGroup } from "../../services/attributes";

export default function CreateAttributeGroup({
  isModalVisible,
  onCancel,
  categories,
  onCreate,
}) {
  const [t] = useTranslation();
  const [form] = Form.useForm();
  const onChange = () => {
    console.log("select change");
  };
  const onFinish = async ({ Code, ItemType, tr_TR }) => {
    const reqData = {
      Code,
      ItemType,
      Label: tr_TR,
      Translations: [
        {
          Locale: "tr-TR",
          Label: tr_TR,
        },
      ],
    };
    const res = await createAttributeGroup(reqData);
    const { Value } = res.data;
    if (Value.Status == -1) {
      return message.error(Value.Message);
    }
    form.resetFields();
    message.success(Value.Message);
    onCreate(reqData);
  };
  const handleOk = () => form.submit();

  return (
    <>
      <Modal
        title="Create new - Attribute Group"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={onCancel}
        maskClosable={false}
      >
        <Form layout="vertical" onFinish={onFinish} form={form}>
          <Form.Item name="Code" label="Code" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
          <Form.Item
            name="ItemType"
            label="Resource Type"
            rules={[{ required: true }]}
          >
            <Select placeholder="Select...">
              {categories.map(({ Id, Label }) => (
                <Select.Option key={Id} value={Id}>
                  {Label}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item name="tr_TR" label="Label" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
}

CreateAttributeGroup.propTypes = {
  isModalVisible: PropTypes.bool.isRequired,
  onCancel: PropTypes.func,
  categories: PropTypes.any,
  onCreate: PropTypes.func,
};
