import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Space, Input } from "antd";
import { useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../components/Layouts/Default";
import BreadcrumbNav from "../../components/BreadcrumbNav";
import Container from "../../components/Layouts/Container";
import DefaultTable from "../../components/Table/DefaultTable";
import Content from "../../components/Content";
import CreateAssociationType from "../../components/Modal/CreateAssociationType";

// Services
import { getAssocationTypes } from "../../services/assocations";

export default function AssociationTypes() {
  let history = useHistory();
  const [t] = useTranslation();
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [filters, setFilters] = useState({});
  const columns = [
    {
      title: t("Code"),
      dataIndex: "Code",
    },
    {
      title: t("Label"),
      dataIndex: "Label",
    },
  ];

  const onEdit = ({ Id }) => {
    history.push(`/associations/${Id}`);
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  useEffect(() => {
    async function fetchData() {
      const res = await getAssocationTypes(filters);
      setData(res.data.Value.data);
    }
    fetchData();
  }, [filters]);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("AssociationTypes"), path: "#" },
          ]}
        ></BreadcrumbNav>
      </Container>

      <CreateAssociationType
        isModalVisible={isModalVisible}
        onCancel={() => setIsModalVisible(false)}
      />
      <Container>
        <Content
          id="association-types"
          title={t("AssociationTypes")}
          actionTitle={t("CreateAssociationType")}
          onClick={() => setIsModalVisible(true)}
        >
          <DefaultTable
            actions
            loading={loading}
            dataSource={data}
            columns={columns}
            onEdit={onEdit}
            filters={
              <Space size={10}>
                <Input
                  name="Code"
                  placeholder={t("Code")}
                  style={{ width: 200 }}
                  onChange={(val) => filterChange(val)}
                />
                <Input
                  name="Label"
                  placeholder={t("Label")}
                  style={{ width: 200 }}
                  onChange={(val) => filterChange(val)}
                />
              </Space>
            }
          />
        </Content>
      </Container>
    </DefaultLayout>
  );
}
