import React from "react";
import { Switch } from "react-router-dom";
import PropTypes from "prop-types";

import Families from "../pages/settings/families";
import EditFamily from "../pages/settings/editFamily";

// Components
import PrivateRoute from "../components/PrivateRoute";

const FamilyRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute path={`${url}/:id`} component={EditFamily} />
      <PrivateRoute path={`${url}`} component={Families} />
    </Switch>
  </>
);

FamilyRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default FamilyRoute;
