import React from "react";
import { Table } from "antd";
import { useTranslation } from "react-i18next";

const AssociationTemplate = () => {
  const [t] = useTranslation();
  const columns = [
    {
      title: t("TemplateNames"),
      dataIndex: "name",
      key: "name",
      sorter: (a, b) => a.length - b.length,
    },
    {
      title: t("Department"),
      dataIndex: "age",
      key: "age",
      sorter: (a, b) => a.length - b.length,
    },
    {
      title: t("Category"),
      dataIndex: "address",
      key: "address",
      sorter: (a, b) => a.length - b.length,
    },
  ];
  return <Table columns={columns} pagination={true} />;
};

export default AssociationTemplate;
