import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import {
  Row,
  Col,
  Tabs,
  Skeleton,
  Form,
  Input,
  Select,
  Button,
  Switch,
  Popconfirm,
  message,
  Card,
} from "antd";
import styled from "styled-components";
import { DeleteOutlined } from "@ant-design/icons";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import Content from "../../../components/Content";
import { Settings } from "./tabs";
// Tabs
import UserGroupPermission from "../../enrich/editItem/tabs/userGroupPermission";
import UserPermission from "../../enrich/editItem/tabs/userPermissions";
import History from "../../enrich/editItem/tabs/history";

import {
  DeleteAttribute,
  GetEditAttribute,
  GetValidationValues,
} from "../../../services/attributes";
import { useLocation, useHistory, useParams } from "react-router-dom";

export default function EditAttribute() {
  let { attributeId } = useParams();
  let history = useHistory();
  const [t] = useTranslation();
  const [loading, setLoading] = useState(false);
  const [languageForm] = Form.useForm();
  const [rules, setRules] = useState([]);
  const [editAttributeType, setEditAttributeType] = useState(null);

  useEffect(() => {
    getAttrTypes();
  }, []);

  const getAttrTypes = async () => {
    try {
      setLoading(true);
      const {
        data: { Value },
      } = await GetEditAttribute(attributeId);
      setEditAttributeType(Value);
      console.log("Value", Value);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.log(e);
    }
  };
  const getRules = async () => {
    try {
      setLoading(true);
      const {
        data: { Value },
      } = await GetValidationValues(attributeId);
      console.log(Value);
    } catch (e) {
      setLoading(false);
      console.log(e);
    }
    setRules([
      {
        name: "Null",
      },
      {
        name: "NotNull",
      },
      {
        name: "Empty",
      },
      {
        name: "NotEmpty",
      },
      {
        name: "Equal",
      },
      {
        name: "NotEqual",
      },
    ]);
  };

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  useEffect(() => {
    getRules();
  }, []);

  const onDelete = async () => {
    try {
      message.loading({ content: t("loading"), key: "deleteAttributes" });
      await DeleteAttribute(editAttributeType.Id);
      history.replace("/attributes");
      message.success({
        content: t("deleteAttributesSuccessMessage"),
        key: "deleteAttributes",
        duration: 4,
      });
    } catch (e) {
      message.error({
        content: e.message || e.Message,
        key: "deleteAttributes",
        duration: 4,
      });
    }
  };

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: t("Settings") },
            { label: t("EditAttribute"), path: `/attributes` },
          ]}
        />
      </Container>
      <Container>
        <Content
          id="edit-attribute"
          title={t("EditAttribute")}
          actionType="danger"
          actionTitle={t("Delete")}
        >
          <Row>
            <Col span={24}>
              {loading && skeletonWrapper()}
              {!loading && editAttributeType && (
                <Tabs type="card" defaultActiveKey="1">
                  <Tabs.TabPane tab={t("Properties")} key="1">
                    <Settings
                      editAttributeType={editAttributeType}
                      rules={rules}
                    />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab={t("UserGroupPermissions")} key="3">
                    <UserGroupPermission />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab={t("UserPermissions")} key="4">
                    <UserPermission />
                  </Tabs.TabPane>
                  <Tabs.TabPane tab={t("History")} key="5">
                    <History />
                  </Tabs.TabPane>
                </Tabs>
              )}
            </Col>
          </Row>
        </Content>
      </Container>
    </DefaultLayout>
  );
}

const TitleWrapper = styled.p`
  background-color: #fff;
  padding: 10px 20px;
`;

const LeftTabsWrapper = styled(Tabs)`
  background-color: ${({ theme }) => theme.mainColor};
  min-height: 50vh;

  .ant-tabs-nav {
    width: 200px;
  }

  .ant-tabs-content-holder {
    background-color: ${({ theme }) => theme.bodyColor};
  }
`;
