import React, { useState } from "react";
import { Row, Col, Form, Table, Input, Radio } from "antd";
import styled from "styled-components";
import PropTypes from "prop-types";

// Mock Data
import { permissonOptions } from "../../mockData";

export default function PermissonTable({ dataSource, columns, loading }) {
  const [data, setData] = useState(dataSource);
  function getColumnsData(columns) {
    return [
      ...columns,
      {
        title: "Permisson",
        render(text, row, index) {
          return (
            <Radio.Group
              value={row.permisson}
              onChange={(e) => permissonChange(e, row, index)}
            >
              <Row gutter={8}>
                {permissonOptions.map(({ label, value }) => (
                  <Col key={value}>
                    <Radio value={value}>{label}</Radio>
                  </Col>
                ))}
              </Row>
            </Radio.Group>
          );
        },
      },
    ];
  }
  function permissonChange(e, row, index) {
    console.log(row);
    data[index] = {
      ...row,
      permisson: e.target.value,
    };
    setData([...data]);
  }

  return (
    <>
      <Row>
        <Col span={12}>
          <FilterWrapper>
            <Form
              layout="inline"
              name="basic"
              initialValues={{ remember: true }}
            >
              <Form.Item name="username">
                <Input placeholder="Filtre 1" />
              </Form.Item>
            </Form>
          </FilterWrapper>
        </Col>
      </Row>
      <Table
        loading={loading}
        columns={getColumnsData(columns)}
        dataSource={data}
      />
    </>
  );
}
PermissonTable.propTypes = {
  dataSource: PropTypes.any,
  columns: PropTypes.any,
  loading: PropTypes.bool,
};
const FilterWrapper = styled.div`
  margin-bottom: 20px;
  display: flex;
`;
