import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Link, useParams } from "react-router-dom";
import { message } from "antd";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";

// Services
import { getItems, toggleFavItem } from "../../services/turkcell/items";

// Components
import Layout from "../../components/turkcell/Layout";
import Content from "../../components/turkcell/Content";
import Filter from "../../components/turkcell/TemplateFilter";
import ContractTable from "../../components/turkcell/ContractTable";
import CustomPanel from "../../components/turkcell/CustomPanel";
import Button from "../../components/turkcell/Button";

// Helpers
import { getTemplateTableColumns, getColumnsKeys } from "../../helpers/table";

// Consts
import { TEMPLATE_CATEGORY, TEMPLATE_ITEM_TYPE } from "../../consts";

// Actions
import { fetchCategory } from "../../store/actions/category";

// Selectors
import { getCategory } from "../../store/selectors/category";

export default function Items() {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const [items, setItems] = useState([]);
  const [filters, setFilters] = useState(null);
  const [loading, setLoading] = useState(true);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });
  const { categoryId } = useParams();
  const selectedCategoryId = categoryId ? categoryId : TEMPLATE_CATEGORY;
  const category = getCategory(selectedCategoryId);
  const navs = [
    {
      label: t("Templates"),
      path: "/",
    },
  ];

  useEffect(() => {
    dispatch(fetchCategory(TEMPLATE_CATEGORY, TEMPLATE_ITEM_TYPE));
  }, []);

  useEffect(() => {
    async function fetchData() {
      const items = await handleGetItems(pagination);
      setPagination({
        ...pagination,
        total: items.recordsTotal,
      });
    }
    fetchData();
  }, [categoryId, filters]);

  const handleGetItems = async (pagination) => {
    const { current, pageSize } = pagination;
    const reqData = {
      OrderedColumns: getColumnsKeys(tableColumns),
      ItemType: "ContractTemplate",
      ChannelId: 2,
      SelectedCategoryId: selectedCategoryId,
      CurrentPage: current - 1,
      PageSize: pageSize,
    };
    if (filters) {
      const modifyFilter = { ...filters };
      const unit = modifyFilter["MADDE_ILGILI_BIRIM"];
      if (unit) {
        modifyFilter["MADDE_ILGILI_BIRIM"] = modifyFilter[
          "MADDE_ILGILI_BIRIM"
        ].join();
      }
      reqData.DynamicFilters = modifyFilter;
    }
    setLoading(true);
    const itemRes = await getItems(reqData);
    const items = itemRes.data.data;
    setItems(items);
    setPagination({ ...pagination, total: itemRes.data.recordsTotal });
    setLoading(false);
    return itemRes.data;
  };

  const handleOnFinish = (values) => {
    setFilters(values);
  };

  const onLike = async ({ Id, IsFavorite }) => {
    message.loading({ content: t("Loading"), key: "fav" });
    await toggleFavItem(Id);
    const modifyItems = items.map((item) => {
      if (item.Id == Id) {
        item.IsFavorite = !item.IsFavorite;
      }
      return item;
    });
    message.success({
      content: IsFavorite ? t("RemovedFromFavorites") : t("AddedtoFavorites"),
      key: "fav",
    });

    setItems(modifyItems);
  };

  const getTableItems = () => {
    return items.map((item) => {
      if (item.Associations.length) {
        return {
          ...item,
          children: item.Associations[0].Items,
        };
      }
      return {
        ...item,
        children: [],
      };
    });
  };

  const tableColumns = category
    ? getTemplateTableColumns(category, onLike, t)
    : [];

  return (
    <Layout>
      <Content navs={[...navs, { label: category && category.code }]}>
        <Filter onFinish={handleOnFinish} onClear={() => setFilters(null)} />
        <TableWrapper>
          <CustomPanel label={`${category?.code} ${t("Templates")}`}>
            <ContractTable
              id="template-table"
              data={getTableItems()}
              columns={tableColumns}
              loading={loading}
              pagination={pagination}
              onChange={handleGetItems}
            />
          </CustomPanel>
        </TableWrapper>
        <ButtonWrapper>
          <Link to={`/templates/${selectedCategoryId}/create`}>
            <Button
              id="new-template"
              btnType="yellow"
              title={t("Yeni Şablon")}
            />
          </Link>
        </ButtonWrapper>
      </Content>
    </Layout>
  );
}

const TableWrapper = styled.div`
  margin-top: 20px;
`;
const ButtonWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
  margin-bottom: 50px;
`;
