import { FETCH_CATEGORY, SET_CATEGORY_LOADING } from "../consts/category";
import { getCategoiresTree } from "../../services/turkcell/items";
import { DEFAULT_CATEGORY } from "../../consts";

export function fetchCategory(categoryId, itemType) {
  return async (dispatch) => {
    dispatch({ type: SET_CATEGORY_LOADING, payload: true });
    try {
      const res = await getCategoiresTree(categoryId, itemType);
      dispatch({
        type: FETCH_CATEGORY,
        payload: res.data[0] ? res.data[0] : {},
      });
      dispatch({ type: SET_CATEGORY_LOADING, payload: false });
    } catch (err) {
      dispatch({ type: SET_CATEGORY_LOADING, payload: false });
    }
  };
}
