import { privateAxiosInstance } from "./api";
import {
  GETATTRIBUTES,
  DELETEATTRIBUTE,
  DELETEATTRIBUTES,
  GETATTRIBUTESTYPE,
  GETVALIDATIONPARAMETERS,
  NEWATTRIBUTEBYMODEL,
  GETEDITATTRIBUTE,
  GETVALIDATIONVALUES,
  ATTRIBUTE_GROUPS,
  ATTRIBUTE_GROUP_LIST,
  GET_ATTRIBUTE_GROUP,
  REMOVE_ATTRIBUTE_GROUP,
  CREATE_ATTRIBUTE_GROUP,
  EDIT_ATTRIBUTE,
} from "../consts/api";

export async function Getattributes(filter = {}) {
  return await privateAxiosInstance.post(GETATTRIBUTES, filter);
}

export async function DeleteAttributes(idListArray) {
  return await privateAxiosInstance.post(DELETEATTRIBUTES, {
    idList: idListArray,
  });
}

export async function DeleteAttribute(id) {
  return await privateAxiosInstance.post(`${DELETEATTRIBUTE}?id=${id}`);
}

export async function GetAttributesType(type) {
  return await privateAxiosInstance.post(`${GETATTRIBUTESTYPE}${type}`);
}

export async function GetValidationParameters(type) {
  return await privateAxiosInstance.post(`${GETVALIDATIONPARAMETERS}${type}`);
}

export async function CreateAttributeByModel(attributeModel) {
  return await privateAxiosInstance.post(NEWATTRIBUTEBYMODEL, attributeModel);
}

export async function GetEditAttribute(attributeId) {
  return await privateAxiosInstance.post(`${GETEDITATTRIBUTE}${attributeId}`);
}
export async function GetValidationValues(attributeId) {
  return await privateAxiosInstance.post(
    `${GETVALIDATIONVALUES}${attributeId}`
  );
}
export async function getAttributeGroups() {
  return await privateAxiosInstance.get(`${ATTRIBUTE_GROUPS}`);
}
export async function getAttributeGroupList(type) {
  return await privateAxiosInstance.post(
    `${ATTRIBUTE_GROUP_LIST}?type=${type}`
  );
}
export async function getAttributeGroup(id) {
  return await privateAxiosInstance.get(`${GET_ATTRIBUTE_GROUP}?id=${id}`);
}
export async function removeAttributeGroup(id) {
  return await privateAxiosInstance.post(`${REMOVE_ATTRIBUTE_GROUP}?id=${id}`);
}

export async function createAttributeGroup(data) {
  return await privateAxiosInstance.post(`${CREATE_ATTRIBUTE_GROUP}`, data);
}
export async function editAttribute(data) {
  return await privateAxiosInstance.post(`${EDIT_ATTRIBUTE}`, data);
}
