import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Button, Tabs, Modal, Skeleton } from "antd";
import NoImage from "../../../assets/images/no-image.png";
import styled from "styled-components";
import { CheckOutlined, LeftOutlined } from "@ant-design/icons";

// components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import ItemLine from "../../../components/Shared/ItemLine";

// Tabs
import Preview from "./tabs/preview";
import Attributes from "./tabs/attributes";
import Categories from "./tabs/categories";
import RelationShips from "./tabs/relationShips";
import UserGroupPermission from "./tabs/userGroupPermission";
import UserPermission from "./tabs/userPermissions";
import History from "./tabs/history";
import BreadcrumbNav from "../../../components/BreadcrumbNav";

export default function EditItem() {
  const [t] = useTranslation();
  const [itemStatus, setItemStatus] = useState(true);
  const [tabs, setTabs] = useState([]);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [loading, setLoading] = useState(false);

  const contentRequest = () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  };

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleModalOk = () => {
    setIsModalVisible(false);
  };

  const handleModalCancel = () => {
    setIsModalVisible(false);
  };

  const tabRequest = () => {
    setTabs([
      {
        name: t("preview"),
        key: "preview",
        component: <Preview />,
      },
      {
        name: t("attributes"),
        key: "attributes",
        component: <Attributes />,
      },
      {
        name: t("categories"),
        key: "categories",
        component: <Categories />,
      },
      {
        name: t("relationships"),
        key: "relationships",
        component: <RelationShips />,
      },
      {
        name: t("usergrouppermission"),
        key: "usergrouppermission",
        component: <UserGroupPermission />,
      },
      {
        name: t("userpermission"),
        key: "userpermission",
        component: <UserPermission />,
      },
      {
        name: t("history"),
        key: "history",
        component: <History />,
      },
    ]);
  };

  const skeletonWrapper = () => {
    return <Skeleton paragraph={{ rows: 16 }} active />;
  };

  useEffect(() => {
    contentRequest();
    tabRequest();
  }, []);

  const onChangeTabHandle = (tabKey) => {
    console.log(tabKey);
  };

  const changeItemStatus = () => {
    setItemStatus(!itemStatus);
  };

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[{ label: "Enrich" }, { label: "Edit Item", path: "#" }]}
        />
      </Container>

      {loading && <Container>{skeletonWrapper()}</Container>}
      {!loading && (
        <>
          <ItemLine></ItemLine>
          <Container>
            <Modal
              title="Basic Modal"
              visible={isModalVisible}
              onOk={handleModalOk}
              onCancel={handleModalCancel}
            >
              <p>Some contents...</p>
              <p>Some contents...</p>
              <p>Some contents...</p>
            </Modal>

            <ItemHeadWrapper>
              <Row gutter={20}>
                <Col span={4}>
                  <img src={NoImage} width="200"></img>
                </Col>
                <Col span={14}>
                  <ItemInfoWrapper>
                    <h2>1.05452.AG.110.01</h2>
                    <h1>VC5452B01 30CM SIYAH VITROSERAMIK OCAK</h1>
                    {itemStatus && (
                      <Button onClick={changeItemStatus} type="primary">
                        {t("active")}
                      </Button>
                    )}

                    {!itemStatus && (
                      <Button onClick={changeItemStatus} type="danger">
                        {t("disabled")}
                      </Button>
                    )}
                    <p>{t("family")}: Ocak(Ticari)</p>
                  </ItemInfoWrapper>
                </Col>
                <Col span={6}>
                  <ItemInfoWrapper>
                    <Row gutter={8} justify="end">
                      <Col>
                        <Button size="small" icon={<LeftOutlined />}></Button>
                      </Col>

                      {/* <Col>
                      <Button
                        size="small"
                        icon={<CheckOutlined />}
                        type="primary"
                      >
                        {t("save")}
                      </Button>
                    </Col> */}

                      <Col>
                        <Button
                          onClick={showModal}
                          size="small"
                          icon={<CheckOutlined />}
                          type="primary"
                        >
                          {t("actions")}
                        </Button>
                      </Col>
                    </Row>
                  </ItemInfoWrapper>
                </Col>
              </Row>
            </ItemHeadWrapper>
          </Container>

          <Container>
            <TabsWrapper type="card" onChange={onChangeTabHandle}>
              {tabs.map((item) => (
                <Tabs.TabPane tab={item.name} key={item.key}>
                  {item.component}
                </Tabs.TabPane>
              ))}
            </TabsWrapper>
          </Container>
        </>
      )}
    </DefaultLayout>
  );
}

const TabsWrapper = styled(Tabs)`
  padding: 20px 0;
`;

const ItemHeadWrapper = styled.div`
  padding: 20px 0;
`;

const ItemInfoWrapper = styled.div`
  padding: 20px 0;

  h2 {
    color: ${({ theme }) => theme.orange};
    font-size: 1.5rem;
    margin: 0;
  }

  h1 {
    font-size: 1.5rem;
  }

  p {
    padding-top: 15px;
    font-size: 1rem;
    margin: 0;
  }
`;
