import React from "react";
import { Switch, Route } from "react-router-dom";
import PropTypes from "prop-types";
import Roles from "../pages/system/roles";
import NewRole from "../pages/system/newRole";
import EditRole from "../pages/system/editRole";

// Components
import PrivateRoute from "../components/PrivateRoute";

const RolesRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute path={`${url}/newRole`} component={NewRole} />
      <PrivateRoute path={`${url}/:id`} component={EditRole} />
      <PrivateRoute path={`${url}`} component={Roles} />
    </Switch>
  </>
);

RolesRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};

export default RolesRoute;
