import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Row, Col, Space, Tooltip, message } from "antd";
import { DragOutlined } from "@ant-design/icons";

import { Copy, Share } from "../icons";
import StatusIcon from "../Table/Columns/Status";

const SummaryItem = ({ bgColor, item }) => {
  const { Attributes } = item;
  const { MADDE_ADI, MADDE_METNI } = Attributes.reduce((acc, curr) => {
    acc[curr.Code] = curr.Value;
    return acc;
  }, {});

  const handleCopy = () => message.success("Copied!");
  return (
    <Row
      gutter={10}
      align="middle"
      style={{
        backgroundColor: bgColor,
        paddingTop: 20,
        paddingBottom: 20,
        paddingRight: 10,
      }}
    >
      <Col span={2} align="middle">
        <DragIcon />
      </Col>
      <Col span={16}>
        <Title>{MADDE_ADI}</Title>
        <SubTitle>Tahsilat</SubTitle>
        <Content
          dangerouslySetInnerHTML={{
            __html: MADDE_METNI ? MADDE_METNI.slice(0, 300) : "",
          }}
        ></Content>
      </Col>
      <Col span={6} align="middle">
        <Space size={30}>
          <StatusIcon status={1} />
          <Tooltip title="Copy">
            <Copy onClick={handleCopy} />
          </Tooltip>
          <Tooltip title="Share">
            <Share onClick={handleCopy} />
          </Tooltip>
        </Space>
      </Col>
    </Row>
  );
};

const Title = styled.h3`
  font-size: 18px;
  color: #4d4d4d;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 5px;
  line-height: 1;
`;

const SubTitle = styled.span`
  font-size: 12px;
  color: #979797;
  margin-bottom: 5px;
`;

const Content = styled.p`
  font-size: 14px;
  color: #4d4d4d;
  max-height: 50px;
  overflow-y: auto;
`;

const DragIcon = styled(DragOutlined)`
  font-size: 24px;
  color: ${({ theme }) => theme.turkcell.mainColor};
`;

SummaryItem.propTypes = {
  bgColor: PropTypes.string,
  item: PropTypes.any,
};

export default SummaryItem;
