import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Modal, Tabs, Form, Input, Select, message } from "antd";
import { useTranslation } from "react-i18next";

// Services
import { createFamily } from "../../services/families";
import { ItemTypes } from "../../services/items";

export default function CreateFamiliy({ isModalVisible, onCancel, onCreate }) {
  const [t] = useTranslation();
  const [form] = Form.useForm();
  const [itemTypes, setItemTypes] = useState([]);
  const [loading, setLoading] = useState([]);

  const onFinish = async (values) => {
    const res = await createFamily(values);
    const { Value } = res.data;
    if (Value.Status == -1) {
      return message.error(Value.Message);
    }
    form.resetFields();
    onCreate(Value.Result);
    message.success(Value.Message);
  };
  const handleOk = () => form.submit();

  useEffect(() => {
    const getItemTypes = async () => {
      try {
        const result = await ItemTypes();
        setItemTypes(result.data.Value);
        setLoading(false);
        onCreate();
      } catch (err) {
        console.log(err);
        setLoading(false);
      }
    };
    getItemTypes();
  }, []);
  return (
    <>
      <Modal
        title={t("CreateFamily")}
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={onCancel}
        maskClosable={false}
      >
        <Form layout="vertical" onFinish={onFinish} form={form}>
          <Form.Item
            name="code"
            label={t("FamilyName")}
            rules={[{ required: true }]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="type"
            label={t("ResourceType")}
            rules={[{ required: true }]}
          >
            <Select placeholder="Select...">
              {Object.keys(itemTypes).map((code) => (
                <Select.Option key={code} value={code}>
                  {code}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
}

CreateFamiliy.propTypes = {
  isModalVisible: PropTypes.bool.isRequired,
  onCancel: PropTypes.func,
  onCreate: PropTypes.func,
};
