import React from "react";
import styled from "styled-components";
import { PlusOutlined } from "@ant-design/icons";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";

export default function CreateBar({ title, clickEvent }) {
  const [t] = useTranslation();

  return (
    <BarWrapper>
      <BarTitle clickEvent={clickEvent}>{title}</BarTitle>
      {clickEvent && (
        <BarLink onClick={clickEvent}>
          <PlusOutlined />
          <BtnText>{t("createNew")}</BtnText>
        </BarLink>
      )}
    </BarWrapper>
  );
}

const BarWrapper = styled.div`
  background-color: ${({ theme }) => theme.mainColor};
  margin-top: 15px;
  margin-bottom: 15px;
`;
const BarTitle = styled.span`
  padding-right: 10px;
  ${({ clickEvent }) =>
    clickEvent && `border-right: 1px solid ${({ theme }) => theme.gray};`}
`;
const BtnText = styled.span`
  margin-left: 5px;
`;
const BarLink = styled.a`
  padding-left: 10px;
`;
CreateBar.propTypes = {
  title: PropTypes.node.isRequired,
};

CreateBar.propTypes = {
  clickEvent: PropTypes.func,
};

CreateBar.propTypes = {
  path: PropTypes.string,
};

CreateBar.propTypes = {
  create: PropTypes.bool,
};

CreateBar.defaultProps = {
  create: true,
};
