import React from "react";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";
import { Form, Select, Input, Switch, Button, Tabs, message } from "antd";
import { useParams } from "react-router-dom";

import { editAttribute } from "../../../../services/attributes";

const Settings = ({ editAttributeType, rules }) => {
  const [t] = useTranslation();
  const { attributeId } = useParams();
  const {
    Code,
    AttributeType,
    ItemType,
    ItemTypes,
    Localizable,
  } = editAttributeType;

  const onFinish = async (values) => {
    message.loading({ content: "Loading...", key: "onFinish" });
    try {
      await editAttribute({
        Id: attributeId,
        ...values,
      });
      message.success({ content: "Saved!", key: "onFinish" });
    } catch (err) {
      message.error({ content: "There is error!", key: "onFinish" });
    }
  };

  return (
    <Tabs defaultActiveKey="1" tabPosition="left" type="card">
      <Tabs.TabPane tab={t("General")} key="1">
        <Form layout="vertical" name="basic" onFinish={onFinish}>
          <Form.Item
            label={t("Code")}
            name="Code"
            initialValue={Code}
            rules={[
              {
                required: true,
                message: `${t("PleaseInputYour")} ${t("Code")}`,
              },
            ]}
          >
            <Input id="attribute-code"></Input>
          </Form.Item>
          <Form.Item
            label={t("AttributeType")}
            name="AttributeType"
            initialValue={AttributeType}
            rules={[
              {
                required: true,
                message: `${t("PleaseInputYour")} ${t("AttributeType")}`,
              },
            ]}
          >
            <Input id="attribute-type" disabled />
          </Form.Item>
          <Form.Item
            label={t("ResourceType")}
            name="ItemType"
            initialValue={ItemType}
            rules={[
              {
                required: true,
                message: `${t("PleaseInputYour")} ${t("ResourceType")}`,
              },
            ]}
          >
            <Select id="attribute-resource-type">
              {ItemTypes.map((itemType) => {
                return (
                  <Select.Option key={itemType.Id} value={itemType.Id}>
                    {itemType.Label}
                  </Select.Option>
                );
              })}
            </Select>
          </Form.Item>
          <Form.Item
            label={t("Localizable")}
            name="Localizable"
            valuePropName={"Localizable"}
            initialValue={Localizable}
          >
            <Switch id="attribute-localizable" />
          </Form.Item>

          <Form.Item label={t("Filters")} name="filters">
            <Switch id="attribute-filters"></Switch>
          </Form.Item>
          <Form.Item>
            <Button
              id="attribute-settings-save"
              htmlType="submit"
              type="primary"
            >
              {t("Save")}
            </Button>
          </Form.Item>
        </Form>
      </Tabs.TabPane>
      <Tabs.TabPane tab={t("ValidationParams")} key="2">
        <Form layout="vertical" name="basic" onFinish={onFinish}>
          {rules.map((item) => (
            <>
              <Form.Item label={item.name} name={item.name}>
                <Switch id="validation-params"></Switch>
              </Form.Item>
            </>
          ))}
          <Form.Item>
            <Button
              id="attribute-validation-params-save"
              htmlType="submit"
              type="primary"
            >
              Save Validation Rules
            </Button>
          </Form.Item>
        </Form>
      </Tabs.TabPane>
      <Tabs.TabPane tab={t("LabelTranslations")} key="3">
        <Form layout="vertical" name="basic" onFinish={onFinish}>
          <Form.List name="Translations">
            {() => (
              <>
                <Form.Item
                  id="attribute-translations-en"
                  label="English"
                  name="en-US"
                  initialValue={editAttributeType.en_US}
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  id="attribute-translations-tr"
                  label="Turkish"
                  name="tr-TR"
                  initialValue={editAttributeType.tr_TR}
                  rules={[
                    {
                      required: true,
                    },
                  ]}
                >
                  <Input />
                </Form.Item>

                <Form.Item>
                  <Button
                    id="attribute-save-translations"
                    htmlType="submit"
                    type="primary"
                  >
                    Save Translations
                  </Button>
                </Form.Item>
              </>
            )}
          </Form.List>
        </Form>
      </Tabs.TabPane>
    </Tabs>
  );
};

Settings.propTypes = {
  editAttributeType: PropTypes.any,
  rules: PropTypes.any,
};

export default Settings;
