import React from "react";
import { Row, Col, Modal, Button } from "antd";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import {
  CalendarOutlined,
  NumberOutlined,
  FileTextOutlined,
  FileTextFilled,
  CaretDownFilled,
  CaretRightFilled,
  BulbOutlined,
  UserOutlined,
  ToolFilled,
  MoneyCollectOutlined,
  CodeOutlined,
} from "@ant-design/icons";
const attributesType = [
  {
    title: "Date",
    key: "Date",
    icon: <CalendarOutlined />,
  },
  {
    title: "Number",
    key: "Number",
    icon: <NumberOutlined />,
  },
  {
    title: "Text",
    key: "Text",
    icon: <FileTextOutlined />,
  },
  {
    title: "Rich Text",
    key: "Rich Text",
    icon: <FileTextFilled />,
  },
  {
    title: "Simple Select",
    key: "Simple Select",
    icon: <CaretDownFilled />,
  },
  {
    title: "Multiple Select",
    key: "Multiple Select",
    icon: <CaretRightFilled />,
  },
  {
    title: "Bool",
    key: "Bool",
    icon: <BulbOutlined />,
  },
  {
    title: "User",
    key: "User List",
    icon: <UserOutlined />,
  },
  {
    title: "Resource Select",
    key: "Resource Select",
    icon: <ToolFilled />,
  },
  {
    title: "Price",
    key: "Price",
    icon: <MoneyCollectOutlined />,
  },
  {
    title: "Image Base64",
    key: "ImageBase64",
    icon: <CodeOutlined />,
  },
  {
    title: "Clob",
    key: "Clob",
    icon: <CodeOutlined />,
  },
];

export default function CreateAttribute({ isModalVisible, onCancel }) {
  const [t] = useTranslation();

  return (
    <Modal
      title={t("createanewattribute")}
      visible={isModalVisible}
      onCancel={onCancel}
      footer={null}
    >
      <Row gutter={[16, 16]}>
        {attributesType.map((type) => {
          return (
            <Col span={12} key={type.key}>
              <ModalItem to={`/attributes/create/${type.key}`}>
                {type.icon}
                <p>{type.title}</p>
              </ModalItem>
            </Col>
          );
        })}
      </Row>
    </Modal>
  );
}

const ModalItem = styled(Link)`
  padding: 5px 10px;
  background-color: ${({ theme }) => theme.mainColor};
  width: 100%;
  display: flex;
  align-items: center;
  color: black;

  p {
    margin: 0;
    padding-left: 10px;
  }
`;

CreateAttribute.propTypes = {
  isModalVisible: PropTypes.bool.isRequired,
  onCancel: PropTypes.func,
};
