import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { Tabs, Skeleton, Modal, message } from "antd";
import { useParams, useHistory } from "react-router-dom";
// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import Content from "../../../components/Content";
import { Properties } from "./tabs";
import History from "../../enrich/editItem/tabs/history";

// Services
import { deleteAssocationType } from "../../../services/assocations";

export default function EditAssociationType() {
  const [t] = useTranslation();
  const [loading, setLoading] = useState(false);
  const { id } = useParams();
  const history = useHistory();

  const deleteHandler = () => {
    Modal.confirm({
      title: "Silme işlemi",
      content: "Assocation type silenecek onayliyor musunuz?",
      okText: "EVET",
      cancelText: "HAYIR",
      onOk: async () => {
        message.loading({ content: "Loading..", key: " delete" });
        await deleteAssocationType(id);
        message.success({ content: "Silindi!", key: " delete" });
        history.push("/associations");
      },
    });
  };

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: "Settings" },
            { label: "Association Types", path: "/AssociationTypes" },
          ]}
        ></BreadcrumbNav>
      </Container>
      <Container>
        <Content
          id="edit-associations-type"
          title="Edit Association Type"
          actionType="danger"
          actionTitle="Association Type Delete"
          onClick={deleteHandler}
        >
          <Tabs type="card" defaultActiveKey="1">
            <Tabs.TabPane tab={t("properties")} key="1">
              <Properties />
            </Tabs.TabPane>
            <Tabs.TabPane tab={t("history")} key="2">
              <History />
            </Tabs.TabPane>
          </Tabs>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
