import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Card } from "antd";
import { useParams } from "react-router-dom";

// Components
import DefaultTable from "../../../../components/Table/DefaultTable";

// Services
import { UsersHistory } from "../../../../services/history";

const History = () => {
  const [t] = useTranslation();
  const { id } = useParams();
  const [history, setUserHistory] = useState([]);
  const [loading, setLoading] = useState(true);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });

  const getColumns = () => [
    {
      title: t("Version"),
      dataIndex: "Version",
      key: "Version",
    },
    {
      title: t("Author"),
      dataIndex: "UserName",
      key: "UserName",
    },
    {
      title: t("Loggedat"),
      dataIndex: "LoggedAt",
      key: "LoggedAt",
    },
    {
      title: t("Old Values"),
      dataIndex: "OldValues",
      key: "OldValues",
      render(text, record) {
        let oldValues = "";

        record.OldValues.forEach(function (item) {
          oldValues += item.Label + ", ";
        });

        return <label>{oldValues}</label>;
      },
    },
    {
      title: t("New Values"),
      dataIndex: "NewValues",
      key: "NewValues",
      render(text, record) {
        let newValues = "";

        record.NewValues.forEach(function (item) {
          newValues += item.Label + ", ";
        });

        return <label>{newValues}</label>;
      },
    },
    {
      title: t("Comment"),
      dataIndex: "Comment",
      key: "Comment",
    },
  ];

  const getUsersHistory = async (pagination) => {
    const { PageSize, CurrentPage, data } = pagination;
    setLoading(true);

    try {
      const result = await UsersHistory({
        Id: id,
        PageSize,
        CurrentPage: CurrentPage - 1,
      });

      setUserHistory(result.data.Value.data);
      setPagination({ ...data, total: result.data.Value.recordsTotal });
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getUsersHistory({
      PageSize: pageSize,
      CurrentPage: current,
      data: pagination,
    });
  };

  useEffect(() => {
    getUsersHistory({
      PageSize: 10,
      CurrentPage: 1,
      data: pagination,
    });
  }, []);

  return (
    <Row>
      <Col span={24}>
        <DefaultTable
          columns={getColumns()}
          dataSource={history}
          loading={loading}
          style={{ marginTop: "15px" }}
          pagination={pagination}
          scroll={{ x: "1000px" }}
          onChange={handleTableChange}
        />
      </Col>
    </Row>
  );
};

History.propTypes = {};

export default History;
