import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Modal, Button, Form, Input, message } from "antd";
import PropTypes from "prop-types";

import { getActions } from "../../services/items";

const ActionsModal = ({ isModalVisible, onCancel }) => {
  const [t] = useTranslation();
  const [form] = Form.useForm();
  const [actions, setActions] = useState([]);

  const handleSave = () => {
    form.submit();
  };

  const handleAction = () => alert("action!!!!");
  useEffect(() => {
    async function fetchData() {
      const res = await getActions("Clause");
      setActions(res.data.Value);
    }
    fetchData();
  }, []);

  return (
    <Modal
      visible={isModalVisible}
      title={t("Actions")}
      okButtonProps={null}
      onCancel={onCancel}
      footer={null}
    >
      {actions.map(({ Label, Id }) => (
        <Button onClick={handleAction} key={Id} type="primary">
          {Label}
        </Button>
      ))}
    </Modal>
  );
};

ActionsModal.propTypes = {
  isModalVisible: PropTypes.bool.isRequired,
  onCancel: PropTypes.func,
};

export default ActionsModal;
