import { privateAxiosInstance } from "../api";
import {
  TRKCLL_ITEMS,
  TRKCLL_CATEGORY,
  TRKCLL_ITEM,
  TRKCLL_CREATE,
  TRKCLL_ITEM_FAV,
  TRKCLL_ITEM_HISTORY,
  TRKCLL_ITEM_CATEGORY,
} from "../../consts/api";

export async function getItems(body) {
  const result = await privateAxiosInstance.post(TRKCLL_ITEMS, body);
  return result;
}
export async function getItem(id) {
  const result = await privateAxiosInstance.get(`${TRKCLL_ITEM}?id=${id}`);
  return result;
}
export async function getCategoiresTree(id, itemType) {
  const result = await privateAxiosInstance.get(
    `${TRKCLL_CATEGORY}?id=${id}&itemType=${itemType}`
  );
  return result;
}

export async function createItem(body) {
  const result = await privateAxiosInstance.post(TRKCLL_CREATE, body);
  return result;
}

export async function toggleFavItem(itemId) {
  const result = await privateAxiosInstance.post(
    `${TRKCLL_ITEM_FAV}?itemId=${itemId}`
  );
  return result;
}
export async function getItemHistory(Id) {
  const result = await privateAxiosInstance.post(`${TRKCLL_ITEM_HISTORY}`, {
    Id,
  });
  return result;
}
export async function getItemCategory(Id) {
  const result = await privateAxiosInstance.get(
    `${TRKCLL_ITEM_CATEGORY}?id=${Id}`
  );
  return result;
}
