import React from "react";
import { Form, Input, Button, Row, Col } from "antd";
import LoginImage from "../assets/images/login.jpg";
import Logo from "../assets/images/logo.png";
import styled from "styled-components";
import { Link } from "react-router-dom";

export default function ForgotPassword() {
  const onFinish = async () => {
    console.log("finish");
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  return (
    <FixedBackground img={LoginImage}>
      <LogoWrapper>
        <LogoLogin src={Logo} />
      </LogoWrapper>
      <Row justify={"center"} align={"middle"}>
        <Col span={7}>
          <FormWrapper
            name="basic"
            layout="vertical"
            initialValues={{ remember: true }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
          >
            <FormItem>
              <p>Parolanızı mı unuttunuz ?</p>
              <h5>
                Parolanızı yeniden oluşturmanız için hesabınıza ait mail
                adresinizi giriniz.
              </h5>
            </FormItem>
            <FormItem
              name="email"
              rules={[{ required: true, message: "Email giriniz !" }]}
            >
              <Input type="email" placeholder="Email Adresi" />
            </FormItem>

            <FormItem>
              <Button type="primary" htmlType="submit">
                Gönder
              </Button>
            </FormItem>

            <ForgotWrapper
              to={{
                pathname: "/login",
              }}
            >
              Giriş sayfasına geri dön
            </ForgotWrapper>
          </FormWrapper>
        </Col>
      </Row>
    </FixedBackground>
  );
}

const ForgotWrapper = styled(Link)`
  display: block;
  text-align: center;
`;

const FormItem = styled(Form.Item)`
  margin-bottom: 10px;

  p {
    margin: 0;
  }

  button {
    width: 100%;
  }
`;

const FormWrapper = styled(Form)`
  background-color: rgba(238, 237, 237, 0.8);
  padding: 15px 20px;
  margin-top: 20vh;
`;

const LogoWrapper = styled.div`
  width: 140px;
  padding: 20px;
`;

const LogoLogin = styled.img`
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 4px;
  width: 70%;
`;

const FixedBackground = styled.div`
  background: url(${(props) => props.img}) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
`;
