import React from "react";
import { Row, Col, Tooltip, Menu, Select } from "antd";
import { useTranslation } from "react-i18next";
import { useHistory, Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import {
  QuestionCircleOutlined,
  UserOutlined,
  PoweroffOutlined,
} from "@ant-design/icons";

// Components
import Logo from "../Logo";
import Container from "../Layouts/Container";

// Actions
import { logoutAction } from "../../store/actions/auth";
import { setLang } from "../../store/actions/localization";

export default function Header() {
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  const history = useHistory();
  const auth = useSelector((state) => state.auth);
  const lang = useSelector((state) => state.localization.lang);

  const logout = () => {
    dispatch(logoutAction());
    history.push("/login");
  };

  const handleChangeLang = (lang) => {
    i18n.changeLanguage(lang);
    dispatch(setLang(lang));
  };

  return (
    <StyledHeader>
      <Container>
        <Row align="middle">
          <Col>
            <Logo />
          </Col>
          <Col flex="auto">
            <MenuWrapper mode="horizontal">
              <SubMenuWrapper key="SubMenu3" title={t("Settings")}>
                <MenuItem key="itemtypes">
                  <Link
                    to={{
                      pathname: "/settings/itemtypes",
                    }}
                  >
                    {t("ItemTypes")}
                  </Link>
                </MenuItem>
                <MenuItem key="attributes">
                  <Link
                    to={{
                      pathname: "/attributes",
                    }}
                  >
                    {t("Attributes")}
                  </Link>
                </MenuItem>

                <MenuItem key="attributegroups">
                  <Link
                    to={{
                      pathname: "/attributegroups",
                    }}
                  >
                    {t("AttributeGroups")}
                  </Link>
                </MenuItem>
                <MenuItem key="association">
                  <Link
                    to={{
                      pathname: "/associations",
                    }}
                  >
                    {t("AssociationTypes")}
                  </Link>
                </MenuItem>
                <MenuItem key="families">
                  <Link
                    to={{
                      pathname: "/families",
                    }}
                  >
                    {t("Families")}
                  </Link>
                </MenuItem>
              </SubMenuWrapper>

              <SubMenuWrapper key="SubMenu4" title={t("System")}>
                <SubMenuWrapper key="SubMenu5" title={t("UserManagement")}>
                  <MenuItem key="users">
                    <Link
                      to={{
                        pathname: "/system/userManage",
                      }}
                    >
                      {t("Users")}
                    </Link>
                  </MenuItem>

                  <MenuItem key="Roles">
                    <Link
                      to={{
                        pathname: "/system/roles",
                      }}
                    >
                      {t("Roles")}
                    </Link>
                  </MenuItem>

                  <MenuItem key="Groups">
                    <Link
                      to={{
                        pathname: "/system/groups",
                      }}
                    >
                      {t("Groups")}
                    </Link>
                  </MenuItem>
                </SubMenuWrapper>
              </SubMenuWrapper>
              <SubMenuWrapper key="SubMenu7" title={t("BPM")}>
                <MenuItem key="modeler">
                  <Link
                    to={{
                      pathname: "/bpmn/modeler",
                    }}
                  >
                    {t("Modeler")}
                  </Link>
                </MenuItem>
                <MenuItem key="dashboard">
                  <Link
                    to={{
                      pathname: "/bpmn/dashboard",
                    }}
                  >
                    {t("Dashboard")}
                  </Link>
                </MenuItem>
                <MenuItem key="history">
                  <Link
                    to={{
                      pathname: "/bpmn/history",
                    }}
                  >
                    {t("History")}
                  </Link>
                </MenuItem>
                <MenuItem key="taskList">
                  <Link
                    to={{
                      pathname: "/bpmn/taskList",
                    }}
                  >
                    {t("TaskList")}
                  </Link>
                </MenuItem>
              </SubMenuWrapper>
            </MenuWrapper>
          </Col>

          <Col span={10}>
            <Row justify="end" align="middle">
              <Col>
                <LangSelect
                  id="header-lang-select"
                  defaultValue={lang}
                  onChange={handleChangeLang}
                >
                  <Select.Option value="tr-TR">TR</Select.Option>
                  <Select.Option value="en-US">EN</Select.Option>
                </LangSelect>
              </Col>
              <Col>
                <Link to="/profile" title={t("help")}>
                  <UserActions>
                    <UserInfomation>
                      <UserRole>{auth && auth.userName}</UserRole>
                      <UserOutlined
                        style={{
                          fontSize: 20,
                          color: "#777",
                          marginLeft: "15px",
                        }}
                      />
                    </UserInfomation>
                  </UserActions>
                </Link>
              </Col>
              <Col>
                <Tooltip>
                  <Link to="/" title={t("Help")}>
                    <QuestionCircleOutlined
                      style={{
                        fontSize: 20,
                        color: "#777",
                        marginLeft: "25px",
                      }}
                    />
                  </Link>
                </Tooltip>
              </Col>
              <Col>
                <Link to="/" onClick={logout}>
                  <PoweroffOutlined
                    style={{
                      fontSize: 20,
                      color: "#777",
                      marginLeft: "25px",
                    }}
                  ></PoweroffOutlined>
                </Link>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </StyledHeader>
  );
}

const StyledHeader = styled.div`
  background-color: ${({ theme }) => theme.turkcell.mainColor};
  height: 50px;
  padding: 0px 10px;
  * {
    color: white;
  }
`;
const UserActions = styled.div`
  display: flex;
  transition: 0.3s all;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
  :hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
`;
const UserInfomation = styled.div`
  display: flex;
  color: ${({ theme }) => theme.gray};
  text-align: end;
  align-items: center;
  font-size: 0.8rem;
`;

const UserRole = styled.span`
  font-size: 0.9rem;
`;

const MenuWrapper = styled(Menu)`
  background-color: transparent;
  border: none;
`;

const MenuItem = styled(Menu.Item)`
  color: ${({ theme }) => theme.gray};
`;

const SubMenuWrapper = styled(Menu.SubMenu)`
  color: ${({ theme }) => theme.gray};
  font-size: 0.9rem;
  border: 0 !important;
`;

const LangSelect = styled(Select)`
  width: 100px;
  margin-right: 10px;
  .ant-select-selector {
    background-color: transparent !important;
  }
`;
